<template>
  <div class="comp login_multi_currency">
    <div class="multi_currency_title" @click="show">
      <div class="lt">
        <div class="lt_currency_title text_1">{{ $t('UI.Currency_Site_Title') }}</div>
        <div class="lt_currency text_1">{{ getCurSiteInfo.country }}&nbsp;({{ getCurSiteInfo.currency }})</div>
      </div>
      <div class="rt">
        <icon-font name="icon-Icon_Down" size="16" :dark="commonStyle('multi_currency_title_rt_icon_app_color')" />
      </div>
    </div>

    <site-dialog ref="siteDialogRef" @select-site="onSelectSite" />
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import SiteDialog from '../site-dialog'
import _common from '@style/components/ui.modules.scss'

export default {
  components: {
    SiteDialog
  },
  data() {
    return {}
  },
  computed: {
    ...mapGetters('base', ['getCurSiteInfo'])
  },
  methods: {
    commonStyle: k => _common[k],
    // 打开列表
    show() {
      this.$nextTick(() => {
        const siteDialogRef = this.$refs['siteDialogRef']
        if (siteDialogRef) siteDialogRef.show()
      })
    },
    // 站点切换
    async onSelectSite(item = {}) {
      await this.$store.dispatch('base/changeSite', item).catch(e => e)
    }
  }
}
</script>

<style lang="scss">
@import '@style/components/ui.modules.scss';

.comp.login_multi_currency {
  width: 100%;
  cursor: pointer;
  user-select: none;
  .multi_currency_title {
    width: 100%;
    height: 56px;
    margin: 0 0 24px 0;
    padding: 12px 16px;
    background: $multi_currency_title_app_bg;
    border-radius: 2px;
    @include setBox(row, flex-start, center);
    gap: 0 8px;
    overflow: hidden;
    .lt {
      flex-grow: 1;
      flex-shrink: 1;
      height: 100%;
      overflow: hidden;
      @include setBox(column, space-between, flex-start);
      .lt_currency_title {
        width: 100%;
        font-size: 12px;
        font-weight: 400;
        color: $multi_currency_title_lt_title_app_color;
        line-height: 1;
      }
      .lt_currency {
        width: 100%;
        font-size: 14px;
        font-weight: 400;
        color: $multi_currency_title_lt_currency_app_color;
        line-height: 1;
      }
    }
    .rt {
      flex-grow: 0;
      flex-shrink: 0;
      font-size: 0;
    }
  }
}
// 针对pc样式
@include breakpoint-only-up() {
  .comp.login_multi_currency {
    position: relative;
  }
}
</style>
