<template>
  <custom-dialog v-model="visible" :full="true">
    <div class="comp human_face">
      <div class="human_face_title">
        <div class="close_right cursor_point" @click="close(2)">
          <icon-font
            name="icon-Icon_Cloose"
            :size="commonStyle(isApp ? 'human_face_title_close_icon_app_size' : 'human_face_title_close_icon_pc_size')"
            :dark="commonStyle(isApp ? 'human_face_title_close_icon_app_dark' : 'human_face_title_close_icon_pc_dark')"
          />
        </div>
      </div>
      <div class="human_face_box">
        <div class="human_face_tips">{{ $t('UI.Face_Tip_Text') }}</div>
        <div class="human_face_liveness">
          <div class="human_face_video">
            <video ref="videoElement" autoplay playsinline webkit-playsinline></video>
            <div v-if="faceLoading" class="human_face_loading">
              <div class="human_face_dot"></div>
              <div class="human_face_dot"></div>
              <div class="human_face_dot"></div>
              <div class="human_face_dot"></div>
              <div class="human_face_dot"></div>
            </div>
          </div>
          <canvas ref="canvasid" style="display: none" width="400" height="400"></canvas>
        </div>
      </div>
      <div v-if="showMask" class="human_face_mask"></div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { getDetectFaces, getCompareFaceByImage } from '@/api/modules/face.js'
import _common from '@style/components/ui.modules.scss'

export default {
  data() {
    return {
      visible: false,
      faceLoading: true, // loading
      showMask: false, // 是否开启扫描动画
      compareType: 1, // 1=绑定，2=解绑，3=校验人脸
      rekognition_code: '', // 用户临时凭证
      faceCollections: [], // 人脸图片数组
      mediaConfig: { video: true, audio: false }, // 媒体信息
      faceNum: 3, // 人脸图片数量
      timeNum: 500 // 发送请求间隔时间，毫秒
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user'])
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    /**
     * 打开
     * @param {*} compare_type 1=绑定，2=解绑，3=校验人脸
     * @param {*} rekognition_code 用户临时凭证
     */
    open({ compare_type = 1, rekognition_code = '' } = {}) {
      return new Promise(async (resolve, reject) => {
        this.faceResolve = resolve
        this.faceReject = reject
        // 当前是否可以使用媒体
        if (!navigator.mediaDevices) return this.close(1)

        this.compareType = compare_type == 2 ? 2 : 1
        this.rekognition_code = rekognition_code
        this.visible = true
        // 开启人脸识别
        this.$nextTick(() => {
          if (this.$refs['videoElement']) {
            const stream = this.$refs['videoElement'].srcObject
            const tracks = stream ? stream.getTracks() : []
            tracks.forEach(t => t.stop())
            this.$refs['videoElement'].srcObject = null

            this.$refs['videoElement'].removeEventListener('play', this.startFace)
            this.$refs['videoElement'].removeEventListener('pause', this.endFace)

            navigator.mediaDevices
              .getUserMedia(this.mediaConfig)
              .then(stream => {
                this.$refs['videoElement'].srcObject = stream
                this.$refs['videoElement'].play()
              })
              .catch(e => {
                this.close(3)
              })

            // 开始采集人脸识别信息
            this.$refs['videoElement'].addEventListener('play', this.startFace)
            // 当视频暂停或停止后，停止扫描动画
            this.$refs['videoElement'].addEventListener('pause', this.endFace)
          } else {
            this.close(4)
          }
        })
      })
    },
    /**
     * 关闭
     * @param {*} sign 1:浏览器不支持 2:手动关闭 3:找不到设备 4:失败 5:成功
     */
    close(sign = 1) {
      // 在关闭时，需要手动清空媒体缓存
      if (this.$refs['videoElement']) {
        const stream = this.$refs['videoElement'].srcObject
        const tracks = stream ? stream.getTracks() : []
        tracks.forEach(t => t.stop())
        this.$refs['videoElement'].srcObject = null
      }

      this.faceResolve({ code: sign })
      this.visible = false
      this.faceLoading = true
      this.showMask = false
      this.faceCollections = []
    },
    // 开始采集人脸识别信息
    startFace() {
      this.faceLoading = false
      // 开启扫描动画
      this.showMask = true

      const numArr = new Array(this.faceNum).fill(null)
      Promise.all(numArr.map((_, i) => this.execFaceQueue((i + 1) * this.timeNum)))
        .then(async r => {
          this.showMask = false
          // 开始验证本人
          const face = this.faceCollections[this.faceCollections.length - 1] || ''
          this.$pop.loading().show()
          this.faceLoading = true
          const par = {
            token: this.user?.token,
            face,
            compare_type: this.compareType,
            rekognition_code: this.rekognition_code
          }
          const res = await getCompareFaceByImage(par).catch(e => e)
          this.faceLoading = false
          this.$pop.loading().hide()

          if (!res || res.code != 0) return this.close(4)
          this.close(5)
        })
        .catch(e => {
          this.showMask = false
          this.close(4)
        })
    },
    // 结束采集人识别信息
    endFace() {
      // 关闭扫描动画
      this.showMask = false
    },
    /**
     * 采集人脸识别信息
     * @param {*} num 延迟时间 毫秒
     */
    execFaceQueue(num = 0) {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          this.$nextTick(async () => {
            const canvasidRef = this.$refs['canvasid']
            const videoElementRef = this.$refs['videoElement']

            // 在canvas上绘制当前视频帧
            canvasidRef.getContext('2d').drawImage(videoElementRef, 0, 0, canvasidRef.width, canvasidRef.height)
            // 获取图像数据
            const imageData = canvasidRef.getContext('2d').getImageData(0, 0, canvasidRef.width, canvasidRef.height)
            /**
             * 转bese64图片
             */
            // 创建一个临时的canvas元素
            const canvas = document.createElement('canvas')
            canvas.width = imageData.width
            canvas.height = imageData.height
            // 获取canvas的2D渲染上下文
            const ctx = canvas.getContext('2d')
            // 使用指定的imageData更新canvas
            ctx.putImageData(imageData, 0, 0)
            // 将canvas转换为Base64编码的字符串图片
            const base64String = canvas.toDataURL()

            // 验证是否为真人
            const params = {
              token: this.user?.token,
              face: base64String,
              compare_type: this.compareType,
              rekognition_code: this.rekognition_code
            }
            const res = await getDetectFaces(params).catch(e => e)
            if (!res || res.code != 0) return reject()

            this.faceCollections.push(base64String)
            resolve()
          })
        }, num)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

// 针对移动端样式
.comp.human_face {
  width: $human_face_app_width;
  height: $human_face_app_height;
  background-color: $human_face_app_bg;
  position: relative;
  .human_face_title {
    width: $human_face_title_app_width;
    min-height: $human_face_title_app_min_height;
    padding: $human_face_title_app_padding;
    position: relative;
    @include setBox(row, flex-end, center);
    .close_right {
      font-size: 0;
      position: absolute;
      top: $human_face_title_close_app_top;
      right: $human_face_title_close_app_right;
      transform: $human_face_title_close_app_transform;
      z-index: 2;
    }
  }
  .human_face_box {
    width: $human_face_box_app_width;
    padding: $human_face_box_app_padding;
    .human_face_tips {
      width: $human_face_box_tips_app_width;
      margin: $human_face_box_tips_app_margin;
      font-size: $human_face_box_tips_app_size;
      font-weight: $human_face_box_tips_app_weight;
      color: $human_face_box_tips_app_color;
      line-height: $human_face_box_tips_app_line;
      text-align: $human_face_box_tips_app_align;
      @include setBox(row, center, center);
    }
    .human_face_liveness {
      width: $human_face_box_liveness_app_width;
      @include setBox(column, center, center);
      .human_face_video {
        width: $human_face_box_liveness_video_app_width;
        height: $human_face_box_liveness_video_app_height;
        border-radius: $human_face_box_liveness_video_app_radius;
        background-color: $human_face_box_liveness_video_app_bg;
        position: relative;
        video {
          width: $human_face_box_liveness_video_video_app_width;
          height: $human_face_box_liveness_video_video_app_height;
          border-radius: $human_face_box_liveness_video_video_app_radius;
        }
        .human_face_loading {
          width: $human_face_box_liveness_video_loading_app_width;
          height: $human_face_box_liveness_video_loading_app_height;
          border-radius: $human_face_box_liveness_video_loading_app_radius;
          position: absolute;
          left: $human_face_box_liveness_video_loading_app_left;
          right: $human_face_box_liveness_video_loading_app_right;
          top: $human_face_box_liveness_video_loading_app_top;
          transform: $human_face_box_liveness_video_loading_app_transform;
          @include setBox(row, center, center);
          .human_face_dot {
            width: $human_face_box_liveness_video_loading_dot_app_width;
            height: $human_face_box_liveness_video_loading_dot_app_height;
            border-radius: $human_face_box_liveness_video_loading_dot_app_radius;
            background-color: $human_face_box_liveness_video_loading_dot_app_bg;
            animation: spin 1s infinite linear;
            animation-delay: var(--animation-delay, 0s);
            &:nth-child(0) {
              --animation-delay: 0.15s;
            }
            &:nth-child(1) {
              --animation-delay: 0.3s;
            }
            &:nth-child(2) {
              --animation-delay: 0.45s;
            }
            &:nth-child(3) {
              --animation-delay: 0.6s;
            }
            &:nth-child(4) {
              --animation-delay: 0.75s;
            }
          }
        }
        @keyframes spin {
          0% {
            transform: translateY(0px) scale(1);
          }
          60% {
            transform: translateY(50px) scale(0.6);
          }
          100% {
            transform: translateY(0px) scale(1);
          }
        }
      }
    }
  }
  .human_face_mask {
    position: absolute;
    top: $human_face_mask_app_top;
    right: $human_face_mask_app_right;
    bottom: $human_face_mask_app_bottom;
    left: $human_face_mask_app_left;
    background: $human_face_mask_app_bg;
    background-size: $human_face_mask_app_bg_size;
    animation: scanAnimation 3s linear infinite;
  }
  @keyframes scanAnimation {
    0% {
      background-position: bottom;
    }
    70% {
      background-position: top;
    }
    100% {
      background-position: bottom;
    }
  }
}
// 针对pc样式
@include breakpoint-only-up() {
  .comp.human_face {
    width: $human_face_pc_width;
    height: $human_face_pc_height;
    .human_face_box {
      padding: $human_face_box_pc_padding;
      .human_face_liveness {
        .human_face_video {
          width: $human_face_liveness_video_pc_width;
          height: $human_face_liveness_video_pc_height;
          video {
            width: $human_face_liveness_video_video_pc_width;
            height: $human_face_liveness_video_video_pc_height;
            border-radius: $human_face_liveness_video_video_pc_radius;
          }
          .human_face_loading {
            .human_face_dot {
              width: $human_face_loading_dot_pc_width;
              height: $human_face_loading_dot_pc_height;
            }
          }
        }
      }
    }
  }
}
</style>
