<template>
  <v-menu offset-y v-model="showMenu" nudge-left="13">
    <template v-slot:activator="{ on, attrs }">
      <div class="d-flex align-center" v-bind="attrs" v-on="on">
        <div class="select_text">{{ state ? `+${state}` : `+${areaPhone.code || ''}` }}</div>
        <div class="d-flex align-center arrow_icon">
          <icon-font :name="showMenu ? 'icon-up' : 'icon-down'" :size="commonStyle('user_select_arrow_icon_size')" />
        </div>
        <v-divider class="v_divider" vertical></v-divider>
      </div>
    </template>
    <v-list>
      <v-list-item @click="selectedPlace(item)" v-for="(item, index) in list" :key="index">
        <v-row>
          <v-col cols="12">
            {{ item.value ? item.value : item.code }}
          </v-col>
        </v-row>
      </v-list-item>
    </v-list>
  </v-menu>
</template>

<script>
import { mapState } from 'vuex'
import { lodash } from '@/utils/helper'
import _common from '@style/components/game-container.login.modules.scss'

export default {
  props: {
    userIcon: String,
    list: Array,
    value: String,
    keyName: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      state: '',
      imge: '',
      showMenu: false
    }
  },
  watch: {
    value: {
      immediate: true,
      handler(e) {
        if (this.list.length > 0) this.state = lodash.isBlank(e) ? this.list[0].value : e
      }
    }
  },
  computed: {
    ...mapState('source', ['phonePlaces']),
    // 获取第一个区号
    areaPhone() {
      const item = this.phonePlaces[0] || {}
      return item
    }
  },
  methods: {
    commonStyle: k => _common[k],
    selectedPlace(params) {
      this.$emit('change', params)
      this.imge = params.flag
    }
  }
}
</script>

<style scoped lang="scss">
@import '@style/components/game-container.login.modules.scss';

.select_text {
  color: $user_select_text_color;
  font-size: $user_select_text_font_size;
  font-weight: $user_select_text_font_weight;
}
.arrow_icon {
  margin: $user_select_arrow_icon_margin;
}
.v_divider {
  height: $user_select_v_divider_height;
  margin: $user_select_v_divider_margin;
}
</style>
