<template>
  <custom-dialog v-model="rechargeWithdrawalShow" :full="true" :mask="true" @close="onClose">
    <div class="deposit_withdraw_box">
      <div class="deposit_withdraw_title">
        <div class="title_content">
          <div
            v-for="item in tabList"
            :key="item.value"
            class="title_item"
            :class="{ active: item.value == tabIndex }"
            @click="onChangeTab(item)"
          >
            <div v-if="tabShow" class="title_item_text">
              <auto-font :uuid="item.value" maxFont="14" fontWeight="900" :level="2" :text="item.label" />
            </div>
          </div>
        </div>
        <div class="title_close" @click="onClose">
          <icon-font name="icon-Icon_Cloose" size="18" dark="t_text_color_2" />
        </div>
      </div>
      <div class="deposit_withdraw_content">
        <component :is="componentName" />
      </div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { sport_btr_gid } from '@/api/setting.js'
import SimpleTabBtn from '@/components/ui/simple-tab-btn'
import _styles from '@style/components/game-container.modules.scss'

export default {
  components: {
    SimpleTabBtn
  },
  data() {
    return {
      // tab文字显示
      tabShow: true,
      // 充值提现弹窗tab显示 deposit：充值，withdraw：提现
      tabIndex: ''
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    ...mapState('stepLoader', ['depositDetailName', 'withdrawDetailName']),
    ...mapState('modal', ['depositWithdrawShow', 'depositWithdrawStatus', 'withdrawEntrance', 'updateWithdrawType']),
    ...mapGetters('base', ['isWithdrawShow']),
    ...mapGetters('stepLoader', ['globalModules']),
    // 充值提现弹窗显隐藏
    rechargeWithdrawalShow: {
      get() {
        return this.depositWithdrawShow
      },
      set(bool = false) {
        this.$store.commit('modal/SET_DEPOSIT_WITHDRAW_SHOW', bool)
      }
    },
    /**
     * 充值提现tab列表
     * isWithdrawShow 积分兑换功能/提现是否隐藏 true隐藏 false显示
     */
    tabList() {
      return [
        {
          value: 'deposit',
          compName: 'deposit-detail',
          label: this.$t('UI_recharge_desposit'),
          hidden: this.depositDetailName && this.withdrawEntrance != 2
        },
        {
          value: 'withdraw',
          compName: 'withdraw-detail',
          label: this.$t('UI.MakeMoney_7_Apply_5'),
          hidden: !this.isWithdrawShow && this.withdrawDetailName
        }
      ].filter(f => f.hidden)
    },
    // 当前渲染的组件
    componentName() {
      const item = this.tabList.find(item => item.value == this.tabIndex)
      return item ? item.compName : ''
    }
  },
  watch: {
    isApp() {
      this.tabShow = false
      this.$nextTick(() => {
        this.tabShow = true
      })
    },
    // 监听充值提现弹窗显隐藏
    rechargeWithdrawalShow(val) {
      if (!val) return
      /**
       * 充值提现弹窗tab显示
       * deposit：充值，withdraw：提现
       * withdrawEntrance 1：从推广中的提款功能进入 2: 活动邀请进入
       */
      let tab = this.depositWithdrawStatus
      if (this.withdrawEntrance == 1 || this.withdrawEntrance == 2) tab = 'withdraw'
      if (this.tabList.length == 1) tab = this.tabList[0].value

      this.tabIndex = tab
    }
  },
  methods: {
    // 切换tab
    onChangeTab(item = {}) {
      this.tabIndex = item.value
      this.$store.commit('modal/SET_DEPOSIT_WITHDRAW_STATUS', item.value)
    },
    // 关闭弹窗
    async onClose() {
      this.rechargeWithdrawalShow = false

      // 如果是在 sport 页面，要重新上分
      if (this.$route.name == 'sportView') {
        const params = {
          token: this.user.token,
          gid: sport_btr_gid,
          language: this.languageType,
          type: this.isApp ? 100 : 101,
          direction: 1
        }

        this.$store.commit('source/SET_IS_ROTATE', true)
        await this.$store.dispatch('sport/upperDivision', params).catch(e => e)
        this.$store.commit('source/SET_IS_ROTATE', false)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.modules.scss';

// 针对移动端样式
.deposit_withdraw_box {
  width: 100%;
  height: 100%;
  background: $vip_modal_deposit_withdraw_bg;
  user-select: none;
  @include setBox(column, center, center);
  .deposit_withdraw_title {
    flex-grow: 0;
    flex-shrink: 0;
    width: 100%;
    height: 70px;
    padding: 0 65px;
    border-bottom: 1px solid $border_hr_color;
    @include setBox(row, center, center);
    position: relative;
    .title_content {
      width: 100%;
      height: 100%;
      @include setBox(row, center, center);
      .title_item {
        flex: 1;
        max-width: 140px;
        height: 100%;
        cursor: pointer;
        &.active {
          border-bottom: 2px solid $primary_dark_color;
          .title_item_text {
            color: $white;
          }
        }
        .title_item_text {
          color: $text_color_2;
        }
      }
    }
    .title_close {
      position: absolute;
      top: 0;
      right: 0;
      width: 65px;
      height: 100%;
      cursor: pointer;
      @include setBox(row, center, center);
    }
  }
  .deposit_withdraw_content {
    flex-grow: 1;
    flex-shrink: 1;
    width: 100%;
    overflow-y: auto;
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .deposit_withdraw_box {
    width: 720px;
    border-radius: 8px;
    .deposit_withdraw_title {
      .title_content {
        .title_item {
          max-width: 170px;
        }
      }
      .title_close {
        width: 90px;
      }
    }
  }
}
</style>
