<template>
  <div class="cpf_regist">
    <div class="cpf_regist_top">
      <span>{{ $t('UI_Account_BindMyCPF') }}</span>
      <icon-font name="icon-Icon_Cloose" class="cpf_regist_top_close" :size="defaultSize" @click="onCloose" />
    </div>
    <div class="cpf_regist_content">
      <v-form ref="form">
        <div v-show="isFirst">
          <div class="text_field">
            <span>{{ $t('UI_Login_CPF_id') }}</span>
            <span class="color_red">*</span>
          </div>
          <v-text-field
            v-model="params.reg_bind_cpf"
            placeholder="000.000.000-00"
            background-color="t_login_input_bg"
            color="t_text_color_3"
            type="text"
            clearable
            single-line
            solo
            flat
            hide-details
            class="form_field"
            :height="variables.cpf_regist_fields_input_height_pc"
            @input="inputFn"
            @blur="cpfBlur"
            @focus="cpfFocus"
          >
            <template slot="prepend-inner">
              <div class="prepend_cpf">
                <icon-font
                  name="icon-icon_mingpian"
                  :size="commonStyle(isApp ? 'line_input_cpf_icon_app_size' : 'line_input_cpf_icon_pc_size')"
                  :dark="commonStyle('line_input_cpf_icon_app_color')"
                />
              </div>
            </template>
            <template slot="append" v-if="systemConfig?.person_center_cpf_check_switch == 1">
              <v-btn
                v-if="!verifySuccess"
                :height="variables.cpf_regist_verify_btn_height"
                :width="variables.cpf_regist_verify_btn_width"
                color="t_primary_dark_color"
                class="btn_radius verify_btn"
                @click="onVerify"
              >
                {{ $t('UI_Login_CPF_Verification') }}
              </v-btn>
              <div class="verify_text" v-else>
                <icon-font name="icon-select" :dark="variables.cpf_regist_verify_text_color" />
                <span class="verify_text_box">{{ $t('UI_Account_Info4') }} </span>
              </div>
            </template>
          </v-text-field>
          <div class="text_field">
            <span>{{ $t('UI_Login_CPF_Surname') }}</span>
            <span class="color_red">*</span>
          </div>
          <v-text-field
            v-model="params.name"
            :placeholder="$t('UI_Login_CPF_Name')"
            :height="variables.cpf_regist_fields_input_height_pc"
            background-color="t_login_input_bg"
            color="t_text_color_3"
            type="text"
            :disabled="cpfAutoVerify"
            clearable
            single-line
            solo
            flat
            hide-details
            class="form_field"
          >
            <template slot="prepend-inner">
              <div class="prepend_name">
                <icon-font
                  name="icon-renyuan"
                  :size="commonStyle(isApp ? 'line_input_name_icon_app_size' : 'line_input_name_icon_pc_size')"
                  :dark="commonStyle(isApp ? 'line_input_name_icon_app_color' : 'line_input_name_icon_pc_color')"
                />
              </div>
            </template>
          </v-text-field>
          <!-- 出生年月 -->
          <div class="birthday_limit">
            <div class="age_title">{{ $t('UI_Login_CPF_Birth') }}</div>
            <div class="age_birthday" :class="{ fail: birthdayError }">
              <birthday-age v-model="params.birthday" :disabled="cpfAutoVerify" />
            </div>
          </div>
          <div class="text_field">
            <span>{{ $t('UI_Gender') }}</span>
            <span class="color_red">*</span>
          </div>
          <div class="gender_box">
            <RadioGroup v-model="params.cpf_gender">
              <RadioBox
                v-for="(ridioItem, index) in radioList"
                :key="index"
                :value="ridioItem.value"
                :disabled="cpfAutoVerify"
                class="gender_box_radio_box"
                offIcon="icon-weixuanzhong"
                onIcon="icon-xuanzhong"
              >
                <template>
                  <div class="fields_radio_title">{{ ridioItem.label }}</div>
                </template>
              </RadioBox>
            </RadioGroup>
          </div>
          <v-btn
            class="login_btn btn_radius"
            color="t_primary_dark_color"
            width="100%"
            :height="variables.login_regist_btn_height"
            @click="onSubmit"
          >
            <div class="login_btn_text">{{ $t('UI_Login_CPF_Continue') }}</div>
          </v-btn>
        </div>
        <div v-show="!isFirst">
          <!-- 手机号 -->
          <template v-if="systemConfig?.cpf_fields_switch_arr?.cpf_tel_switch">
            <div class="text_field">
              <span>{{ $t('UI_LOGIN_Phone') }}</span>
              <span v-if="systemConfig?.cpf_fields_switch_arr?.cpf_tel_switch == 2" class="color_red">*</span>
            </div>
            <v-text-field
              v-model="formParams.cpf_tel"
              :placeholder="$t('UI_LOGIN_Phone')"
              :rules="verificationPhone"
              background-color="t_login_input_bg"
              color="t_text_color_3"
              type="number"
              clearable
              single-line
              solo
              flat
              hide-details
              :height="variables.cpf_regist_fields_input_height_pc"
              class="form_field"
            >
              <template slot="prepend-inner">
                <div class="form_field_phone">
                  <icon-font
                    name="icon-Icon_info"
                    :size="commonStyle(isApp ? 'line_input_phone_icon_app_size' : 'line_input_phone_icon_pc_size')"
                    :dark="commonStyle(isApp ? 'line_input_phone_icon_app_color' : 'line_input_phone_icon_pc_color')"
                  />
                  <user-select
                    :value="formParams.area"
                    :userIcon="$u.optionalChaining(phonePlaces[0], 'flag')"
                    :list="phonePlaces"
                    @change="par => (formParams.area = par.code)"
                  />
                </div>
              </template>
            </v-text-field>
          </template>
          <!-- 邮箱 -->
          <template v-if="systemConfig?.cpf_fields_switch_arr?.cpf_email_switch">
            <div class="text_field">
              <span>{{ $t('UI_LOGIN_Mail') }}</span>
              <span v-if="systemConfig?.cpf_fields_switch_arr?.cpf_email_switch == 2" class="color_red">*</span>
            </div>
            <v-text-field
              v-model="formParams.cpf_email"
              :label="$t('UI_LOGIN_Mail')"
              :rules="verificationEmail"
              background-color="t_login_input_bg"
              color="t_text_color_3"
              type="text"
              clearable
              single-line
              solo
              flat
              hide-details
              class="form_field"
              :height="variables.cpf_regist_fields_input_height_pc"
            >
              <template slot="prepend-inner">
                <div class="prepend_email">
                  <icon-font
                    name="icon-InBox_2"
                    :size="commonStyle(isApp ? 'line_input_email_icon_app_size' : 'line_input_email_icon_pc_size')"
                    :dark="commonStyle(isApp ? 'line_input_email_icon_app_color' : 'line_input_email_icon_app_color')"
                  />
                </div>
              </template>
            </v-text-field>
          </template>
          <!-- Postal code -->
          <template v-if="systemConfig?.cpf_fields_switch_arr?.cpf_postal_code_switch">
            <div class="text_field">
              <span>{{ $t('UI_Postal') }}</span>
              <span v-if="systemConfig?.cpf_fields_switch_arr?.cpf_postal_code_switch == 2" class="color_red">*</span>
            </div>
            <v-text-field
              v-model="formParams.cpf_postal_code"
              class="form_field"
              placeholder="ex.70297-400"
              background-color="t_login_input_bg"
              color="t_text_color_3"
              type="number"
              clearable
              single-line
              solo
              flat
              hide-details
              :height="variables.cpf_regist_fields_input_height_pc"
              @blur="addressBlur"
              @input="addressInput"
            >
              <template v-if="systemConfig?.cpf_check_auto_fill_address_switch == 2" slot="append">
                <v-btn
                  v-if="!addressVerifySuccess"
                  :height="variables.cpf_regist_verify_btn_height"
                  :width="variables.cpf_regist_verify_btn_width"
                  color="t_primary_dark_color"
                  class="btn_radius verify_btn"
                  @click="onAddressVerify"
                >
                  {{ $t('UI_Login_CPF_Verification') }}
                </v-btn>
                <div v-else class="verify_text">
                  <icon-font name="icon-select" :dark="variables.cpf_regist_verify_text_color" />
                  <span class="verify_text_box">{{ $t('UI_Account_Info4') }} </span>
                </div>
              </template>
            </v-text-field>
          </template>
          <template v-if="systemConfig?.cpf_fields_switch_arr?.cpf_street_switch">
            <div class="text_field">
              <span>{{ $t('UI_Street') }} </span>
              <span v-if="systemConfig?.cpf_fields_switch_arr?.cpf_street_switch == 2" class="color_red">*</span>
            </div>
            <v-text-field
              v-model="formParams.cpf_street"
              class="form_field"
              placeholder="ex.Rua das Flores,12 -apto 201,ltaipú"
              background-color="t_login_input_bg"
              color="t_text_color_3"
              type="text"
              clearable
              single-line
              solo
              flat
              hide-details
              :height="variables.cpf_regist_fields_input_height_pc"
              :disabled="cpfStreetFn"
            />
          </template>
          <template v-if="systemConfig?.cpf_fields_switch_arr?.cpf_complement_switch">
            <div class="text_field">
              <span>{{ $t('UI_Complement') }}</span>
              <span v-if="systemConfig?.cpf_fields_switch_arr?.cpf_complement_switch == 2" class="color_red">*</span>
            </div>
            <v-text-field
              v-model="formParams.cpf_complement"
              background-color="t_login_input_bg"
              color="t_text_color_3"
              type="text"
              clearable
              single-line
              solo
              flat
              hide-details
              :height="variables.cpf_regist_fields_input_height_pc"
              class="form_field"
            />
          </template>

          <div class="form_across">
            <div v-if="systemConfig?.cpf_fields_switch_arr?.cpf_city_switch" class="form_across_item">
              <div class="text_field">
                <span>{{ $t('UI_City') }}</span>
                <span v-if="systemConfig?.cpf_fields_switch_arr?.cpf_city_switch == 2" class="color_red">*</span>
              </div>
              <v-text-field
                v-model="formParams.cpf_city"
                placeholder="ex. Säo Paulo"
                background-color="t_login_input_bg"
                color="t_text_color_3"
                type="text"
                clearable
                single-line
                solo
                flat
                hide-details
                :height="variables.cpf_regist_fields_input_height_pc"
                :disabled="isCpfCItyFn"
                class="form_field"
              />
            </div>
            <div v-if="systemConfig?.cpf_fields_switch_arr?.cpf_district_switch" class="form_across_item">
              <div class="text_field">
                <span>{{ $t('UI_Estado') }}</span>
                <span v-if="systemConfig?.cpf_fields_switch_arr?.cpf_district_switch == 2" class="color_red">*</span>
              </div>
              <v-text-field
                v-model="formParams.cpf_district"
                placeholder="ex.Goiás"
                background-color="t_login_input_bg"
                color="t_text_color_3"
                type="text"
                clearable
                single-line
                solo
                flat
                hide-details
                :height="variables.cpf_regist_fields_input_height_pc"
                :disabled="isDistrictFn"
                class="form_field"
              />
            </div>
          </div>
          <template v-if="systemConfig?.cpf_fields_switch_arr?.cpf_country_switch">
            <div class="text_field">
              <span>{{ $t('UI_Country') }}</span>
            </div>
            <v-text-field
              v-model="formParams.cpf_country"
              label="Country"
              background-color="t_login_input_bg"
              color="t_text_color_3"
              type="text"
              clearable
              single-line
              solo
              flat
              hide-details
              disabled
              :height="variables.cpf_regist_fields_input_height_pc"
              class="form_field"
            />
          </template>

          <div class="btn_box">
            <v-btn
              class="cpf_btn_continue btn_radius"
              color="t_primary_dark_color"
              width="100%"
              :height="variables.login_regist_btn_height"
              @click="onContinue"
            >
              <div class="login_btn_text">{{ $t('UI_Login_CPF_Continue') }}</div>
            </v-btn>
            <v-btn
              class="cpf_btn_return btn_radius"
              width="100%"
              :height="variables.login_regist_btn_height"
              @click="onReturn"
            >
              <div class="login_btn_text">{{ $t('UI_Return') }}</div>
            </v-btn>
          </div>
        </div>
      </v-form>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import variables from '@style/components/game-container.login.modules.scss'
import birthdayAge from '@/components/ui/birthday-age/index'
import RadioBox from '@/components/ui/radio-group/index'
import RadioGroup from '@/components/ui/radio-group/radio-group'
// import inputAutocompleteSuffix from '@/components/ui/input-autocomplete-suffix'
import UserSelect from '@/components/ui/user-select'
import { validateRegBindCpf, checkAddressByZipCode, regBindCpf } from '@/api/modules/login'
import { isEmail, isPhone } from '@/utils/helper'

export default {
  components: {
    birthdayAge,
    RadioBox,
    RadioGroup,
    // inputAutocompleteSuffix,
    UserSelect
  },
  props: {},
  data() {
    return {
      variables,
      birthdayError: false, // 出生日期错误
      radioList: [
        {
          label: this.$t('UI_Male'),
          value: 1
        },
        {
          label: this.$t('UI_Female'),
          value: 2
        }
      ],
      params: {
        // 参数
        reg_bind_cpf: '',
        name: '',
        birthday: '2000-11-13',
        cpf_gender: 1
      },
      formParams: {
        cpf_tel: '',
        cpf_email: '',
        cpf_postal_code: '',
        cpf_district: '',
        cpf_complement: '',
        cpf_city: '',
        cpf_street: '',
        cpf_country: 'Brasil'
      },
      originalCpf: '', // cpf 转换显示字段
      verifySuccess: false, // 验证成功
      isFirst: true, // 首次
      cpfAutoVerify: false, // 服务端验证
      cpfAddressAutoVerify: false, // 服务端验证
      addressVerifySuccess: false,
      cpf_street_verify_value: '', // 验证成功后的值
      isCpfVerifyPass: false // 是否验证通过过
    }
  },
  computed: {
    ...mapState('source', ['phonePlaces']),
    ...mapState('base', ['isApp', 'user', 'systemConfig', 'languageType']),
    defaultSize() {
      return this.size ? this.size : this.isApp ? 20 : 28
    },
    // 邮箱验证
    verificationEmail() {
      return [v => !!v || this.$t('UI_LOGIN_emailEmpty'), v => (v && isEmail(v)) || this.$t('UI_LOGIN_emailFormat')]
    },
    verificationPhone() {
      return [v => !!v || this.$t('UI.System_inputPhone'), v => (v && !isPhone(v)) || this.$t('UI.Phone_Format_Error')]
    },
    isCpfCItyFn() {
      return this.cpfAddressAutoVerify && this.formParams.cpf_city != ''
    },
    isDistrictFn() {
      return this.cpfAddressAutoVerify && this.formParams.cpf_district != ''
    },
    cpfStreetFn() {
      return this.cpfAddressAutoVerify && this.formParams.cpf_street != ''
    }
  },
  watch: {
    originalCpf: {
      handler(newVal, oldVal) {
        if (this.$u.againstCPF(newVal) != this.$u.againstCPF(oldVal)) this.verifySuccess = false
      }
    }
  },
  mounted() {
    this.cpfAutoVerify = false
    this.isFirst = true
    if (this.person_center_cpf_check_switch == 0) {
      this.verifySuccess = true
    }
  },
  methods: {
    commonStyle: (k = '') => variables[k],
    async onVerify() {
      this.cpfBlur()
      if (!this.params.reg_bind_cpf) {
        this.$pop.message().show({ content: this.$t('UI_Login_CPF_PleaseEnter') })
        return
      }
      const par = {
        token: this.user.token,
        req_type: 1,
        reg_bind_cpf: this.$u.againstCPF(this.params.reg_bind_cpf),
        language: this.languageType
      }
      let res = await validateRegBindCpf(par).catch(e => e)
      if (res.code != 0) {
        this.$pop.message().show({ content: res?.msg })
        return
      }
      const { data = {} } = res
      // cpf_check_auto_fill_switch == 1 服务端验证
      if (this.systemConfig?.cpf_check_auto_fill_switch == 1) {
        this.cpfAutoVerify = true
      }
      this.params.cpf_gender = data.cpf_gender
      this.params.name = data.name
      if (data?.birthday) {
        const birthday = data?.birthday.split('/')
        this.params.birthday = birthday[2] + '-' + birthday[1] + '-' + birthday[0]
      }
      this.verifySuccess = true
    },
    addressBlur() {
      // 如果当前修改值和之前值一样，就不调用接口
      if (this.cpf_street_verify_value == this.formParams.cpf_postal_code) return
      // this.cpf_street_verify_value = this.cpf_street_verify_value
      this.cpf_street_verify_value = this.formParams.cpf_postal_code
      // 如果有验证通过过验证，触发就该数据离开输入框就调用接口
      if (this.isCpfVerifyPass && this.systemConfig?.cpf_check_auto_fill_address_switch) {
        this.onAddressVerify()
        return
      }
      let cpf_postal_code = this.formParams.cpf_postal_code
      if (cpf_postal_code) {
        cpf_postal_code = cpf_postal_code.replace(/'-'/g, '')
      }
      if (
        (this.formParams.cpf_postal_code &&
          cpf_postal_code.length == 8 &&
          this.systemConfig?.cpf_check_auto_fill_address_switch == 1) ||
        (this.addressVerifySuccess && this.systemConfig?.cpf_check_auto_fill_address_switch)
      ) {
        this.onAddressVerify()
      }
    },
    addressInput(value) {
      let cpf_postal_code = ''
      if (value) {
        cpf_postal_code = value.replace(/[^0-9-]/g, '')
      }
      setTimeout(() => {
        this.formParams.cpf_postal_code = cpf_postal_code
      }, 10)
    },
    // 查询地址验证接口
    async onAddressVerify() {
      if (!this.formParams.cpf_postal_code && !this.addressVerifySuccess) return
      let res = await checkAddressByZipCode({
        zipcode: this.formParams.cpf_postal_code,
        language: this.languageType
      }).catch(e => e)
      if (res.code != 0) {
        this.$pop.message().show({ content: res?.msg })
        this.formParams.cpf_city = ''
        this.formParams.cpf_country = 'Brasil'
        this.formParams.cpf_district = ''
        this.formParams.cpf_street = ''
        this.cpfAddressAutoVerify = false
        this.addressVerifySuccess = false
        return
      }
      const { data = {} } = res
      this.formParams.cpf_city = data.cpf_city
      this.formParams.cpf_country = data.cpf_country
      this.formParams.cpf_district = data.cpf_district
      this.formParams.cpf_street = data.cpf_street
      this.cpfAddressAutoVerify = true
      this.addressVerifySuccess = true
      this.isCpfVerifyPass = true
      return
    },
    // cfp 装配
    cpfBlur() {
      let reg_bind_cpf = this.$u.createCPF(this.params.reg_bind_cpf)
      this.params.reg_bind_cpf = reg_bind_cpf
    },
    cpfFocus() {
      let reg_bind_cpf = this.$u.againstCPF(this.params.reg_bind_cpf)
      this.params.reg_bind_cpf = reg_bind_cpf
    },
    inputFn(value) {
      let reg_bind_cpf = value.replace(/[^0-9]/g, '')
      setTimeout(() => {
        this.params.reg_bind_cpf = reg_bind_cpf
      }, 10)
    },
    onSubmit() {
      if (this.person_center_cpf_check_switch == 1) {
        if (!this.verifySuccess) {
          this.$pop.message().show({ content: this.$t('UI_Login_CPF_PleaseEnter') })
          return
        }
      }
      if (this.params.name.trim() == '') {
        this.$pop.message().show({ content: this.$t('UI_Login_CPF_PleaseName') })
        return
      }
      if (this.systemConfig?.cpf_fields_switch_arr?.cpf_fields_show_switch != 1) {
        let info = {
          ...this.params,
          birthday: this.params.birthday.replace(/-/g, '/')
        }
        this.regBindCpfFn(info)
        // this.$emit('onCpfSubmit',info);
      }
      this.isFirst = false
    },
    onContinue() {
      this.isFirst = false
      if (this.systemConfig?.cpf_fields_switch_arr?.cpf_tel_switch == 2 && this.formParams.cpf_tel.trim() == '') {
        this.$pop.message().show({ content: this.$t('UI.System_inputPhone') })
        return
      }
      // 正确手机更新
      if (this.formParams.cpf_tel.trim() && isPhone(this.formParams.cpf_tel)) {
        this.$pop.message().show({ content: this.$t('UI.Phone_Format_Error') })
        return
      }
      if (this.systemConfig?.cpf_fields_switch_arr?.cpf_email_switch == 2 && this.formParams.cpf_email.trim() == '') {
        this.$pop.message().show({ content: this.$t('UI.System_45') })
        return
      }
      // 正确邮箱更新
      if (this.formParams.cpf_email.trim() && !isEmail(this.formParams.cpf_email)) {
        this.$pop.message().show({ content: this.$t('UI_LOGIN_emailFormat') })
        return
      }
      if (
        this.systemConfig?.cpf_fields_switch_arr?.cpf_postal_code_switch == 2 &&
        this.formParams.cpf_postal_code.trim() == true
      ) {
        this.$pop.message().show({ content: this.$t('UI_CPF_Postal_Empty') })
        return
      }
      if (this.systemConfig?.cpf_check_auto_fill_address_switch == 2) {
        if (!this.addressVerifySuccess) {
          this.$pop.message().show({ content: this.$t('UI_CPF_Need_Verify') })
          return
        }
      }
      if (this.systemConfig?.cpf_fields_switch_arr?.cpf_street_switch == 2 && this.formParams.cpf_street.trim() == '') {
        this.$pop.message().show({ content: this.$t('UI_CPF_Add_Empty') })
        return
      }
      if (
        this.systemConfig?.cpf_fields_switch_arr?.cpf_complement_switch == 2 &&
        this.formParams.cpf_complement.trim() == ''
      ) {
        this.$pop.message().show({ content: this.$t('UI_CPF_complement_Empty') })
        return
      }
      if (this.systemConfig?.cpf_fields_switch_arr?.cpf_city_switch == 2 && this.formParams.cpf_city.trim() == '') {
        this.$pop.message().show({ content: this.$t('UI_CPF_City_Empty') })
        return
      }
      if (
        this.systemConfig?.cpf_fields_switch_arr?.cpf_district_switch == 2 &&
        this.formParams.cpf_district.trim() == ''
      ) {
        this.$pop.message().show({ content: this.$t('UI_CPF_State_Empty') })
        return
      }
      let info = {
        cpf_extra_info: { ...this.formParams },
        birthday: this.params.birthday.replace(/-/g, '/'),
        name: this.params.name,
        reg_bind_cpf: this.$u.againstCPF(this.params.reg_bind_cpf),
        cpf_gender: this.params.cpf_gender,
        language: this.languageType,
        token: this.user.token
      }
      this.regBindCpfFn(info)
      //  this.$emit('onCpfSubmit',info);
    },
    async regBindCpfFn(info) {
      const res = await regBindCpf(info).catch(e => e)
      if (!res || res.code != 0) {
        this.$pop.message().show({ content: res.msg })
        return
      }
      if (res.code == 0) {
        let userData = this.user
        userData.reg_bind_cpf = this.$u.againstCPF(this.params.reg_bind_cpf)
        this.$store.commit('base/SET_USER', userData)
        this.onCloose()
      }
    },
    onReturn() {
      this.isFirst = true
    },
    onCloose() {
      this.$emit('onClose')
    }
  }
}
</script>

