<template>
  <div v-if="sportShow" class="comp nav_button">
    <div class="nav_btn" :class="{ active: routerName != 'sportView' }" @click="onBtn('menu', 'OTHER_BTN_BOTTOM_MENU')">
      <icon-font name="icon-Icon_Home" :size="commonStyle('nav_button_icon_size')" />
      <div class="nav_button_text">
        {{ $t('UI_Menu_AllGames') }}
      </div>
    </div>
    <div
      class="nav_btn"
      :class="{ active: routerName == 'sportView' }"
      @click="onBtn('sportView', 'OTHER_BTN_BOTTOM_SPORT')"
    >
      <icon-font name="icon-Icon_Sports" :size="commonStyle('nav_button_icon_size')" />
      <div class="nav_button_text">
        {{ $t('UI_Tab_Sports.sports') }}
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import _common from '@style/components/game-container.top-navigation.modules.scss'
import { AJ_DOTTING } from '@/utils/dotting.js'

export default {
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['systemConfig']),
    // 体育赛事开关 ext_game_sport_show:1 开启 0 关闭
    sportShow() {
      const { ext_game_sport_show = 0 } = this.systemConfig
      return ext_game_sport_show == 1
    },
    // 当前页面路由
    routerName() {
      const { name = '' } = this.$route
      return name
    }
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    onBtn(name = '', aj = '', enterRef = '') {
      if (aj) AJ_DOTTING.getAjdata(aj)
      if (this.$route.name === 'menu' && name === 'menu') return
      const routeQuery = {}
      if (name === 'menu') routeQuery.ref = 'casino'
      this.$router.push({ name, query: routeQuery })
    }
  }
}
</script>

<style scoped lang="scss">
@import '@style/components/game-container.top-navigation.modules.scss';

.comp.nav_button {
  @include setBox(row, center, center);
  background: $nav_button_container_bg;
  border-radius: $nav_button_radius;
  .nav_btn {
    @include setBox(row, center, center);
    gap: $nav_button_gap;
    width: $nav_button_width;
    height: $nav_button_height;
    color: $nav_button_active_color;
    font-weight: $nav_button_active_weight;
    font-size: $nav_button_active_font_size;
    border-radius: $nav_button_radius;
    cursor: pointer;
    &.active {
      background: $nav_button_active_bg;
      &:hover {
        @include setFilter($nav_button_active_filter, $nav_button_active_filter_type);
      }
    }
  }
}
</style>
