<!-- 文本段落自动调整字体大小 -->
<template>
  <div ref="wrapper_text" :style="styles">{{ text }}</div>
</template>

<script>
import textfit from 'textfit'
export default {
  name: 'ParagraphFit',
  props: {
    width: [String, Number],
    height: [String, Number],
    text: String,
    maxFont: {
      type: [String, Number],
      default: 16
    },
    minFont: {
      type: [String, Number],
      default: 6
    },
    // 配置项
    // https://github.com/STRML/textFit
    options: Object
  },
  computed: {
    styles() {
      return {
        width: this.width ? parseInt(this.width) + 'px' : '100%',
        height: this.height ? parseInt(this.height) + 'px' : '100%'
      }
    },
    textfitOptions() {
      return {
        maxFontSize: parseInt(this.maxFont),
        minFontSize: parseInt(this.minFont),
        multiLine: true,
        ...this.options
      }
    }
  },
  watch: {
    text(val) {
      this.reFiText(val)
    }
  },
  mounted() {
    setTimeout(() => {
      this.createdTextfit()
      window.addEventListener('resize', this.createdTextfit)
    }, 30)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.createdTextfit)
  },
  methods: {
    reFiText(text) {
      const $el = this.$refs['wrapper_text']
      if (!$el) return
      const fitWrapper = $el.querySelector('.textFitted')
      if (!fitWrapper) return
      fitWrapper.innerHTML = text
      textfit($el, this.textfitOptions)
    },
    createdTextfit() {
      const $el = this.$refs['wrapper_text']
      if (!$el) return
      textfit($el, this.textfitOptions)
    }
  }
}
</script>
