<template>
  <v-navigation-drawer class="comp drawer_right" :width="360" app right touchless :value="show" @input="onShow">
    <email-list :key="emailKey" />
  </v-navigation-drawer>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import EmailList from '@/components/email-modal/ui/email-list'
import MenuType from '@/events/menu.js'
import PublishSubscribe from 'publish-subscribe-js'

export default {
  components: {
    EmailList
  },
  model: {
    prop: 'show',
    event: 'change'
  },
  props: {
    show: {
      type: Boolean,
      require: true
    }
  },
  data() {
    return {
      // 用来更新站内信组件
      emailKey: 0
    }
  },
  computed: {
    ...mapState('modal', ['rightMenu'])
  },
  mounted() {
    // 更新信息
    PublishSubscribe.subscribe(MenuType.UPDATE_EMAIL, this.updateData)
  },
  beforeDestroy() {
    // 卸载订阅
    PublishSubscribe.unsubscribe(MenuType.UPDATE_EMAIL)
  },
  methods: {
    // 右侧栏显隐藏
    onShow(value = false) {
      this.$emit('change', value)
    },
    // 更新右侧栏数据
    updateData() {
      this.emailKey++
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.drawer-pannel.modules.scss';
</style>
