<template>
  <div class="skeleton game_list">
    <div v-if="isApp" class="game_list_app_tabs flex_row">
      <v-skeleton-loader class="game_item_app_tab" v-for="(s, i) in 3" :key="i" type="text"></v-skeleton-loader>
    </div>
    <div v-else class="game_list_pc_tabs">
      <v-skeleton-loader class="game_item_pc_tabs" type="text"></v-skeleton-loader>
    </div>
    <div class="skeleton_game" v-for="(s, i) in 3" :key="i">
      <div class="game_list_game_head">
        <v-skeleton-loader type="text"></v-skeleton-loader>
      </div>
      <div class="game_list_game_box flex_row">
        <v-skeleton-loader
          v-for="(s, i) in isApp ? 3 : 6"
          :width="cardWidth"
          :height="cardHeight"
          :key="i"
          type="image"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  props: {
    // 样式
    styleOpt: {
      type: Object,
      default() {
        return {}
      }
    },
    attrs: {
      type: Object,
      default() {
        return {
          boilerplate: true,
          elevation: 2
        }
      }
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp']),
    // 计算游戏卡片骨架屏宽度
    cardWidth() {
      if (this.isApp) {
        let w = 0
        const margin = 15
        const padding = 0
        const spacing = 6
        const size = this.containerBox.width || 375
        w = (size - margin * 2 - padding * 2 - spacing * 3) / 3
        return `${w}px`
      } else {
        let w = 0
        const spacing = 16
        const size = this.containerBox.width || 1027
        w = (size - spacing * 5) / 6
        return `${w}px`
      }
    },
    // 计算游戏卡片骨架屏高度
    cardHeight() {
      const rate = 11 / 8 //图片比例
      return `${parseInt(this.cardWidth) * rate}px`
    }
  }
}
</script>

<style lang="scss">
@import '@style/components/skeleton.scss';

.skeleton.game_list {
  margin: $game_list_marign;

  .game_list_app_tabs {
    .game_item_app_tab {
      width: $game_item_app_tab_width;
      margin: $game_item_app_tab_margin;

      &:first-child {
        margin-left: 0;
      }

      &.theme--dark.v-skeleton-loader .v-skeleton-loader__text {
        height: $game_item_app_tab_skeleton_height !important;
        border-radius: $game_item_app_tab_skeleton_radius !important;
      }
    }
  }

  .game_list_pc_tabs {
    .game_item_pc_tabs {
      width: $game_list_pc_tab_width;

      &.theme--dark.v-skeleton-loader .v-skeleton-loader__text {
        height: $game_item_pc_tab_skeleton_height !important;
      }
    }
  }

  .skeleton_game {
    margin: $game_list_game_margin;

    .game_list_game_head {
      padding: $game_list_game_head_padding;

      .theme--dark.v-skeleton-loader .v-skeleton-loader__text {
        height: $game_list_game_head_skeleton_height !important;
      }
    }

    .game_list_game_box {
      justify-content: space-between;

      .theme--dark.v-skeleton-loader .v-skeleton-loader__image {
        width: 100% !important;
        height: 100% !important;
      }
    }
  }
}
</style>
