<script>
export default {
  created() {
    if (document.getElementsByClassName('openAnimation')[0])
      document.getElementsByClassName('openAnimation')[0].remove()
  },
  mounted() {
    this.initContainerBox()
    window.onresize = () => {
      this.initContainerBox()
    }
  },
  methods: {
    initContainerBox() {
      if (!this.$refs.boxIds) return
      let box = this.$refs.boxIds.getBoundingClientRect()
      this.$store.commit('layout/SET_CONTAINER_BOX', box)
    }
  }
}
</script>
