<template>
  <v-container class="bind_box">
    <div class="bind_box_top">
      <div class="top_title break_word">{{ title || backTextFun }}</div>
      <div class="top_icon">
        <icon-font name="icon-Icon_Cloose" :size="loadVariables('dialog_close_icon_size')" @click="backClick" />
      </div>
    </div>
    <v-form ref="form">
      <!-- 绑定邮箱和手机号 -->
      <template v-if="sign == 'email' || sign == 'phone'">
        <v-row class="line_first" no-gutters>
          <!-- 邮箱 -->
          <v-col v-if="sign == 'email'" :cols="12" align="center">
            <v-text-field
              v-model="email"
              :placeholder="$t('UI_MINE_EmailRetirada')"
              background-color="t_input_bg"
              :height="fieldHeight"
              :disabled="!!timeSet"
              hide-details
              clearable
              single-line
              class="input_radius"
              solo
              flat
              dense
            >
              <template slot="prepend-inner">
                <div class="prepend-icon-wrap">
                  <icon-font name="icon-Icon_InBox" :size="loadVariables('bind_box_field_icon_size')" />
                </div>
              </template>
            </v-text-field>
          </v-col>
          <!-- 手机号 -->
          <v-col v-else :cols="12" align="center">
            <v-text-field
              v-model="phone"
              :placeholder="$t('UI.Charge_1_info_4')"
              background-color="t_input_bg"
              color="t_text_color_3"
              :height="fieldHeight"
              :disabled="!!timeSet"
              hide-details
              clearable
              class="input_radius"
              solo
              flat
              dense
            >
              <template slot="prepend-inner">
                <user-select
                  :list="phonePlaces"
                  :value="area"
                  :user-icon="$u.optionalChaining(phonePlaces[0], 'flag') || loadIcon('common_user')"
                  @change="onChangeArea"
                >
                </user-select>
              </template>
            </v-text-field>
          </v-col>
          <!-- 验证码 -->
          <v-col class="line" :cols="12" align="center">
            <v-row dense no-gutters>
              <v-text-field
                v-model="code"
                class="code input_radius"
                :height="fieldHeight"
                single-line
                solo
                flat
                dense
                hide-details
                background-color="t_input_bg"
                :placeholder="$t('UI.Login_0_main_10')"
              >
                <template slot="append">
                  <v-btn
                    small
                    color="t_text_color_3"
                    block
                    :disabled="sign == 'email' ? !(email && !timeSet) : !(phone && !timeSet)"
                    @click="onCode"
                  >
                    {{ time ? `${time}s` : $t('UI.Login_0_main_10') }}
                  </v-btn>
                </template>
              </v-text-field>
            </v-row>
          </v-col>
          <!-- 确定按钮 -->
          <v-col class="line_last">
            <v-btn
              width="100%"
              color="t_primary_dark_color"
              :disabled="sign == 'email' ? !(email && code) : !(phone && code)"
              @click="onSubmit"
            >
              {{ sign == 'phone' ? $t('UI_MINE_VinculativoBtn_Phone') : $t('UI_MINE_VinculativoBtn_Email') }}
            </v-btn>
          </v-col>
        </v-row>
      </template>
      <!-- 修改密码 -->
      <template v-else>
        <v-row class="line_first" no-gutters>
          <v-col :cols="12" align="center">
            <v-text-field
              v-model="oldPwd"
              class="input_radius"
              background-color="t_input_bg"
              :placeholder="$t('UI_MINE_SenhaAtual')"
              :type="!oldPwdShow ? 'password' : 'text'"
              :height="fieldHeight"
              maxlength="16"
              hide-details
              single-line
              solo
              flat
              dense
              outlined
            >
              <template slot="prepend-inner">
                <div class="prepend-icon-wrap">
                  <icon-font name="icon-Icon_Lock" :size="loadVariables('bind_box_field_icon_size')" />
                </div>
              </template>
              <template slot="append">
                <icon-font
                  class="cursor_point"
                  :name="oldPwdShow ? eye : off"
                  :size="loadVariables('bind_box_field_icon_size')"
                  @click="oldPwdShow = !oldPwdShow"
                />
              </template>
            </v-text-field>
          </v-col>
          <v-col class="line" :cols="12" align="center">
            <v-text-field
              v-model="newPwd"
              background-color="t_input_bg"
              class="input_radius"
              outlined
              :placeholder="$t('UI_Mine_New_Pwd_Place')"
              :type="!newPwdShow ? 'password' : 'text'"
              :height="fieldHeight"
              maxlength="16"
              hide-details
              single-line
              solo
              flat
              dense
            >
              <template slot="prepend-inner">
                <div class="prepend-icon-wrap">
                  <icon-font name="icon-Icon_Lock" :size="loadVariables('bind_box_field_icon_size')" />
                </div>
              </template>
              <template slot="append">
                <icon-font
                  class="cursor_point"
                  :name="newPwdShow ? eye : off"
                  :size="loadVariables('bind_box_field_icon_size')"
                  @click="newPwdShow = !newPwdShow"
                />
              </template>
            </v-text-field>
          </v-col>
          <v-col class="line" :cols="12" align="center">
            <v-text-field
              v-model="newPwdTwo"
              background-color="t_input_bg"
              class="input_radius"
              outlined
              :placeholder="$t('UI_Mine_Two_New_Pwd_Place')"
              :type="!newPwdTwoShow ? 'password' : 'text'"
              :height="fieldHeight"
              maxlength="16"
              hide-details
              single-line
              solo
              flat
              dense
            >
              <template slot="prepend-inner">
                <div class="prepend-icon-wrap">
                  <icon-font name="icon-Icon_Lock" :size="loadVariables('bind_box_field_icon_size')" />
                </div>
              </template>
              <template slot="append">
                <icon-font
                  class="cursor_point"
                  :name="newPwdTwoShow ? eye : off"
                  :size="loadVariables('bind_box_field_icon_size')"
                  @click="newPwdTwoShow = !newPwdTwoShow"
                />
              </template>
            </v-text-field>
          </v-col>
          <!-- 确定按钮 -->
          <v-col class="line_last">
            <v-btn
              width="100%"
              color="t_primary_dark_color"
              :disabled="!(oldPwd && newPwd && newPwdTwo)"
              @click="onSubmitPwd"
            >
              {{ $t('UI_ChangePassword') }}
            </v-btn>
          </v-col>
        </v-row>
      </template>
    </v-form>
    <template v-if="recaptchaState">
      <vue-recaptcha-verify
        ref="recaptchaRef"
        :titleText="$t('UI.Login_Regist_Tips')"
        :contentText="$t('UI.Login_Regist_Content_Text')"
        v-model="recaptchaState"
        @onSuccess="onSuccess"
        @onBack="onBack"
      ></vue-recaptcha-verify>
    </template>
  </v-container>
</template>

<script>
import { getEmailcode, get_code, userBingAccount, userBingTel, changePwd } from '@/api/modules/login'
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import UserSelect from '@/components/ui/user-select'
import variables from '@style/components/ui.modules.scss'
import VueRecaptchaVerify from '@/components/ui/vue-recaptcha-verify/index'

export default {
  name: 'BindInfo',
  components: {
    UserSelect,
    VueRecaptchaVerify
  },
  props: {
    // 分享的数据
    bindData: {
      type: String,
      default: ''
    },
    // title
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      sign: 'email', // 标识类型 email=邮箱 phone:手机号 pwd:密码
      email: '', // 邮箱
      area: '', // 手机号前缀
      phone: '', // 手机号
      code: '', // 验证码
      time: '', // 秒数
      timeSet: null, // 倒计时方法
      oldPwd: '', // 旧密码
      newPwd: '', // 新密码
      newPwdTwo: '', // 二次新密码
      oldPwdShow: false, // 旧密码显示
      newPwdShow: false, // 新密码显示
      newPwdTwoShow: false, // 二次新密码显示
      recaptchaState: false, // 谷歌图片开关
      recaptcha: '', // 谷歌图片验证返回值
      currentRecaptcha: 1, // 1 getcode验证码 2 修改验证
      verify_img_params: '' // 图形自验验证码加密code
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'deviceid']),
    ...mapState('source', ['phonePlaces']),
    // 输入框的高度
    fieldHeight() {
      return 40
    },
    // 查看密码图标 显示
    eye() {
      return 'icon-Icon_Eye'
    },
    // 查看密码图标 隐藏
    off() {
      return 'icon-Icon_Closseeye'
    },
    backTextFun() {
      if (this.sign == 'email') return this.$t('UI_Edit_EMail')
      if (this.sign == 'phone') {
        return this.$t('UI_Edit_Phone_Number')
      } else {
        return this.$t('UI_Reset_Password')
      }
    }
  },
  mounted() {
    // sign： email=邮箱 phone:手机号
    this.sign = this.bindData
    this.area = this.phonePlaces[0]?.code
  },
  beforeDestroy() {
    this.endTime()
  },
  methods: {
    loadVariables(name) {
      return variables[name]
    },
    loadIcon(type) {
      return loadIcon(type, false)
    },
    // 选择手机号前缀
    onChangeArea(par) {
      const { code = '' } = par
      this.area = code
    },
    // 请求验证码
    async onCode() {
      // 邮箱参数
      const paramsEmail = {
        token: this.user.token,
        email: this.email,
        language: this.languageType,
        type: 1,
        device_id: this.deviceid
      }
      // 手机号参数
      const paramsPhone = {
        token: this.user.token,
        tel: this.area + this.phone,
        area: this.area,
        language: this.languageType,
        type: 1,
        device_id: this.deviceid
      }

      this.$pop.loading().show()

      if (this.recaptchaState) {
        paramsEmail.recaptcha = this.recaptcha
        paramsPhone.recaptcha = this.recaptcha
      }

      // 图形自验验证码加密串
      if (this.verify_img_params) {
        paramsEmail.verify_img_params = this.verify_img_params
        paramsPhone.verify_img_params = this.verify_img_params
      }
      this.verify_img_params = ''

      // sign： email=邮箱 phone:手机号
      const getInterface = this.sign == 'email' ? getEmailcode : get_code
      const params = this.sign == 'email' ? paramsEmail : paramsPhone
      const res = await getInterface(params).catch(e => e)
      const { code, data = 0, msg = '' } = res

      this.$pop.loading().hide()
      // 谷歌验证码弹起来
      if (res && res.code == 300) {
        this.currentRecaptcha = 1
        this.recaptchaState = true
        return
      }
      // code == 301 图形验证自验验证码
      if (code == 301) {
        const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = data
        const resDrag = await this.$pop
          .imgDrag({ baseSrc, checkCode, checkSrc })
          .show()
          .catch(e => e)
        if (resDrag == 'cancel') return

        const { encrypted = '' } = resDrag
        this.verify_img_params = encrypted
        this.onCode()
        return
      }

      // 关闭谷歌图片验证弹窗
      this.recaptchaState = false

      if (code != 0) return this.$pop.message().show({ content: msg })

      this.$pop.message().show({ content: msg })
      this.time = Number(data) || 60

      this.startTime()
    },
    // 开始倒计时
    startTime() {
      this.timeSet = setInterval(() => {
        this.time--

        // 判断 this.time 不存在了，就销毁定时器
        if (!this.time) this.endTime()
      }, 1 * 1000)
    },
    // 销毁定时器
    endTime() {
      if (this.timeSet) {
        clearInterval(this.timeSet)
        this.timeSet = null
      }
    },
    // 修改绑定邮箱 或 手机号
    async onSubmit() {
      let paramsEmail = {
        token: this.user.token,
        account: this.email,
        code: this.code,
        language: this.languageType,
        device_id: this.deviceid
      }
      let paramsPhone = {
        token: this.user.token,
        area: this.area,
        tel: this.phone,
        code: this.code,
        language: this.languageType,
        device_id: this.deviceid
      }

      if (this.recaptchaState) {
        paramsEmail.recaptcha = this.recaptcha
        paramsPhone.recaptcha = this.recaptcha
      }

      // 图形自验验证码加密串
      if (this.verify_img_params) {
        paramsEmail.verify_img_params = this.verify_img_params
        paramsPhone.verify_img_params = this.verify_img_params
      }
      this.verify_img_params = ''

      this.$pop.loading().show()
      const getInterface = this.sign == 'email' ? userBingAccount : userBingTel
      const params = this.sign == 'email' ? paramsEmail : paramsPhone
      const res = await getInterface(params).catch(e => e)
      const { code, msg = '', data } = res

      this.$pop.loading().hide()
      // 谷歌验证码弹起来
      if (res && res.code == 300) {
        this.currentRecaptcha = 2
        this.recaptchaState = true
        return
      }
      // code == 301 图形验证自验验证码
      if (code == 301) {
        const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = data
        const resDrag = await this.$pop
          .imgDrag({ baseSrc, checkCode, checkSrc })
          .show()
          .catch(e => e)
        if (resDrag == 'cancel') return

        const { encrypted = '' } = resDrag
        this.verify_img_params = encrypted
        this.onSubmit()
        return
      }

      // 关闭谷歌图片验证弹窗
      this.recaptchaState = false
      this.$pop.message().show({ content: msg })
      if (code != 0) {
        this.code = ''
        this.time = ''
        this.endTime()
        return
      }

      // 手动更新用户信息里的数据
      if (this.sign == 'email') {
        this.$store.commit('base/SET_USER', { ...this.user, account: this.email })
      } else {
        this.$store.commit('base/SET_USER', { ...this.user, tel: this.phone, area: data.area })
      }

      this.$emit('submit', this.sign)
      this.backClick()
    },
    // 修改密码
    async onSubmitPwd() {
      // 一次新密码，格式校验
      if (!this.$u.isPwdCheck(this.newPwd))
        return this.$pop.message().show({ content: this.$t('UI.Pwd_Format_Check_Text') })
      // 新密码与二次新密码不一样
      if (this.newPwd != this.newPwdTwo) return this.$pop.message().show({ content: this.$t('UI_ChangePassword3') })
      // 新旧密码一样
      if (this.oldPwd == this.newPwdTwo) return this.$pop.message().show({ content: this.$t('UI_ChangePassword2') })
      const params = {
        token: this.user.token,
        old_pwd: this.oldPwd,
        new_pwd: this.newPwd,
        language: this.languageType,
        device_id: this.deviceid
      }
      if (this.recaptchaState) {
        params.recaptcha = this.recaptcha
      }

      // 图形自验验证码加密串
      if (this.verify_img_params) params.verify_img_params = this.verify_img_params
      this.verify_img_params = ''

      this.$pop.loading().show()
      const res = await changePwd(params).catch(e => e)
      const { code, data = {}, msg } = res
      this.$pop.loading().hide()
      // 谷歌验证码弹起来
      if (res && res.code == 300) {
        this.currentRecaptcha = 3
        this.recaptchaState = true
        return
      }
      // code == 301 图形验证自验验证码
      if (code == 301) {
        const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = data
        const resDrag = await this.$pop
          .imgDrag({ baseSrc, checkCode, checkSrc })
          .show()
          .catch(e => e)
        if (resDrag == 'cancel') return

        const { encrypted = '' } = resDrag
        this.verify_img_params = encrypted
        this.onSubmitPwd()
        return
      }

      // 关闭谷歌图片验证弹窗
      this.recaptchaState = false
      this.$pop.message().show({ content: msg })

      if (code == 0) {
        this.$emit('back')
      }
    },
    backClick() {
      this.$emit('back')
    },
    // 谷歌验证码成功后返回数据
    onSuccess(res) {
      this.recaptcha = res
      if (this.currentRecaptcha == 1) {
        this.onCode()
      }
      if (this.currentRecaptcha == 2) {
        this.onSubmit()
      }
      if (this.currentRecaptcha == 3) {
        this.onSubmitPwd()
      }
    },
    onBack() {
      this.recaptchaState = false
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.bind_box {
  background-color: $bind_box_background;
  width: 100%;
  height: 100%;
  padding: $bind_box_padding;
  border-radius: 4px;
}

.col_title {
  font-weight: $bind_box_title_font_weight;
  font-size: $bind_box_title_font_size;
  color: $bind_box_title_color;
  text-align: center;
}
.col_icon {
  position: absolute;
  right: $bind_box_field_offset_right;
  top: 50%;
  transform: translate(0, -50%);
  cursor: pointer;
  color: $bind_box_close_icon_color;
}

.line,
.line_first,
.line_last {
  padding: $bind_box_line_pa;
}

.line_first {
  margin-top: $bind_box_line_first_margin_top;
}

.line_last {
  margin-top: $bind_box_line_last_margin_top;
  padding-bottom: $bind_box_line_last_padding_bottom !important;
}

.line {
  margin-top: $bind_box_line_margin_top;
}

.bind_box_top {
  width: 100%;
  position: relative;
  background: $comp_dialog_head_bg;
  padding: $bind_box_top_padding;
  @include setBox(row, space-between, center);
  line-height: 1.2;

  .top_title {
    width: 100%;
    padding: 0 32px;
    text-align: center;
  }

  .top_icon {
    cursor: pointer;
    @include setBox(row, center, center);
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translateY(-50%);
  }
}
.prepend-icon-wrap {
  margin-right: $bind_prepend_icon_wrap_mr;
}

// 针对pc样式
@include breakpoint-only-up() {
  .bind_box {
    width: 343px;
  }
}
</style>
