<template>
  <div v-if="state" v-zinx="{ page: zinx }">
    <div ref="wrapperMaskrRef" class="wrapper-mask" @click="onMaskClose"></div>
    <div class="custom_dialog" :class="{ full }">
      <div ref="customDialogRef" class="custom_dialog_box">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import { POP_TIME } from '@/api/setting'

export default {
  model: {
    prop: 'visible',
    event: 'change'
  },
  props: {
    // 层级显示调整
    zinx: {
      type: String,
      default: 'custom'
    },
    // 移动端是否全屏
    full: {
      type: Boolean,
      default: false
    },
    // 是否显示
    visible: {
      type: Boolean,
      default: false
    },
    // 是否点击遮罩关闭
    mask: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      state: false
    }
  },
  watch: {
    visible(n) {
      if (n) return this.show()
      this.close()
    }
  },
  methods: {
    // 显示
    show() {
      this.state = !this.state

      this.$nextTick(() => {
        const customDialogRef = this.$refs['customDialogRef']
        if (customDialogRef) {
          // 动画状态1
          if (!this.full) this.$anim.insert(customDialogRef)
          // 动画状态2
          if (this.full) this.$anim.insert2(customDialogRef)
        }

        // 遮罩区域控制
        const wrapperMaskrRef = this.$refs['wrapperMaskrRef']
        if (wrapperMaskrRef) {
          const rect = wrapperMaskrRef.getBoundingClientRect() || {}
          const { bottom = 0, left = 0, right = 0, top = 0 } = rect
          wrapperMaskrRef.style.top = `-${top}px`
          wrapperMaskrRef.style.left = `-${left}px`
          wrapperMaskrRef.style.right = `-${right}px`
          wrapperMaskrRef.style.bottom = `-${bottom}px`
        }
      })
    },
    // 关闭
    close() {
      this.$nextTick(() => {
        const customDialogRef = this.$refs['customDialogRef']

        if (customDialogRef) {
          // 动画状态1
          if (!this.full) this.$anim.close(customDialogRef)
          // 动画状态2
          if (this.full) this.$anim.close2(customDialogRef)

          setTimeout(() => {
            this.state = false
            this.$emit('change', false)
            this.$emit('close', false)
          }, POP_TIME)
        }
      })
    },
    // 遮罩关闭
    onMaskClose() {
      if (!this.mask) return
      this.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.custom_dialog {
  width: 92%;
  position: fixed;
  top: 50%;
  left: 50%;
  right: unset;
  bottom: unset;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  &.full {
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    bottom: constant(safe-area-inset-bottom);
    bottom: env(safe-area-inset-bottom);
    transform: none;
  }
  .custom_dialog_box {
    width: 100%;
    height: 100%;
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .custom_dialog {
    width: auto;
    position: fixed;
    top: 50%;
    left: 50%;
    right: unset;
    bottom: unset;
    transform: translate(-50%, -50%);
    @include setBox(row, center, center);
    &.full {
      width: auto;
      top: 50%;
      left: 50%;
      right: unset;
      bottom: unset;
      transform: translate(-50%, -50%);
    }
    .custom_dialog_box {
      width: auto;
      height: auto;
      @include setBox(row, center, center);
    }
  }
}
</style>
