<template>
  <div class="comp space-ui">
    <component v-for="(item, i) in signArr" :key="i" :is="item" :attrs="attrs" :style-opt="styleOpt"></component>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import banner from './ui/banner.vue'
import search from './ui/search.vue'
import gameList from './ui/game-list.vue'
import activityList from './ui/activity-list.vue'
import activityImg from './ui/activity-img.vue'
import activityDetail from './ui/activity-detail.vue'
import gridList from './ui/grid-list.vue'
import pesquisaTabs from './ui/pesquisa-tabs.vue'
import pesquisaList from './ui/pesquisa-list.vue'
import recompensas from './ui/recompensas.vue'
import recomendarLnvite from './ui/recomendar-lnvite.vue'
import washCode from './ui/wash-code.vue'
import regression from './ui/regression.vue'

export default {
  name: 'space-ui',
  components: {
    m_carousel: banner, // 首页轮播图
    search: search, // 搜索
    game_list: gameList, // 首页游戏列表
    activity_list: activityList, // 活动列表
    activity_img: activityImg, // 活动列表 -- 图片
    activity_detail: activityDetail, // 活动列表 -- 详情
    grid_list: gridList, // 更多游戏, 游戏列表子页
    pesquisa_tabs: pesquisaTabs, // 搜索页面 -- tabs
    pesquisa_list: pesquisaList, // 搜索页面列表
    recompensas: recompensas, // vip页面
    recomendarLnvite: recomendarLnvite, // 分享 lnvite tab
    washCode: washCode, // 洗码页面
    regression: regression // 回归任务
  },
  props: {
    // 标识页面 "sign" 或 ["sign", "sign"]
    sign: {
      type: [String, Array],
      default: ''
    },
    // 样式
    styleOpt: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp']),
    // 计算游戏卡片骨架屏宽度
    cardWidth() {
      if (this.isApp) {
        let w = 0
        const margin = 15
        const padding = 0
        const spacing = 6
        const size = this.containerBox.width
        w = (size - margin * 2 - padding * 2 - spacing * 3) / 3
        return `${w}px`
      } else {
        let w = 0
        const spacing = 16
        const size = this.containerBox.width
        w = (size - spacing * 5) / 6
        return `${w}px`
      }
    },
    // 计算游戏卡片骨架屏高度
    cardHeight() {
      const rate = 11 / 8 //图片比例
      return `${parseInt(this.cardWidth) * rate}px`
    },
    // 标识处理
    signArr() {
      if (this.sign && typeof this.sign == 'string') return [this.sign]
      return this.sign
    }
  },
  data() {
    return {
      attrs: {
        boilerplate: true,
        elevation: 2
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/space-ui.css';
</style>
