<template>
  <div class="comp back_bar">
    <div v-if="isApp" class="back_app flex_row" :style="`padding: ${pyHeight ? pyHeight : 20}px 0;`">
      <div class="left flex_row">
        <div class="back_l cursor_point c_box" :style="`width: ${defaultSize}px;`" @click="doBack">
          <icon-font v-if="leftArrow" name="icon-Icon_Back" :size="defaultSize" />
        </div>
        <span class="center" :class="`text_${center}`">{{ backText }}</span>
      </div>
      <div class="right">
        <div class="cursor_point c_box" :style="`width: ${defaultSize}px;`" @click="close">
          <icon-font v-if="rightArrow" name="icon-Icon_Cloose" :size="defaultSize" />
        </div>
      </div>
    </div>

    <div v-else class="back_pc flex_row" :style="`padding: ${pyHeight ? pyHeight : 26}px 0;`">
      <div class="left flex_row">
        <div class="back_l cursor_point c_box" :style="`width: ${defaultSize}px;`" @click="doBack">
          <icon-font v-if="leftArrow" name="icon-Icon_Back" :size="defaultSize" />
        </div>
        <span class="center" :class="`text_${center}`">{{ backText }}</span>
      </div>
      <div class="right cursor_point">
        <div class="right_icon c_box" :style="`width: ${defaultSize}px;`" @click="close">
          <icon-font v-if="rightArrow" name="icon-Icon_Cloose" :size="defaultSize" />
        </div>
      </div>
    </div>

    <div class="br_back" v-if="line"></div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  props: {
    // 图标大小
    size: {
      type: [Number, String],
      default: ''
    },
    // 是否是左边模式
    leftArrow: {
      type: Boolean,
      default: true
    },
    // 是否是右边边模式
    rightArrow: {
      type: Boolean,
      default: false
    },
    // 返回的文字提示
    backText: {
      type: String,
      default: ''
    },
    // 是否显示底部边线
    line: {
      type: Boolean,
      default: true
    },
    // 标题是否居中
    center: {
      type: String,
      default: 'center'
    },
    pyHeight: {
      type: [Number],
      default: 0
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    // 默认icon大小
    defaultSize() {
      return this.size ? this.size : this.isApp ? 16 : 24
    }
  },
  methods: {
    doBack() {
      if (this.$listeners && this.$listeners.back) {
        this.$emit('back')
      } else {
        this.$router.go(-1)
      }
    },
    close() {
      this.$emit('close')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.comp.back_bar {
  width: $back_bar_page_width;

  .cursor_point {
    cursor: pointer;
  }

  .back_app {
    width: $back_bar_app_width;

    .left {
      flex-grow: 1;
      flex-shrink: 1;

      .back_l {
        margin: $back_bar_app_left_margin;
        flex-grow: 0;
        flex-shrink: 0;
      }

      .center {
        font-size: $back_bar_app_center_text_size;
        font-weight: $back_bar_app_center_text_bold;
        padding: $back_bar_app_center_text_padding;
        flex-grow: 1;
        flex-shrink: 1;
      }
    }

    .right {
      margin: $back_bar_app_right_margin;
      flex-grow: 0;
      flex-shrink: 0;
    }
  }

  .back_pc {
    width: $back_bar_pc_width;

    .left {
      flex-grow: 1;
      flex-shrink: 1;

      .back_l {
        margin: $back_bar_pc_left_margin;
        flex-grow: 0;
        flex-shrink: 0;
      }

      .center {
        font-size: $back_bar_pc_center_text_size;
        font-weight: $back_bar_pc_center_text_bold;
        padding: $back_bar_pc_center_text_padding;
        flex-grow: 1;
        flex-shrink: 1;
      }
    }

    .right {
      flex-grow: 0;
      flex-shrink: 0;

      .right_icon {
        margin: $back_bar_pc_right_margin;
      }
    }
  }

  .br_back {
    width: 100%;
    border-bottom: 1px solid $back_bar_border;
  }
}
</style>
