<template>
  <v-bottom-navigation class="comp bottom_nav" app>
    <div v-for="(item, index) in navList" :key="index" class="nav_item">
      <bottom-tab v-zoom :nav-item="item" @click.native="changePage(item)" />
    </div>
  </v-bottom-navigation>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import BottomTab from './bottom-tab'
import navList from '@source/config/bottom_nav.js'
import { AJ_DOTTING } from '@/utils/dotting.js'
import { hasShowGuideInstall } from '@/utils/hasShowGuideInstall'

export default {
  components: {
    BottomTab
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['systemConfig', 'user']),
    ...mapGetters('base', ['isTourist']),
    /**
     * 底部导航栏数据
     * navigation_switch 底部导航是否是后台配置 1:开 2:关
     * navigation_config 配置信息 ----> is_show：0:隐藏，1:显示
     */
    navList() {
      const { navigation_switch = 2, navigation_config = [] } = this.systemConfig

      const list = navList.map(item => {
        const { sign = '' } = item
        const navItem = navigation_config.find(f => f.name.toLocaleLowerCase() == sign.toLocaleLowerCase())
        if (!navItem) return null
        if (navigation_switch == 1) {
          // 如果底部导航后台配置开启判断is_show是否开启
          return +navItem?.is_show ? { ...item, ...navItem } : null
        }
        return item
      })

      return list.filter(Boolean).sort((a, b) => Number(b.weight) - Number(a.weight))
    }
  },
  methods: {
    // 点击跳转路由
    changePage(params = {}) {
      const { router = '', aj = '' } = params
      if (this.$route.name == router) return

      if (aj) AJ_DOTTING.getAjdata(aj)
      // 站内信
      if (router == 'inbox') {
        if (!this.isTourist) return this.$store.dispatch('modal/setEmailState', true)

        this.$store.dispatch('modal/setLoginRegistShow')
        return
      }
      // 强制按照apk
      if (router == 'sportView' && hasShowGuideInstall(0)) this.$pop.guideInstall().show()

      const routeQuery = {}
      if (router === 'menu') routeQuery.ref = 'casino'
      this.$router.push({ name: router, query: routeQuery })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.bottom-navigation.modules.scss';

.comp.bottom_nav {
  display: flex;
  width: 100%;
  left: 0;
  bottom: 0;
  height: $bottom_navigation_height !important;
  height: calc(#{$bottom_navigation_height} + constant(safe-area-inset-bottom)) !important;
  height: calc(#{$bottom_navigation_height} + env(safe-area-inset-bottom)) !important;
  padding-bottom: 0;
  box-sizing: border-box;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
  background-color: $bottom_navigation_bg;
  border-top: $bottom_navigation_border_top_color;
  z-index: $bottom_navigation_zindex !important;
  overflow: hidden;
  .nav_item {
    flex: 1;
  }
}
@media (min-width: 600px) {
  .comp.bottom_nav {
    display: none;
  }
}
</style>
