<template>
  <img :src="imgSrc" :style="`width: ${width}px; height: ${height}px;`" />
</template>

<script>
import loadIcon from '@/assets/icons'
export default {
  props: {
    width: {
      type: [String, Number],
      default: 50
    },
    height: {
      type: [String, Number],
      default: 50
    },
    item: {
      type: Object,
      default() {
        return {
          url: '',
          fury_icon: ''
        }
      }
    }
  },
  watch: {
    item: {
      handler() {
        this.loadImg()
      },
      immediate: true
    }
  },
  data() {
    return {
      imgSrc: loadIcon('common_bg_gameicon', false) // 默认占位图
    }
  },
  methods: {
    // 图片加载
    async loadImg() {
      const { url = '', fury_icon = '' } = this.item
      if (url) {
        const res = await this.$u.isImgLoad(url).catch(e => e)
        if (res) {
          this.imgSrc = url
          return
        }
      }
    }
  }
}
</script>
