<template>
  <div class="comp bank_account btn_radius">
    <span class="symbol" style="line-height: 1">{{ currencySymbol }}</span>
    <!-- bank_account_btn_text 是金币动画效果class  -->
    <span class="bank_account_btn_text gold_text text_1" style="line-height: 1">{{ goldText }}</span>
    <template v-if="!isSportPage">
      <icon-font
        class="refresh_icon"
        :class="{ rotate: isRotate }"
        name="icon-Icon_Refresh"
        :size="commonStyle('bank_account_refresh_icon_size')"
        :dark="commonStyle('bank_account_refresh_icon_color')"
        @click="refreshDigit"
      >
      </icon-font>
    </template>

    <!-- 金额加入动画 -->
    <div class="animated_gold_box" :class="{ animated_fade: inviteGiftGold > 0 }">
      <div class="gold_box_view">
        <img class="gold_box_img" :src="getCacheIcon('referGift_turntable_gold')" alt="" />
        <span>{{ inviteGiftGold }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { isFormatMillageAndNumber } from '@/utils/helper'
import _common from '@style/components/game-container.top-navigation.modules.scss'
import { getCacheIcon } from '@/assets/imgCache.js'

export default {
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    ...mapState('source', ['isRotate', 'inviteGiftGold']),
    ...mapState('sport', ['betbyMoney']),
    // 货币符号
    currencySymbol() {
      const { currency_symbol = '' } = this.systemConfig
      return this.currencyShow ? currency_symbol : ''
    },
    // 用户金额
    goldNumber() {
      return this.$u.lodash.isBlank(this.user.gold) ? 0.0 : this.user.gold
    },
    // 判断当前页面是否是体育游戏
    isSportPage() {
      const { name = '' } = this.$route
      return name == 'sportView'
    },
    // 金额显示
    goldText() {
      if (this.isRotate) return this.$t('SC_goldRefreshLoading')
      if (this.isSportPage) return this.isFormatMillageAndNumber(this.betbyMoney, true)

      return this.isFormatMillageAndNumber(this.goldNumber, true)
    }
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    },
    // 点击刷新金币
    refreshDigit() {
      this.$store.commit('source/SET_IS_ROTATE', true)
      this.$store.dispatch('base/refreshAccount', () => {
        this.$store.commit('source/SET_IS_ROTATE', false)
      })
    },
    // 千分比转换
    isFormatMillageAndNumber(key, isState) {
      return isFormatMillageAndNumber(key, isState, this.systemConfig?.thousand_mark || 0)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.top-navigation.modules.scss';

.comp.bank_account {
  min-width: $bank_account_btn_width;
  height: $top_navigation_btn_height;
  border: 1px solid $border_color;
  padding: $bank_account_btn_padding !important;
  position: relative;
  // overflow: hidden;
  @include setBox(row, center, center);
  .symbol {
    flex-shrink: 0;
    flex-grow: 0;
    font-size: $bank_account_currency_font_size;
    font-weight: $bank_account_currency_font_weight;
    color: $text_color_1;
  }
  .gold_text {
    flex-shrink: 1;
    flex-grow: 1;
    font-size: $bank_account_text_font_size;
    font-weight: $bank_account_text_font_weight;
    margin: $bank_account_text_margin;
    text-align: left;
  }
  .refresh_icon {
    flex-shrink: 0;
    flex-grow: 0;
    &.rotate {
      animation-name: fadenum;
      animation-duration: 0.5s;
      animation-timing-function: linear;
      animation-delay: 0;
      animation-iteration-count: infinite;
    }
  }
  .animated_gold_box {
    display: none;
    position: absolute;
    top: 10px;
    left: 0px;
    margin: 50px;
    &.animated_fade {
      display: inline-block;
      /* 开始动画，动画时长2秒，循环次数2，延迟开始时间为1秒 */
      animation: growAndFade 2s ease-in-out;
    }
    .gold_box_view {
      @include setBox(row, center, center);
      .gold_box_img {
        width: 24px;
        height: 24px;
      }
    }
  }
}

@keyframes growAndFade {
  0% {
    transform: scale(0);
    opacity: 0;
    filter: alpha(opacity=0);
  }
  50% {
    transform: scale(1); /* 动画过程中放大至原始大小 */
    opacity: 1;
    filter: alpha(opacity=100);
  }
  75% {
    transform: translateY(-60px) scale(1.5); /* 向上移动并稍微放大 */
    opacity: 1;
    filter: alpha(opacity=100);
  }
  100% {
    transform: translateY(-50px) scale(2); /* 向上移动并最终放大 */
    opacity: 0;
    filter: alpha(opacity=0);
  }
}
@keyframes fadenum {
  100% {
    transform: rotate(180deg);
  }
}
</style>
