<template>
  <div class="simple_tab">
    <div v-if="isAppType" class="simple_tab_app br_back">
      <div v-if="showAutoFont" class="tabs">
        <div
          v-for="(t, i) in list"
          :key="i"
          class="tab_item"
          :class="[value == i ? 'active recharge_back_13' : 'recharge_back_tran_13']"
          :style="{ fontSize: `${fontSize}px` }"
          @click="change(i)"
        >
          <div :style="{ width: '100%', height }">
            <auto-font
              :uuid="`simple_${i}`"
              :fontWeight="loadVariables('simple_tab_item_weight')"
              :maxFont="fontSize"
              :level="2"
              :text="t"
            />
          </div>
          <span v-if="borderBottm" class="border_bottm"></span>
        </div>
      </div>
    </div>
    <div v-if="isIcon" class="close_right cursor_point" @click="closeDialog">
      <icon-font name="icon-Icon_Cloose" :size="loadVariables('simple_close_size')" dark="t_text_color_2" />
    </div>
    <!-- <div v-else class="simple_tab_pc m_mt_20">
      <div
        v-for="(t, i) in list"
        :key="i"
        class="pc_tab_item m_px_15 m_py_6"
        :class="[value == i ? 'pc_active' : '', `m_font_${fontSize}`]"
        @click="change(i)"
      >
        {{ t }}
      </div>
    </div> -->
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/components/ui.modules.scss'

export default {
  props: {
    value: {
      type: Number,
      default: 0
    },
    list: {
      type: Array,
      default() {
        return []
      }
    },
    isAppType: {
      type: Boolean,
      default: true
    },
    fontSize: {
      type: [String, Number],
      default: parseInt(variables.tab_btn_default_font_size)
    },
    isIcon: {
      type: Boolean,
      default: true
    },
    height: {
      type: [String],
      default: variables.tab_btn_default_height
    },
    borderBottm: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      size: 16,
      showAutoFont: true
    }
  },
  computed: {
    ...mapState('base', ['isApp'])
  },
  watch: {
    isApp() {
      this.showAutoFont = false
      setTimeout(() => {
        this.showAutoFont = true
      }, 200)
    }
  },
  methods: {
    loadVariables: (name = '') => variables[name],
    change(i) {
      this.$emit('change', i)
    },
    closeDialog() {
      this.$emit('close')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

// 针对移动端样式
.simple_tab {
  width: 100%;
  position: relative;
  .simple_tab_app {
    width: 100%;
    position: relative;
    @include setBox(row, center, center);
    .tabs {
      width: $simple_tabs_app_width;
      @include setBox(row, center, center);
      .tab_item {
        height: 100%;
        width: 100%;
        @include setBox(column, center, center);
        color: $tab_btn_title_color;
        flex: 1;
        cursor: pointer;
        .border_bottm {
          width: $tab_border_bottom_width;
          height: $tab_border_bottom_height;
          background: transparent;
        }
        &.active {
          color: $tab_btn_title_active_color;
          .border_bottm {
            background: $tab_btn_line_active_bg;
          }
        }
      }
    }
  }
  .close_right {
    position: absolute;
    right: $simple_close_right_offset_right;
    top: 50%;
    padding: $simple_close_right_pa;
    transform: translateY(-50%);
    cursor: pointer;
  }
  .br_back {
    border-bottom: 1px solid $tab_btn_line_bg;
  }
  // .simple_tab_pc {
  // 	.pc_tab_item {
  // 		// color: $color_3;
  // 		text-align: center;
  // 		cursor: pointer;
  // 		border: 2px solid transparent;
  // 	}
  // 	.pc_active {
  // 		// color: $color;
  // 		// border: 2px solid #2283F6 !important;
  // 		border-radius: $tab_pac_active_radius;
  // 	}
  // }
}

// 针对pc样式
@include breakpoint-only-up() {
  .simple_tab {
    .simple_tab_app {
      .tabs {
        width: $simple_tabs_pc_width;
      }
    }
  }
}
</style>
