<template>
  <v-app-bar class="comp top_nav_bar" height="68" app flat>
    <android-down />
    <!-- 收缩左侧菜单 -->
    <div class="top_title">
      <div class="top_nav_left">
        <m-icon />
        <nav-buttons v-show="!isApp && sportShow" />
      </div>
      <div class="top_nav_right">
        <!-- 未登录 -->
        <template v-if="isTourist">
          <div class="login_regist_btn d-flex align-center">
            <v-btn
              v-if="loginSwitch"
              v-zoom
              :height="commonStyle('top_navigation_btn_height')"
              outlined
              class="login_btn btn_radius t_btn_color_1"
              :ripple="false"
              plain
              @click="showLoginDialog(true)"
            >
              <span class="white--text">{{ $t('UI_Login') }}</span>
            </v-btn>
            <v-btn
              v-if="regSwitch"
              v-zoom
              class="btn_radius"
              color="regist_btn t_btn_host_color"
              :height="commonStyle('top_navigation_btn_height')"
              :ripple="false"
              @click="showLoginDialog(false)"
            >
              {{ $t('UI_Sign_Up') }}
            </v-btn>
            <!-- 多货币/站点 -->
            <site-lang-head v-if="!isApp" />
          </div>
        </template>
        <!-- 已登录 -->
        <template v-else>
          <account-menu @show="showAccount" />
        </template>
      </div>
    </div>
  </v-app-bar>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import NavButtons from './nav-buttons'
import AccountMenu from './account-menu'
import MIcon from './m-icon'
import AndroidDown from './android-down'
import SiteLangHead from '@/components/ui/site-lang-head'
import _common from '@style/components/game-container.top-navigation.modules.scss'

export default {
  components: {
    NavButtons,
    MIcon,
    AccountMenu,
    AndroidDown,
    SiteLangHead
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('modal', ['loginRegistShow']),
    ...mapState('base', ['user', 'languageType', 'isApp', 'systemConfig']),
    ...mapGetters('base', ['isTourist', 'loginSwitch', 'regSwitch']),
    // 体育赛事开关 ext_game_sport_show:1 开启 0 关闭
    sportShow() {
      const { ext_game_sport_show = 0 } = this.systemConfig
      return ext_game_sport_show == 1
    }
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    // 登记登录或者注册
    showLoginDialog(state) {
      this.$store.dispatch('modal/setLoginRegistShow', { tab: state })
    },
    /**
     * 标识
     * @param tab deposit：充值 withdraw：提现
     */
    showAccount(tab = 'deposit') {
      this.$store.dispatch('modal/setDepositWithdrawShow', { tab })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.top-navigation.modules.scss';

.comp.top_nav_bar {
  width: 100% !important;
  left: 0 !important;
  right: 0 !important;
  z-index: $menu_top_bottom_zindex !important;
  transform: none !important;
  contain: unset !important;
  &::v-deep .v-toolbar__content {
    height: 100% !important;
    padding: 0;
    border-bottom: thin solid $toolbar_content_border_color;
    background: $top_nav_bg;
    .top_title {
      width: 100%;
      padding: $top_navigation_padding;
      @include setBox(row, space-between, center, nowrap);
      .top_nav_left {
        display: flex;
        align-items: center;
        gap: 24px;
      }
      .top_nav_right {
        flex-grow: 1;
        flex-shrink: 1;
        // overflow: hidden;
        @include setBox(row, flex-end, center, nowrap);
        .login_regist_btn {
          gap: 0 6px;
          .login_btn {
            border-color: $btn_border_color !important;
            font-size: $top_navigation_btn_font_size;
            font-weight: $top_navigation_btn_font_weight;
            .v-btn__content {
              opacity: 1 !important;
            }
          }
          .regist_btn {
            font-size: $top_navigation_btn_font_size;
            font-weight: $top_navigation_btn_font_weight;
          }
        }
      }
    }
  }
}
</style>
