<template>
  <div class="list_box" ref="listRef">
    <!-- 空数据的展示  -->
    <template v-if="isDefault">
      <v-row class="w_100" jusity="center" no-gutters>
        <v-col :cols="12" align="center" align-self="center">
          <v-row no-gutters>
            <v-col :cols="12">
              <icon-font
                name="icon-icon_email_empty "
                dark="t_text_color_5"
                :size="variables.list_view_icon_size"
              ></icon-font>
            </v-col>
            <v-col :cols="12">
              <span class="empty_list_text">
                {{ $t('UI_Code_HistoryNodata') }}
              </span>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </template>

    <!-- 列表内容-->
    <template v-else>
      <slot></slot>
    </template>
  </div>
</template>

<script>
import loadIcon from '@/assets/icons'
import { scrollable } from '@/utils/scroller.js'
import variables from '@style/components/ui.modules.scss'

export default {
  props: {
    //  是否展示缺省页
    isDefault: {
      type: Boolean,
      required: true,
      default() {
        return false
      }
    },
    // 是否加载完成
    isLoad: {
      type: Boolean,
      required: true,
      default() {
        return true
      }
    },
    // 防抖
    isShake: {
      type: Boolean,
      required: true,
      default() {
        return true
      }
    }
  },
  data() {
    return {
      variables,
      scroller: null // 滚动条实例
    }
  },
  created() {
    //当页面第一次展示时，且页面有数据时，绑定滚动事件
    this.$nextTick(() => {
      const listRef = this.$refs['listRef']
      if (!listRef) return

      this.scroller = scrollable(listRef, { offset: 20 })
      this.scroller.on('reachBottom', () => {
        if (this.isLoad || this.isShake) return

        // 请求下一页
        this.nextPage()
      })
    })
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    // 下一页
    nextPage() {
      this.$emit('nextPage')
    }
  },
  beforeDestroy() {
    // 卸载滚动条
    if (this.scroller) {
      this.scroller.destroy()
      this.scroller = null
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';
.list_box {
  width: 100%;
  overflow: hidden;
  overflow-y: auto;
  @include setFlex(1);
  @include setBox(column, flex-start, center, nowrap);

  .empty_list_text {
    color: $list_view_empty_text_color;
    font-size: $list_view_empty_text_font_size;
    font-weight: $list_view_empty_text_font_weight;
    margin: $list_view_empty_text_margin;
  }
}
</style>
