<template>
  <div class="withdrawal_details_recharg">
    <back-bar
      class="history_back"
      :leftArrow="false"
      :backText="$t('UI_Code_HistoryDetails')"
      :line="true"
      :rightArrow="true"
      @close="closeFun"
    ></back-bar>
    <div class="withdrawal_details_recharg_content">
      <div class="withdrawal_details_recharg_item" v-for="(item, inx) in withdrawalData" :key="inx">
        <div class="withdrawal_details_recharg_item_title">
          {{ item.label }}
        </div>
        <div class="withdrawal_details_recharg_item_text">
          {{ item.value }}
        </div>
      </div>
      <div class="withdrawal_details_recharg_item">
        <div class="withdrawal_details_recharg_item_title">
          {{ this.$t('UI.cashout_Details_Remarks') }}
        </div>
        <div class="withdrawal_details_recharg_item_remark">
          {{ remark }}
        </div>
      </div>
      <div class="withdrawal_details_recharg_btn" v-if="isRechargeType" :style="getFixedByApp">
        <div class="withdrawal_details_recharg_btn_bg">
          <v-btn class="btn btn_radius" @click="withdrawalPreview" color="t_primary_dark_color" height="40">
            <div>{{ $t('UI.credential_Text') }}</div>
          </v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BackBar from '@/components/ui/back-bar'
import { mapState } from 'vuex'
export default {
  name: 'withdrawaldetails',
  data() {
    return {
      withdrawalData: [],
      remark: '',
      show_order_status: '', // 是否显示凭证按钮
      success_screenshot_path: '' // 预览路径
    }
  },
  props: {
    detailsData: {
      type: Object,
      default: {}
    },
    rechargeType: {
      type: [String, Number],
      default: 1
    }
  },
  components: {
    BackBar
  },
  watch: {
    detailsData: {
      handler(val) {
        const resData = val.data && this.$u.lodash.isObject(val.data) ? val.data : {}
        if (!resData.prepay_fileds_map || !resData.prepay_fileds_show) {
          // 兼容旧的版本
          const arr = []
          for (const key in resData) {
            if (key == 'order_no') {
              arr.push({
                label: this.$t('UI_Cash_order_no'),
                value: resData[key] || ''
              })
            }
            if (key == 'email') {
              arr.push({
                label: this.$t('UI.cashout_Details_Email'),
                value: resData.email || ''
              })
            }
            if (key == 'pix_key') {
              arr.push({
                label: this.$t('UI_Shop_InputTaxId'),
                value: resData.pix_key || ''
              })
            }
            if (key == 'remark') {
              this.remark = resData.remark || ''
            }
          }
          this.withdrawalData = arr
        } else {
          const {
            prepay_fileds_map = {},
            prepay_fileds_show = {},
            remark = '',
            success_screenshot_path = '',
            show_order_status = '0'
          } = resData
          this.success_screenshot_path = success_screenshot_path
          this.show_order_status = show_order_status
          const arr = []
          for (const key in prepay_fileds_map) {
            arr.push({
              label: prepay_fileds_map[key],
              value: prepay_fileds_show[key]
            })
          }
          this.withdrawalData = arr
          this.remark = remark
        }
      },
      immediate: true
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    isRechargeType() {
      // 凭证功能按钮显隐
      if (this.rechargeType == 2 && this.show_order_status == '1') {
        return true
      } else {
        return false
      }
    },
    getFixedByApp() {
      return { position: 'absolute', bottom: '0px', left: '0px' }
    }
  },
  created() {},
  methods: {
    closeFun() {
      this.$emit('close')
    },
    withdrawalPreview() {
      if (this.success_screenshot_path) {
        // 路径存在弹窗预览
        this.$emit('previewHall')
      } else {
        this.$pop.message().show({ content: this.$t('UI_Proof_Text') })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';
.withdrawal_details_recharg {
  width: 100%;
  height: 100%;
  background: $withdrawal_details_recharg_bg;
  overflow: hidden;
  position: relative;
  border-radius: 4px;
  .withdrawal_details_recharg_content {
    padding: $withdrawal_details_recharg_content_padding;
    overflow-y: auto;
    max-height: $withdrawal_details_recharg_content_max_heigth;
    .withdrawal_details_recharg_item {
      font-size: $withdrawal_details_recharg_item_title_font_size;
      margin: $withdrawal_details_recharg_item_margin;
      .withdrawal_details_recharg_item_title {
        font-weight: $withdrawal_details_recharg_item_title_font_weight;
        color: $withdrawal_details_recharg_item_title_color;
      }
      .withdrawal_details_recharg_item_text {
        font-size: $withdrawal_details_recharg_item_text_font_size;
        font-weight: $withdrawal_details_recharg_item_text_font_weight;
        color: $withdrawal_details_recharg_item_text_color;
        background: $withdrawal_details_recharg_item_text_bg;
        border-radius: $withdrawal_details_recharg_item_text_border_radius;
        padding: $withdrawal_details_recharg_item_text_padding;
        height: $withdrawal_details_recharg_item_text_height;
        line-height: $withdrawal_details_recharg_item_text_height;
        margin: $withdrawal_details_recharg_item_text_margin;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .withdrawal_details_recharg_item_remark {
        width: 100%;
        min-height: $withdrawal_details_recharg_item_remark_min_height;
        border-radius: $withdrawal_details_recharg_item_remark_border_radius;
        padding: $withdrawal_details_recharg_item_remark_border_padding;
        margin: $withdrawal_details_recharg_item_remark_border_margin;
        background: $withdrawal_details_recharg_item_remark_bg;
        word-wrap: break-word;
      }
    }

    .withdrawal_details_recharg_btn {
      width: 100%;
      padding: 0px 16px 8px 16px;
      .withdrawal_details_recharg_btn_bg {
        width: 100%;
        padding: 0px 16px 8px 16px;
        background: $withdrawal_details_recharg_btn_bg;
      }
      .btn {
        margin: $withdrawal_details_recharg_btn_margin;
        width: 100%;
      }
    }
  }
}

// pc端样式
@include breakpoint-only-up() {
  .withdrawal_details_recharg {
    border-radius: 8px;
  }
}
</style>
