<template>
  <div class="comp birthday_age">
    <div class="birthday_box_content">
      <div class="sun" @click="onSelece('sun')">
        <span class="text">{{ sun || $t('UI_Birth_day') }}</span>
        <span class="down" :class="{ active: dateSign == 'sun' }"></span>
      </div>
      <div class="month" @click="onSelece('month')">
        <span class="text">{{ month || $t('UI_Birth_month') }}</span>
        <span class="down" :class="{ active: dateSign == 'month' }"></span>
      </div>
      <div class="year" @click="onSelece('year')">
        <span class="text">{{ year || $t('UI_Birth_year') }}</span>
        <span class="down" :class="{ active: dateSign == 'year' }"></span>
      </div>

      <div v-show="dateState" class="date_ul_mask" @click="onMask"></div>
      <div v-show="dateState" class="date_ul_box">
        <div class="date_title">{{ dateTitle }}</div>
        <ul class="date_ul">
          <li
            :id="`registAgeLi${item}`"
            class="date_li"
            :class="{
              active:
                (dateSign == 'year' && item == year) ||
                (dateSign == 'month' && item == month) ||
                (dateSign == 'sun' && item == sun)
            }"
            v-for="item in dateList"
            :key="item"
            @click="onChange(item)"
          >
            {{ item }}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  model: {
    prop: 'time',
    event: 'change'
  },
  props: {
    // 时间 2024-08-28
    time: {
      type: String,
      default: '2024-08-28'
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      dateState: false, // 下拉状态
      dateSign: '' // 下拉框标识
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    // 下拉框title
    dateTitle() {
      if (this.dateSign == 'year') return this.$t('UI_Birth_year')
      if (this.dateSign == 'month') return this.$t('UI_Birth_month')
      if (this.dateSign == 'sun') return this.$t('UI_Birth_day')

      return ''
    },
    // 日期时间
    dateList() {
      // 获取近100年 年份
      if (this.dateSign == 'year') {
        let start = new Date().setFullYear(new Date().getFullYear() - 100)
        start = new Date(start).getFullYear()
        const arr = new Array(100).fill().map((item, index) => start + index + 1)
        return [start, ...arr].sort((a, b) => b - a)
      }
      // 获取月份
      if (this.dateSign == 'month') {
        return ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12']
      }
      // 获取年月对应日的天数
      if (this.dateSign == 'sun') {
        const day = new Date(this.year, this.month, 0)
        const count = day.getDate()
        const arr = new Array(Number(count))
          .toString()
          .split(',')
          .map((item, index) => {
            if (index + 1 < 10) return `0${index + 1}`
            return index + 1
          })

        return arr
      }

      return []
    },
    // 年
    year: {
      get() {
        if (!this.time) return

        let num = this.time.split('-')[0] || ''
        num = this.$u.datetime.strpad(num)
        return num
      },
      set(val) {
        this.$emit('change', `${val}-${this.month}-${this.sun}`)
      }
    },
    // 月
    month: {
      get() {
        if (!this.time) return

        let num = this.time.split('-')[1] || ''
        num = this.$u.datetime.strpad(num)
        return num
      },
      set(val) {
        this.$emit('change', `${this.year}-${val}-${this.sun}`)
      }
    },
    // 日
    sun: {
      get() {
        if (!this.time) return

        let num = this.time.split('-')[2] || ''
        num = this.$u.datetime.strpad(num)
        return num
      },
      set(val) {
        this.$emit('change', `${this.year}-${this.month}-${val}`)
      }
    }
  },
  methods: {
    // 点击遮罩
    onMask() {
      this.dateState = false
      this.dateSign = ''
    },
    // 下拉框
    onSelece(sign = '') {
      if (this.disabled) return
      if (!sign) return

      this.dateState = !this.dateState
      this.dateSign = this.dateState ? sign : ''

      this.$nextTick(() => {
        const birthdayLiId = `registAgeLi${
          sign == 'year' ? this.year : sign == 'month' ? this.month : sign == 'sun' ? this.sun : ''
        }`
        const dom = document.getElementById(birthdayLiId)
        // 滚动条滚动到相对区域
        dom && dom.scrollIntoView({ block: 'center', inline: 'center' })
      })
    },
    // 选择
    onChange(num = '') {
      if (!num) return

      if (this.dateSign == 'year') this.year = num
      if (this.dateSign == 'month') {
        this.month = num
        // 获取计算后的月份天数
        const day = this.$u.datetime.mGetDate(this.year, num)
        if (Number(day) < Number(this.sun)) {
          setTimeout(() => {
            this.sun = String(day)
          }, 67)
        }
      }
      if (this.dateSign == 'sun') this.sun = num

      this.dateState = !this.dateState
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.login.modules.scss';

.comp.birthday_age {
  width: $birthday_age_app_width;
  .birthday_box_content {
    width: $birthday_box_content_app_width;
    @include setBox(row, center, center);
    gap: $birthday_box_content_app_gap;
    position: relative;
    .year,
    .month,
    .sun {
      width: $birthday_box_content_item_app_width;
      height: $birthday_box_content_item_app_height;
      padding: $birthday_box_content_item_app_padding;
      background: $birthday_box_content_item_app_bg;
      border-radius: $birthday_box_content_item_app_radius;
      cursor: pointer;
      @include setBox(row, space-between, center);
      .text {
        font-size: $birthday_box_content_item_text_app_size;
        font-weight: $birthday_box_content_item_text_app_weight;
        color: $birthday_box_content_item_text_app_color;
        line-height: $birthday_box_content_item_text_app_line;
      }
      .down {
        border-top: $birthday_box_content_item_down_app_b_t;
        border-right: $birthday_box_content_item_down_app_b_r;
        border-left: $birthday_box_content_item_down_app_b_l;
        border-bottom: $birthday_box_content_item_down_app_b_b;
        position: relative;
        top: $birthday_box_content_item_down_app_top;
        &.active {
          border-top: $birthday_box_content_item_down_active_app_b_t;
          border-bottom: $birthday_box_content_item_down_active_app_b_b;
          top: $birthday_box_content_item_down_active_app_top;
        }
      }
    }
    .date_ul_mask {
      position: fixed;
      left: $birthday_box_content_date_ul_mask_app_left;
      top: $birthday_box_content_date_ul_mask_app_top;
      right: $birthday_box_content_date_ul_mask_app_right;
      bottom: $birthday_box_content_date_ul_mask_app_bottom;
      background: $birthday_box_content_date_ul_mask_app_bg;
      z-index: 1;
    }
    .date_ul_box {
      position: absolute;
      left: $birthday_box_content_date_ul_box_app_left;
      right: $birthday_box_content_date_ul_box_app_right;
      top: $birthday_box_content_date_ul_box_app_top;
      z-index: 2;
      border-radius: $birthday_box_content_date_ul_box_app_radius;
      background: $birthday_box_content_date_ul_box_app_bg;
      padding: $birthday_box_content_date_ul_box_app_padding;
      .date_title {
        width: $birthday_box_content_date_ul_box_title_app_width;
        margin: $birthday_box_content_date_ul_box_title_app_margin;
        font-size: $birthday_box_content_date_ul_box_title_app_size;
        font-weight: $birthday_box_content_date_ul_box_title_app_weight;
        color: $birthday_box_content_date_ul_box_title_app_color;
        line-height: $birthday_box_content_date_ul_box_title_app_line;
        text-align: $birthday_box_content_date_ul_box_title_app_align;
      }
      .date_ul {
        max-height: $birthday_box_content_date_ul_box_ul_app_max_height;
        padding: $birthday_box_content_date_ul_box_ul_app_padding;
        overflow-y: auto;
        .date_li {
          width: $birthday_box_content_date_ul_box_li_app_width;
          margin: $birthday_box_content_date_ul_box_li_app_margin;
          padding: $birthday_box_content_date_ul_box_li_app_padding;
          font-size: $birthday_box_content_date_ul_box_li_app_size;
          font-weight: $birthday_box_content_date_ul_box_li_app_weight;
          color: $birthday_box_content_date_ul_box_li_app_color;
          line-height: $birthday_box_content_date_ul_box_li_app_line;
          text-align: $birthday_box_content_date_ul_box_li_app_align;
          border-radius: $birthday_box_content_date_ul_box_li_app_radius;
          cursor: pointer;
          &:first-child {
            margin: 0;
          }
          &.active {
            background: $birthday_box_content_date_ul_box_li_active_app_bg;
            color: $birthday_box_content_date_ul_box_li_active_app_color;
          }
        }
      }
    }
  }
}
// 针对pc样式
@include breakpoint-only-up() {
  .comp.birthday_age {
    .birthday_box_content {
      gap: $birthday_box_content_pc_gap;
      .year,
      .month,
      .sun {
        height: $birthday_box_content_item_pc_height;
        padding: $birthday_box_content_item_pc_padding;
        border-radius: $birthday_box_content_item_pc_radius;
        .text {
          font-size: $birthday_box_content_item_text_pc_size;
        }
        .down {
          border-top: $birthday_box_content_item_down_pc_b_t;
          border-right: $birthday_box_content_item_down_pc_b_r;
          border-left: $birthday_box_content_item_down_pc_b_l;
          border-bottom: $birthday_box_content_item_down_pc_b_b;
          top: $birthday_box_content_item_down_pc_top;
          &.active {
            border-top: $birthday_box_content_item_down_active_pc_b_t;
            border-bottom: $birthday_box_content_item_down_active_pc_b_b;
            top: $birthday_box_content_item_down_active_pc_top;
          }
        }
      }
      .date_ul_box {
        top: $birthday_box_content_date_ul_box_pc_top;
        border-radius: $birthday_box_content_date_ul_box_pc_radius;
        padding: $birthday_box_content_date_ul_box_pc_padding;
        .date_title {
          font-size: $birthday_box_content_date_ul_box_title_pc_size;
          margin: $birthday_box_content_date_ul_box_title_pc_margin;
        }
        .date_ul {
          max-height: $birthday_box_content_date_ul_box_ul_pc_max_height;
          .date_li {
            margin: $birthday_box_content_date_ul_box_li_pc_margin;
            padding: $birthday_box_content_date_ul_box_li_pc_padding;
            font-size: $birthday_box_content_date_ul_box_li_pc_size;
            border-radius: $birthday_box_content_date_ul_box_li_pc_radius;
          }
        }
      }
    }
  }
}
</style>
