<template>
  <div class="statistic_info">
    <back-bar
      class="statistic_back"
      :size="backProps.size"
      :line="backProps.line"
      :center="backProps.center"
      :py-height="backProps.padding"
      :left-arrow="isApp"
      :right-arrow="!isApp"
      @back="closeDialog"
      @close="closeDialog"
      :backText="$t('UI_Wash_Code_Statistics_title_1')"
    >
    </back-bar>
    <div v-if="show" class="search_box">
      <vue-select
        v-model="params.time_type"
        :options="options1"
        :backgroundColor="selectStyle.bg"
        class="select_wrap"
        :borderRadius="selectStyle.radius"
        :memuBg="selectStyle.menuBg"
        :border="selectStyle.borderColor"
        :menuBorderRadius="selectStyle.menuRadius"
        :offsetY="selectStyle.offsetY"
        :activeBg="selectStyle.activeBg"
        :menuPadding="selectStyle.menuPadding"
        :activeItemPadding="selectStyle.activeItemPadding"
      />
      <vue-select
        v-model="params.gtype"
        :options="options2"
        :backgroundColor="selectStyle.bg"
        class="select_wrap"
        :borderRadius="selectStyle.radius"
        :memuBg="selectStyle.menuBg"
        :border="selectStyle.borderColor"
        :menuBorderRadius="selectStyle.menuRadius"
        :offsetY="selectStyle.offsetY"
        :activeBg="selectStyle.activeBg"
        :menuPadding="selectStyle.menuPadding"
        :activeItemPadding="selectStyle.activeItemPadding"
        :placeholder="$t('UI_Wash_Code_Statistics_tabel_title_1')"
      />
      <vue-select
        v-model="params.gid"
        :options="options3"
        :backgroundColor="selectStyle.bg"
        class="select_wrap"
        :borderRadius="selectStyle.radius"
        :memuBg="selectStyle.menuBg"
        :border="selectStyle.borderColor"
        :menuBorderRadius="selectStyle.menuRadius"
        :offsetY="selectStyle.offsetY"
        :activeBg="selectStyle.activeBg"
        :menuPadding="selectStyle.menuPadding"
        :activeItemPadding="selectStyle.activeItemPadding"
        :placeholder="$t('UI_Wash_Code_Statistics_tabel_title_2')"
      />
    </div>
    <div class="statistic_view">
      <table-temp :table-head="headers" :table-list="dataTable" :load-more-state="loadMoreState" @nextPage="nextPage">
        <template slot="profit" slot-scope="{ row }">
          <div :class="getProfitStyle(row.profit)">{{ row.profit }}</div>
        </template>
      </table-temp>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { userGameDailyStatistics } from '@/api/modules/bet.js'
import BackBar from '@/components/ui/back-bar/index.vue'
import tableTemp from '@/components/ui/table-temp/index.vue'
import _common from '@style/components/ui.modules.scss'
import vueSelect from '@/components/ui/vue-select'
export default {
  components: {
    BackBar,
    tableTemp,
    vueSelect
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      // 搜索
      page: 1,
      limit: 10,

      headTitle: [], // 表头
      dataTable: [], // 数据
      loadMoreState: true, // 是否显示下一页按钮
      params: {
        time_type: 1,
        gtype: '',
        gid: ''
      },
      options1: [
        { label: this.$t('UI_Wash_Code_Statistics_options_1'), value: 1 }, // 今天
        { label: this.$t('UI_Wash_Code_Statistics_options_2'), value: 7 } // 最近7天
      ],
      options2: [], // 游戏类型
      options3: [], // 平台
      is_first: true
    }
  },
  created() {},
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    // 返回组件的传值
    backProps() {
      const {
        statistic_info_back_size,
        statistic_info_back_line,
        statistic_info_back_center,
        statistic_info_back_padding
      } = _common

      return {
        size: Number(statistic_info_back_size),
        line: !(statistic_info_back_line == false + ''),
        center: statistic_info_back_center,
        padding: Number(statistic_info_back_padding)
      }
    },
    // 处理表头
    headers() {
      const columns = [
        { label: this.$t('UI_Invite_Date'), prop: 'day', align: 'center', fixed: 'left', width: '130px' }, // 日期
        { label: this.$t('UI_Wash_Code_Statistics_tabel_title_1'), prop: 'gtype', align: 'center' }, // 类型
        { label: this.$t('UI_Wash_Code_Statistics_tabel_title_2'), prop: 'game_name', align: 'center' }, // 厂家
        {
          label: this.$t('UI_Wash_Code_Statistics_tabel_title_3'),
          prop: 'sub_game_name',
          align: 'center',
          width: '130px'
        }, // 游戏
        { label: this.$t('UI_Wash_Code_Statistics_tabel_title_4'), prop: 'bet_num', align: 'center' }, // 下注次数
        { label: this.$t('UI_Wash_Code_Statistics_tabel_title_5'), prop: 'bets_gold', align: 'center' }, // 打码量（有效投注总额）
        { label: this.$t('UI_Wash_Code_Statistics_tabel_title_6'), prop: 'profit', align: 'center', slot: 'profit' } // 输赢值
      ]
      const {
        statistic_info_table_size_pc,
        statistic_info_table_size_app,
        statistic_info_table_bold,
        statistic_info_table_radius,
        statistic_info_table_zebra,
        statistic_info_table_is_scroll,
        statistic_info_table_head_bg,
        statistic_info_table_head_text_color,
        statistic_info_table_head_radius,
        statistic_info_table_head_size_pc,
        statistic_info_table_head_size_app,
        statistic_info_table_head_bold,
        statistic_info_table_head_align,
        statistic_info_table_head_margin,
        statistic_info_table_head_padding_app,
        statistic_info_table_head_padding_pc,
        statistic_info_table_head_fixed,
        statistic_info_table_hor_bg,
        statistic_info_table_hor_app_padding,
        statistic_info_table_hor_pc_padding,
        statistic_info_table_hor_border_t,
        statistic_info_table_hor_border_r,
        statistic_info_table_hor_border_b,
        statistic_info_table_hor_border_l,
        statistic_info_table_hor_margin,
        statistic_info_table_hor_color,
        statistic_info_table_hor_size_pc,
        statistic_info_table_hor_size_app,
        statistic_info_table_hor_bold,
        statistic_info_table_hor_align
      } = _common
      return {
        size: {
          fontSize: this.isApp ? statistic_info_table_size_app : statistic_info_table_size_pc,
          fontWeight: statistic_info_table_bold
        },
        radius: statistic_info_table_radius,
        zebra: statistic_info_table_zebra,
        isScroll: !(statistic_info_table_is_scroll == false + ''),
        head: {
          bg: statistic_info_table_head_bg,
          color: statistic_info_table_head_text_color,
          radius: statistic_info_table_head_radius,
          size: {
            fontSize: this.isApp ? statistic_info_table_head_size_app : statistic_info_table_head_size_pc,
            fontWeight: statistic_info_table_head_bold,
            textAlign: statistic_info_table_head_align
          },
          margin: statistic_info_table_head_margin,
          padding: this.isApp ? statistic_info_table_head_padding_app : statistic_info_table_head_padding_pc,
          fixed: !(statistic_info_table_head_fixed == false + '')
        },
        hor: {
          bg: statistic_info_table_hor_bg,
          padding: this.isApp ? statistic_info_table_hor_app_padding : statistic_info_table_hor_pc_padding,
          border_t: statistic_info_table_hor_border_t,
          border_r: statistic_info_table_hor_border_r,
          border_b: statistic_info_table_hor_border_b,
          border_l: statistic_info_table_hor_border_l,
          margin: statistic_info_table_hor_margin,
          color: statistic_info_table_hor_color,
          size: {
            fontSize: this.isApp ? statistic_info_table_hor_size_app : statistic_info_table_hor_size_pc,
            fontWeight: statistic_info_table_hor_bold,
            textAlign: statistic_info_table_hor_align
          }
        },
        columns
      }
    },
    // select 组件的样式
    selectStyle() {
      const {
        statistic_info_select_bg,
        statistic_info_select_pc_radius,
        statistic_info_select_app_radius,
        statistic_info_select_menuBg,
        statistic_info_select_borderColor,
        statistic_info_select_menuBorderColor,
        statistic_info_select_app_menuRadius,
        statistic_info_select_pc_menuRadius,
        statistic_info_select_pc_offsetY,
        statistic_info_select_app_offsetY,
        statistic_info_select_activeBg,
        statistic_info_select_pc_menuPadding,
        statistic_info_select_app_menuPadding,
        statistic_info_select_pc_activeItemPadding,
        statistic_info_select_app_activeItemPadding
      } = _common
      return {
        bg: statistic_info_select_bg,
        radius: this.isApp ? statistic_info_select_app_radius : statistic_info_select_pc_radius,
        menuBg: statistic_info_select_menuBg,
        borderColor: statistic_info_select_borderColor,
        menuBorderColor: statistic_info_select_menuBorderColor,
        menuRadius: this.isApp ? statistic_info_select_app_menuRadius : statistic_info_select_pc_menuRadius,
        offsetY: this.isApp ? `${statistic_info_select_app_offsetY}px` : `${statistic_info_select_pc_offsetY}px`,
        activeBg: statistic_info_select_activeBg,
        menuPadding: this.isApp ? statistic_info_select_app_menuPadding : statistic_info_select_pc_menuPadding,
        activeItemPadding: this.isApp
          ? statistic_info_select_app_activeItemPadding
          : statistic_info_select_pc_activeItemPadding
      }
    }
  },
  watch: {
    params: {
      handler(val) {
        if (this.show) {
          this.page = 1
          this.getList()
        }
      },
      deep: true
    },
    show: {
      handler(val) {
        if (!val) {
          this.is_first = true
          this.page = 1
          this.params = {
            time_type: 1,
            gtype: '',
            gid: ''
          }
        } else {
          this.getList()
        }
      },
      immediate: true
    }
  },
  methods: {
    // 请求列表数据
    async getList() {
      const params = {
        token: this.user.token,
        page: this.page,
        limit: this.limit,
        language: this.languageType,
        is_first: this.is_first ? 1 : 0,
        ...this.params
      }
      const res = await userGameDailyStatistics(params).catch(e => e)
      if (res.code != 0) return

      const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      this.headTitle = resData.title && Array.isArray(resData.title) ? resData.title : []
      if (this.is_first) {
        this.options2 = this.getOptions(resData.gtype_arr || {})
        this.options3 = this.getOptions(resData.game_arr || {})
        this.is_first = false
      }
      const list = Array.isArray(resData.list) ? resData.list : []
      this.dataTable = params.page == 1 ? list : this.dataTable.concat(list)
      this.loadMoreState = this.dataTable.length < resData.total_count
    },

    // 下一页
    nextPage() {
      this.page += 1
      this.getList()
    },
    // 关闭
    closeDialog() {
      this.$emit('close')
    },
    // 构造选项列表
    getOptions(data) {
      const keys = Object.keys(data)
      return keys.map(e => {
        return { label: data[e], value: e }
      })
    },
    getProfitStyle(num) {
      if (typeof num === 'number') {
        return num > 0 ? 'color_up' : 'color_down'
      }
      if (typeof num === 'string') {
        return num.indexOf('-') === -1 ? 'color_up' : 'color_down'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules';

// app样式
.statistic_info {
  background-color: $statistic_info_bg;
  @include setBox(column, flex-start, flex-start);
  position: fixed;
  top: 0px;
  left: 0px;
  height: $statistic_info_app_height;

  .statistic_back {
    flex-shrink: 0;
    flex-grow: 0;
  }
  .search_box {
    @include setBox(row, flex-start, center);
    margin: $statistic_info_search_box_app_margin;
    padding: $statistic_info_search_box_app_padding;
    .select_wrap {
      margin-right: $statistic_info_select_app_layout_space;
      ::v-deep .sm-dropdown-select {
        width: $statistic_info_select_app_width !important;
        height: $statistic_info_select_app_height !important;
      }
    }
  }

  .statistic_view {
    width: $statistic_info_table_width;
    flex-shrink: 1;
    flex-grow: 1;
    overflow: hidden;
    padding: $statistic_info_table_app_padding;
    margin: $statistic_info_table_app_margin;
    ::v-deep .table_list .table_row {
      &:nth-child(2n - 1) .table_cell {
        background-color: $black_4 !important;
      }
      &:nth-child(2n) .table_cell {
        background-color: $black_2 !important;
      }
    }
    .color_up {
      color: $primary_light_color !important;
    }
    .color_down {
      color: $green_dark !important;
    }
  }
}

// pc样式
@include breakpoint-only-up() {
  .statistic_info {
    position: relative;
    height: $statistic_info_pc_height;
    border-radius: $statistic_info_pc_radius;
    .search_box {
      margin: $statistic_info_search_box_pc_margin;
      padding: $statistic_info_search_box_pc_padding;
      .select_wrap {
        margin-right: $statistic_info_select_pc_layout_space;
        ::v-deep .sm-dropdown-select {
          width: $statistic_info_select_pc_width !important;
          height: $statistic_info_select_pc_height !important;
        }
        ::v-deep .sm-dropdown-ul {
          border: 1px solid $statistic_info_select_menuBorderColor !important;
        }
      }
    }
    .statistic_view {
      padding: $statistic_info_table_pc_padding;
      margin: $statistic_info_table_pc_margin;
    }
  }
}
</style>
