<template>
  <div class="comp tab_item" :class="{ active: isActive }">
    <div class="tab_item_icon">
      <icon-font :name="iconName" :size="commonStyle('bottom_tab_icon_size')" />
      <div
        v-if="!!navItem.dot && getDotEntry(navItem.dot).value"
        class="icon_red red_radius"
        :class="{ mail: navItem.dot == 'mail' }"
      >
        {{ navItem.dot == 'mail' ? getDotEntry(navItem.dot).label : '' }}
      </div>
    </div>
    <div class="tab_item_title" :class="{ active: isActive }">
      <auto-font v-if="autoFontType" :uuid="navItem.name" :maxFont="12" :level="1" :text="$t(navItem.title)" />
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { lowerCase } from '@/utils/helper'
import _common from '@style/components/game-container.bottom-navigation.modules.scss'

export default {
  props: {
    navItem: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      autoFontType: false
    }
  },
  computed: {
    ...mapGetters('base', ['getDotEntry']),
    // 是否选中
    isActive() {
      return lowerCase(this.navItem.router) == lowerCase(this.$route.name)
    },
    // 图标
    iconName() {
      return this.isActive ? `icon-${lowerCase(this.navItem.icon)}_active` : `icon-${lowerCase(this.navItem.icon)}`
    }
  },
  mounted() {
    setTimeout(() => {
      this.autoFontType = true
    }, 1000)
  },
  methods: {
    commonStyle: k => _common[k]
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.bottom-navigation.modules.scss';

.comp.tab_item {
  width: 100%;
  height: 100%;
  @include setBox(column, center, center);
  user-select: none;
  -webkit-user-select: none;
  &.active {
    border-top: 2px solid $bottom_tab_border_top_active_color;
  }
  .tab_item_icon {
    font-size: 0;
    position: relative;
    .icon_red {
      position: absolute;
      top: 0;
      right: -2px;
    }
  }
  .tab_item_title {
    width: 100%;
    height: $bottom_tab_title_height;
    color: $bottom_tab_title_color;
    user-select: none;
    -webkit-user-select: none;
    @include setBox(column, center, center);
    &.active {
      color: $bottom_tab_title_active_color;
      line-height: 1;
    }
  }
}
</style>
