<template>
  <div class="scroll_container">
    <transition-group class="refer_table" name="table_item">
      <div
        class="table_item"
        v-for="item in list"
        :key="item.index"
        :style="{
          background: item.index % 2 == 0 ? '#31353d' : '#1f242d'
        }"
      >
        <div class="table_item_key">{{ item.nickname }}</div>
        <div class="table_item_type">{{ item.typeName }}</div>
        <div class="table_item_nub">{{ item.value }}</div>
        <div class="table_item_gold"></div>
      </div>
    </transition-group>
  </div>
</template>
<script>
import { getReceivedRecord } from '@/api/modules/agent'
import vueSeamlessScroll from 'vue-seamless-scroll'
import { mapState } from 'vuex'
export default {
  name: 'referTable',
  props: {
    tableStyle: {
      type: Object,
      default: () => {
        return {}
      }
    },
    amount: {
      type: [String, Number],
      default: ''
    }
  },
  components: {
    vueSeamlessScroll
  },
  data() {
    return {
      classOption: {
        step: 2,
        hoverStop: false
      },
      list: [],
      numberlength: 13, // 生成的总长度
      showState: false,
      totalList: [], // 接口备份数据
      timer: null, // 定时器 动画
      apiTimer: null, // 定时器 接口
      max_id: 0
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('switchState', ['currencyShow'])
  },
  created() {
    // this.randomList()
    let index = 0
    // 轮播动画
    this.timer = setInterval(() => {
      // totalList 接口备份数据
      if (this.totalList?.length) {
        const data = this.totalList.shift()
        this.list.unshift({
          ...data,
          index: index++
        })
        // 当数据超过15条时，删除最后一条数据，防止dom过长
        if (this.list.length > 15) this.list.splice(-1)
      }
    }, 1500)
    this.cutHistory()
    // 每10秒调用一次接口
    this.apiTimer = setInterval(() => {
      // 当数据小于10条时，调用接口获取数据
      if (this.totalList?.length < 10) this.cutHistory()
    }, 10000)
  },
  destroyed() {
    clearInterval(this.timer)
    clearInterval(this.apiTimer)
  },
  methods: {
    // 生成20条数据
    randomList() {
      let list = []
      for (let i = 0; i < 20; i++) {
        let obj = {
          id: this.generateNumber(this.numberlength, !this.user.area ? this.user.area : 63),
          typeName: this.$t('UIWheelJust'),
          value: this.currencySymbol() + this.amount,
          icon: ''
        }
        list.push(obj)
      }
      setTimeout(() => {
        this.showState = true
      }, 300)
      this.list = list
    },
    // 生成随机数字
    generateNumber(length = 13, startWith = '63') {
      let number = String(startWith)
      for (let i = 0; i < length; i++) {
        number += Math.floor(Math.random() * 10) // 生成0-9之间的随机数
      }
      return number.replace(/^(.{5})(.*)(.{4})$/, '$1****$3')
    },

    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 砍一刀历史记录
    async cutHistory() {
      // 当max_id为undefined时，后端接口没有最新数据了，停止接口的调用
      let res = await getReceivedRecord({
        limit: 30,
        max_id: this.max_id,
        token: this.user.token,
        language: this.languageType
      })
      if (res.code == 0) {
        // 当接口没有数据时，停止接口的调用
        let id = res.data?.data?.[0]?.id
        if (id) this.max_id = id
        let _list = res.data.data?.map(v => {
          return {
            id: v.id,
            nickname: v.nickname,
            typeName: this.$t('UIWheelJust'),
            value: this.currencySymbol() + parseFloat(v.total_amount)
          }
        })
        // 把接口数据添加到totalList中
        this.totalList.unshift(..._list)
      }
    }
  }
}
</script>
