  margin-top: $refer_table_scroll_container_margin;
  overflow: hidden;
  white-space: nowrap;
  height: $refer_table_scroll_container_height;
  display: flex;
  flex-direction: column;
}
.refer_table {
  width: $refer_table_width;
  overflow: hidden;
  height: $refer_table_height;
  .table_item {
    @include setBox(row, center, center);
    padding: $refer_table_table_item_padding;
    width: $refer_table_table_item_width;
    color: $refer_table_table_item_color;
    font-size: 14px;
    line-height: 1;
    transition: all 1s;
    border-radius: 4px;

    &:nth-child(odd) {
      background-color: $refer_table_table_item_odd_bg; /* 奇数行背景色 */
    }
    &:nth-child(even) {
      background-color: $refer_table_table_item_even_bg; /* 奇数行背景色 */
    }
    .table_item_key {
      width: 35%;
      justify-self: flex-start;
    }
    .table_item_type {
      width: 35%;
      white-space: nowrap; /* 强制文本在一行内显示 */
      overflow: hidden; /* 隐藏超出容器的内容 */
      text-overflow: ellipsis; /* 超出部分显示省略号 */
    }
    .table_item_nub {
      width: 15%;
    }
    .table_item_gold {
      width: 20px;
      height: 20px;
      background: $invite_quest_quest_content_img_bg;
      flex: none;
    }
  }
}

.table_item-enter, .table_item-leave-to
