<template>
  <v-navigation-drawer class="comp drawer_left" :width="244" app left touchless :value="show" @input="onShow">
    <aside class="left_menu_container">
      <div class="left_pannel_container">
        <div class="left_pannel_box">
          <div class="pannel_box_menu_bonus">
            <menu-bonus />
          </div>
          <div class="activity_select">
            <div class="activity_select_title">
              <img class="title_icon" :src="loadIcon('menu_pannel_box_select_icon')" alt="" />
              <div class="title_text text_1">{{ $t('UI_Left_Sales') }}</div>
              <div class="title_close" @click="activityShow = !activityShow">
                <icon-font :name="activityShow ? 'icon-Icon_Up' : 'icon-Icon_Down'" size="18" dark="t_text_color_4" />
              </div>
            </div>
            <div v-show="activityShow" class="activity_select_all">
              <div
                v-for="item in activityList"
                :key="item.act_id"
                class="activity_select_item"
                :class="{ active: item.act_id == currActDet }"
                @click="onActivityDetail(item)"
              >
                <img v-if="item.icon_url" class="activity_select_item_icon" :src="item.icon_url" />
                <div class="activity_select_item_text text_1">{{ item.label || '' }}</div>
              </div>
            </div>
            <div class="select_btn" @click="onActivityList">{{ $t('UI_All_Promotion') }}</div>
          </div>
          <div class="pannel_box_menu_download">
            <float-btn :drawer-left="true" />
          </div>
        </div>
      </div>
    </aside>
  </v-navigation-drawer>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import MenuBonus from './menu-bonus'
import FloatBtn from '@/components/ui/float-btn'
import { AJ_DOTTING } from '@/utils/dotting.js'

export default {
  components: {
    MenuBonus,
    FloatBtn
  },
  model: {
    prop: 'show',
    event: 'change'
  },
  props: {
    show: {
      type: Boolean,
      require: true
    }
  },
  data() {
    return {
      // 活动列表显隐
      activityShow: true,
      // 监听元素高度变化
      observer: '',
      // 左侧栏dom
      menuDom: null
    }
  },
  computed: {
    ...mapState('theme', ['color']),
    ...mapState('base', ['user', 'languageType', 'isApp']),
    ...mapState('source', ['activity', 'rechargeInfo']),
    // 左侧栏显示活动列表
    activityList() {
      const newActivity = JSON.parse(JSON.stringify(this.activity || '{}'))
      const { activity_config = [], ...rest } = newActivity

      let restList = Object.values(rest) || []
      restList = restList.filter(f => f.act_id && f.switch == 1)

      const activityConfList = activity_config.filter(f => f.is_show == 1)
      let list = activityConfList.map(item => {
        const restItem = restList.find(f => f.act_id == item.id)

        return restItem ? { ...restItem, label: restItem.title, value: restItem.act_id } : null
      })
      list = list.filter(f => f)
      list = list.sort((a, b) => b.sort - a.sort)

      return list
    },
    // 全部活动列表
    activityListAll() {
      const newActivityList = JSON.parse(JSON.stringify(this.rechargeInfo || '{}'))
      let list = Object.values(newActivityList) || []
      list = list.filter(v => v.switch == 1).sort((a, b) => b.sort - a.sort)

      return list
    },
    // 当前的活动详情标识
    currActDet() {
      const { name = '', query = {} } = this.$route
      if (name != 'activityDetail') return ''

      return query.act_id
    }
  },
  mounted() {
    // 设置activity_select_all最大高度
    this.maxHeight()
  },
  beforeDestroy() {
    // 停止监听目标元素
    this.observer.unobserve(this.menuDom)
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    // 左侧栏显隐藏
    onShow(value = false) {
      this.$emit('change', value)
    },
    // 跳转活动详情
    onActivityDetail(item = {}) {
      AJ_DOTTING.getAjdata('LEFT_ACTIVITY_DETAIL')
      this.$router.push({
        name: `activityDetail`,
        query: { act_id: item.act_id }
      })
    },
    // 跳转活动列表
    onActivityList() {
      AJ_DOTTING.getAjdata('LEFT_ACTIVITY')
      this.$router.push({ name: `activity` })
    },
    // 设置activity_select_all最大高度
    maxHeight() {
      this.menuDom = document.querySelector('.pannel_box_menu_bonus')
      if (!this.menuDom) return
      const setHeight = () => {
        const navDom = document.querySelector('.top_nav_bar')
        if (!navDom || !this.menuDom) return
        document.querySelector('.activity_select_all').style.maxHeight =
          window.innerHeight - navDom.offsetHeight - this.menuDom.offsetHeight - 98 + 'px'
      }
      setHeight()
      // 创建 ResizeObserver 实例
      this.observer = new ResizeObserver(() => {
        setHeight()
      })
      // 开始监听目标元素
      this.observer.observe(this.menuDom)
    }
  }
}
</script>

<style scoped lang="scss">
@import '@style/components/game-container.drawer-pannel.modules.scss';

.comp.drawer_left {
  user-select: none;
  .left_menu_container {
    position: $left_menu_container_position;
    left: $left_menu_container_left;
    top: $left_menu_container_top;
    bottom: $left_menu_container_bottom;
    width: $left_menu_container_width;
    background: $left_menu_container_bg;
    border-right: $left_menu_container_border_r;
    overflow-y: auto;
    user-select: none;
    .left_pannel_container {
      width: $left_pannel_container_width;
      padding: $left_pannel_container_padding;
      .left_pannel_box {
        width: $left_pannel_container_pannel_box_width;
        @include setBox(column, center, center);
        .pannel_box_menu_bonus {
          width: $left_pannel_box_menu_bonus_width;
          padding: $left_pannel_box_menu_bonus_padding;
          border-bottom: $left_pannel_box_menu_bonus_border_b;
        }
        .activity_select {
          width: $left_pannel_box_pannel_box_select_width;
          padding: $left_pannel_box_pannel_box_select_padding;
          border-bottom: $left_pannel_box_pannel_box_select_border_b;
          .activity_select_title {
            width: 100%;
            @include setBox(row, center, center);
            .title_icon {
              flex: 0;
              width: 26px;
              height: 26px;
            }
            .title_text {
              flex: 1;
              padding: 0 12px;
              font-size: 18px;
              font-weight: 600;
              color: $white;
              line-height: 1;
              text-align: center;
            }
            .title_close {
              flex: 0;
              cursor: pointer;
            }
          }
          .activity_select_all {
            width: 100%;
            max-height: 300px;
            overflow-y: auto;
            &::-webkit-scrollbar {
              display: block;
              width: 4px;
            }
            .activity_select_item {
              width: 100%;
              min-height: 50px;
              margin: 10px 0 0 0;
              padding: 0 10px;
              border-radius: 10px;
              background: $left_activity_select_all_bg;
              @include setBox(row, flex-start, center);
              gap: 0 10px;
              cursor: pointer;
              &:hover,
              &.active {
                background: $left_activity_select_all_hover_bg;
                .activity_select_item_text {
                  color: $white;
                }
              }
              .activity_select_item_icon {
                width: 20px;
              }
              .activity_select_item_text {
                font-size: 14px;
                font-weight: 600;
                color: $text_color_4;
                line-height: 1.4;
              }
            }
          }
          .select_btn {
            width: $left_pannel_box_pannel_box_select_btn_width;
            padding: $left_pannel_box_pannel_box_select_btn_padding;
            margin: $left_pannel_box_pannel_box_select_btn_margin;
            border: $left_pannel_box_pannel_box_select_btn_border;
            background: $left_pannel_box_pannel_box_select_btn_bg;
            border-radius: $left_pannel_box_pannel_box_select_btn_radius;
            font-size: $left_pannel_box_pannel_box_select_btn_size;
            color: $left_pannel_box_pannel_box_select_btn_color;
            text-align: center;
            line-height: $left_pannel_box_pannel_box_select_btn_line;
            cursor: pointer;
          }
        }
        .pannel_box_menu_download {
          width: $pannel_box_menu_download_width;
          margin: $pannel_box_menu_download_margin;
          padding: $pannel_box_menu_download_padding;
        }
      }
    }
  }
}
</style>
