<template>
  <div class="comp site_lang_head">
    <template v-if="isApp">
      <div class="lang_site_btn">
        <div class="lang_btn" @click="onLangShow">
          <div class="btn_icon">
            <img :src="curLang.icon" alt="" />
          </div>
          <div class="btn_text">
            {{ curLang.langText || '' }}
          </div>
          <div class="btn_arrow">
            <icon-font name="icon-Icon_Down" size="12" :dark="commonStyle('site_lang_head_btn_arrow_app_color')" />
          </div>
        </div>
        <template v-if="isSiteShow">
          <div class="site_btn" @click="onSiteShow">
            <div class="btn_icon">
              <img :src="getCurSiteInfo.icon" alt="" />
            </div>
            <div class="btn_text">
              {{ getCurSiteInfo.currency }}
            </div>
            <div class="btn_arrow">
              <icon-font name="icon-Icon_Down" size="12" :dark="commonStyle('site_lang_head_btn_arrow_app_color')" />
            </div>
          </div>
        </template>
      </div>

      <lang-dialog ref="langDialogRef" />
      <site-dialog ref="siteDialogRef" @select-site="onSelectSite" />
    </template>
    <template v-else>
      <div class="lang_site_btn_pc">
        <div class="lang_btn_pc" @click="onLangShow">
          <div class="btn_icon">
            <img :src="curLang.icon" alt="" />
          </div>
        </div>
        <template v-if="isSiteShow">
          <div class="site_btn_pc" @click="onSiteShow">
            <div class="btn_icon">
              <img :src="getCurSiteInfo.icon" alt="" />
            </div>
          </div>
        </template>

        <lang-dialog ref="langDialogRef" />
        <site-dialog ref="siteDialogRef" @select-site="onSelectSite" />
      </div>
    </template>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import SiteDialog from '../site-dialog'
import LangDialog from '../lang-dialog'
import _common from '@style/components/ui.modules.scss'

export default {
  components: {
    SiteDialog,
    LangDialog
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'languageType']),
    ...mapState('source', ['languageList']),
    ...mapGetters('base', ['isTourist', 'getCurSiteInfo']),
    ...mapGetters('common', ['isMultiSiteShow', 'pageMultiSiteSwitch']),
    // 站点开关是否显示
    isSiteShow() {
      return this.isMultiSiteShow && this.pageMultiSiteSwitch.includes(3)
    },
    // 当前语言
    curLang() {
      const item = this.languageList.find(f => f.lang == this.languageType) || {}

      return item
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 打开站点列表
    onSiteShow() {
      this.$nextTick(() => {
        const siteDialogRef = this.$refs['siteDialogRef']
        if (siteDialogRef) siteDialogRef.show()
      })
    },
    // 打开语言列表
    onLangShow() {
      this.$nextTick(() => {
        const langDialogRef = this.$refs['langDialogRef']
        if (langDialogRef) langDialogRef.show()
      })
    },
    // 站点切换
    onSelectSite(item = {}) {
      if (this.isTourist) {
        this.$store.dispatch('base/changeSite', item).catch(e => e)
      } else {
        this.$pop
          .confirm({
            title: this.$t('General.General_9'),
            content: this.$t('UI.Site_Head_Confirm_Content'),
            confirmText: this.$t('General.General_1'),
            cancelText: this.$t('General.General_2'),
            success: () => {
              this.$store.dispatch('base/changeSite', item).catch(e => e)
            }
          })
          .show()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.comp.site_lang_head {
  margin: 14px 0 0 0;
  padding: 0 16px;
  box-sizing: border-box;
  user-select: none;
  cursor: pointer;
  .lang_site_btn {
    width: 100%;
    padding: 8px 0;
    border: $site_lang_head_lang_btn_app_border;
    border-radius: 4px;
    overflow: hidden;
    @include setBox(row, center, center);
    .lang_btn,
    .site_btn {
      flex: 1;
      padding: 0 10px;
      @include setBox(row, center, center);
      gap: 0 4px;
      overflow: hidden;
      .btn_icon {
        flex-shrink: 0;
        flex-grow: 0;
        width: 18px;
        height: 18px;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .btn_text {
        flex-shrink: 1;
        flex-grow: 1;
        font-size: 12px;
        font-weight: 400;
        color: $site_lang_head_lang_btn_text_app_color;
        line-height: 1;
      }
      .btn_arrow {
        flex-shrink: 0;
        flex-grow: 0;
        font-size: 0;
      }
    }
    .site_btn {
      border-left: $site_lang_head_lang_btn_app_border_r;
    }
  }
}
// 针对pc样式
@include breakpoint-only-up() {
  .comp.site_lang_head {
    margin: 0;
    padding: 0;
    position: relative;
    .lang_site_btn_pc {
      height: 36px;
      padding: 0;
      border: $site_lang_head_btn_pc_border;
      border-radius: 4px;
      overflow: hidden;
      @include setBox(row, center, center);
      .lang_btn_pc,
      .site_btn_pc {
        padding: 0 10px;
        @include setBox(row, center, center);
        .btn_icon {
          width: 18px;
          height: 18px;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }
      .site_btn_pc {
        border-left: $site_lang_head_btn_lang_btn_pc_border_r;
      }
      &::v-deep {
        .lang_dialog_box,
        .currency_dialog_box {
          width: 255px !important;
          left: unset !important;
        }
      }
    }
  }
}
</style>
