<template>
  <div class="site-map-continer">
    <div class="site-map-left">
      <icon-font class="icon-back" name="icon-Icon_Back" :size="iconSize" @click="$emit('click-left')" />
      <span class="site-map-name">{{ title }}</span>
    </div>

    <div v-if="$slots.right" class="site-map-right">
      <slot name="right"></slot>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/components/ui.modules.scss'

export default {
  name: 'SiteMap',
  props: {
    title: String,
    leftArrow: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    iconSize() {
      return variables.icon_back_size
    }
  }
}
</script>

