  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}
.site-map-left {
  display: flex;
  align-items: center;
  justify-content: center;
}

.site-map-name {
  font-weight: $site_name_font_weight;
  font-size: $site_name_font_size;
}

.icon-back {
  margin-right: $icon_back_mr;
  color: $icon_back_color;
