<template>
  <div class="android_down" :class="{ android_down_show: isAndroidShow }">
    <div class="android_down_wrap">
      <img v-if="down_urls.android_img" :src="down_urls.android_img" />
      <div class="text_box">{{ down_urls.new_android_download_text || '' }}</div>
      <v-btn color="t_primary_dark_color" class="android_down_btn" @click="onClickDown">
        {{ $t('UI_Invite_Download') }}
      </v-btn>
      <icon-font
        class="android_close"
        name="icon-close1"
        :size="commonStyle('nav_android_down_icon_size')"
        :dark="commonStyle('nav_android_down_icon_color')"
        @click="closeAndroidDown"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { isNativeClient } from '@/utils/helper'
import _common from '@style/components/game-container.top-navigation.modules.scss'
export default {
  data() {
    return {
      isScroll: false,
      isClickClose: false
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig']),
    down_urls() {
      return this.$u.lodash.isObject(this.systemConfig?.down_urls) ? this.systemConfig.down_urls : {}
    },
    isAndroidShow() {
      const u = navigator.userAgent
      const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1
      // 移动端 && 安卓端 && 不是安卓壳包 && 开关打开 && 滚动 && 是否关闭 && 首页
      const flag =
        this.isApp &&
        isAndroid &&
        !isNativeClient() &&
        this.down_urls.new_android_download_set &&
        this.isScroll &&
        !this.isClickClose &&
        this.$route.name == 'menu'
      this.$emit('androidShow', !!flag)
      return flag
    }
  },
  watch: {
    '$route.name'(val) {
      if (val === 'menu') {
        this.isScroll = false
        setTimeout(() => {
          window.addEventListener('scroll', this.onScroll)
        }, 500)
      } else {
        window.removeEventListener('scroll', this.onScroll)
      }
    }
  },
  created() {},
  mounted() {},
  beforeDestroy() {
    window.removeEventListener('scroll', this.onScroll)
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    onScroll() {
      if (!this.isScroll) {
        this.isScroll = true
      }
    },
    closeAndroidDown() {
      this.isClickClose = true
    },
    onClickDown() {
      window.open(this.down_urls.android_download_url, '_blank')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.top-navigation.modules.scss';

.android_down {
  position: $nav_android_down_position;
  top: $nav_android_down_top;
  left: $nav_android_down_left;
  z-index: $custom_android_down_zindex;
  transition: $nav_android_down_transition;
  &.android_down_show {
    top: $nav_android_down_show_top;
  }
  .android_down_wrap {
    @include setBox(row, flex-start, center, nowrap);
    width: $nav_android_down_wrap_width;
    height: $nav_android_down_wrap_height;
    padding: $nav_android_down_wrap_padding;
    background: $nav_android_down_wrap_background;
    box-sizing: border-box;
    img {
      width: $nav_android_down_wrap_img_width;
      aspect-ratio: 1;
      object-fit: cover;
      border-radius: $nav_android_down_wrap_img_radius;
      margin: $nav_android_down_wrap_img_margin;
    }
    .text_box {
      flex: $nav_android_down_text_box_flex;
      font-weight: $nav_android_down_text_box_weight;
      font-size: $nav_android_down_text_box_size;
      color: $nav_android_down_text_box_color;
      line-height: $nav_android_down_text_box_line;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: $nav_android_down_text_box_clamp;
      line-clamp: $nav_android_down_text_box_clamp;
      word-break: break-word;
      -webkit-box-orient: vertical;
    }
    .android_down_btn {
      margin: $nav_android_down_btn_margin;
    }
    .android_close {
      cursor: pointer;
    }
  }
}
</style>
