<template>
  <div class="skeleton banner">
    <v-skeleton-loader
      class="skeleton_banner"
      type="image"
      :style="{ ...swiperPcStyle, ...styleOpt }"
    ></v-skeleton-loader>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  props: {
    // 样式
    styleOpt: {
      type: Object,
      default() {
        return {}
      }
    },
    attrs: {
      type: Object,
      default() {
        return {
          boilerplate: true,
          elevation: 2
        }
      }
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp']),
    cardWidth() {
      let w = 0
      const gap = 36
      const margin = 0
      const padding = 0
      const spacing = 20
      const size = this.containerBox.width
      if (!this.isApp) w = (size - gap - margin * 2 - padding * 2 - spacing * 2) / 2
      return `${w}px`
    },
    cardHeight() {
      const rate = 320 / 674 //图片比例
      return `${parseInt(this.cardWidth) * rate}px`
    },
    cardStyle() {
      return this.isApp
        ? {}
        : {
            width: this.cardWidth,
            height: this.cardHeight
          }
    },
    swiperPcStyle() {
      return this.isApp
        ? {}
        : {
            height: `${parseInt(this.cardHeight) + 44}px`
          }
    }
  }
}
</script>

<style lang="scss">
@import '@style/components/skeleton.scss';

.skeleton.banner {
  .skeleton_banner {
    width: $banner_width;
    height: $banner_height;

    &.theme--dark.v-skeleton-loader .v-skeleton-loader__image {
      width: 100% !important;
      height: 100% !important;
    }
  }
}
</style>
