import _origin from '@source/config/avator_sprites.js'
import loadIcon from '@/assets/icons'
import { canvas2Img } from '@/assets/imgCache.js'
const { coord = {}, width, height, icon_width, icon_height } = _origin

// 雪碧图坐标
const userSprite = coord

// 雪碧图 size
const SPRITE_WIDTH = width
const SPRITE_HEIGHT = height

const SPRITE_ICON_WIDTH = icon_width
const SPRITE_ICON_HEIGHT = icon_height
export { SPRITE_WIDTH, SPRITE_HEIGHT, SPRITE_ICON_WIDTH, SPRITE_ICON_HEIGHT }
export default userSprite

export function getAvatorBgStyle(sid, size = 146) {
  return new Promise((resolve, reject) => {
    if (`${sid}`.indexOf('http') > -1) {
      resolve(`url(${sid}) center center/${size}px ${size}px no-repeat`)
      return
    }
    const sidCoord = userSprite[parseInt(sid)] || userSprite[1]
    const [spriteIconX = 0, spriteIconY = 0] = sidCoord.active
    const sizeX = SPRITE_WIDTH * (size / SPRITE_ICON_WIDTH)
    const sizeY = SPRITE_HEIGHT * (size / SPRITE_ICON_HEIGHT)
    const sizeRet = `${sizeX}px  ${sizeY}px`
    const positionX = spriteIconX / (SPRITE_WIDTH - SPRITE_ICON_WIDTH)
    const positionY = spriteIconY / (SPRITE_HEIGHT - SPRITE_ICON_HEIGHT)
    const positionRet = `${positionX * 100}% ${positionY * 100}%`
    let imgPath = loadIcon('user_sprite', false)

    // 拼接完整路劲
    if (imgPath.indexOf('data:image/') < 0) {
      if (imgPath.startsWith('/')) {
        imgPath = imgPath.replace('/', '')
      }
      const imgEl = new Image()
      imgEl.onload = () => {
        imgPath = canvas2Img(imgEl)
        resolve(`url(${imgPath}) ${positionRet}/${sizeRet} no-repeat`)
        return
      }
      imgEl.onerror = () => {
        resolve(`url(${imgPath}) ${positionRet}/${sizeRet} no-repeat`)
        return
      }
      imgEl.crossOrigin = 'anonymous'
      imgEl.src = imgPath
    } else {
      resolve(`url(${imgPath}) ${positionRet}/${sizeRet} no-repeat`)
      return
    }
  })
}
