<template>
  <div v-if="visible" v-zinx class="comp lang_dialog">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="lang_dialog_box" :class="{ hide: visibleAnim }">
      <div class="lang_dialog_title">
        <template v-if="isApp">
          <div class="lang_title">
            <div class="title_text text_1">{{ $t('UI.Lang_Dialog_Title') }}</div>
            <div class="title_text_close" @click="hide">
              <icon-font
                name="icon-Icon_Cloose"
                size="20"
                :dark="commonStyle('lang_dialog_box_title_close_icon_app_color')"
              />
            </div>
          </div>
        </template>
        <div class="lang_search">
          <div class="search_befter_icon">
            <icon-font
              name="icon-Icon_Search"
              size="20"
              :dark="commonStyle('lang_dialog_box_input_search_icon_app_color')"
            />
          </div>
          <input v-model="searchText" type="text" :placeholder="$t('UI.Lang_Dialog_Input_Place')" />
          <template v-if="searchText">
            <div class="search_after_icon" @click="searchText = ''">
              <icon-font
                name="icon-close"
                size="14"
                :dark="commonStyle('lang_dialog_box_input_close_icon_app_color')"
              />
            </div>
          </template>
        </div>
      </div>
      <!-- 列表 -->
      <template v-if="languageList.length">
        <div class="lang_list">
          <div
            class="lang_item"
            :class="{ active: item.lang == languageType }"
            v-for="item in langList"
            :key="item.id"
            @click="onSelectLang(item)"
          >
            <div class="lt">
              <div class="lang_item_icon">
                <img :src="item.icon" alt="" />
              </div>
              <div class="lang_item_text text_1">{{ item.langText || '' }}&nbsp;&nbsp;{{ item.name || '' }}</div>
            </div>
            <template v-if="item.lang == languageType">
              <div class="rt">
                <icon-font
                  name="icon-select"
                  :size="isApp ? 24 : 20"
                  :dark="commonStyle('lang_dialog_box_success_icon_app_color')"
                />
              </div>
            </template>
          </div>
        </div>
      </template>
      <!-- 缺省 -->
      <template v-else>
        <div class="lang_empty">
          <empty margin-top="0" />
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import Empty from '@/components/ui/empty'
import { AJ_DOTTING } from '@/utils/dotting.js'
import _common from '@style/components/ui.modules.scss'

export default {
  components: {
    Empty
  },
  data() {
    return {
      // 下拉框显示隐藏
      visible: false,
      // 下拉框是否执行关闭动画
      visibleAnim: false,
      // 搜索
      searchText: ''
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'languageType']),
    ...mapState('source', ['languageList']),
    // 语言选择显示列表
    langList() {
      const list = this.languageList
        .map(item => {
          const { langText = '', name = '' } = item
          const nameText = name ? name.toLowerCase() : ''
          const searchText = this.searchText ? this.searchText.toLowerCase() : ''

          if (langText.includes(searchText) || nameText.includes(searchText)) return item
          return null
        })
        .filter(item => item)

      return list
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 打开列表
    show() {
      this.visibleAnim = false
      this.visible = !this.visible
      // this.$nextTick(() => {
      //   const body = document.querySelectorAll('body')[0]
      //   const lang_dialog = document.querySelectorAll('.comp.lang_dialog')[0]
      //   if (body && lang_dialog) body.appendChild(lang_dialog)
      // })
    },
    // 关闭列表
    hide() {
      this.visibleAnim = true
      this.$emit('close')
      setTimeout(() => {
        this.visible = !this.visible
      }, 200)
    },
    // 选择语言
    onSelectLang(item = {}) {
      const { lang = '' } = item
      if (!lang || this.languageType == lang) return
      this.hide()

      // loading - 营造切换的假象
      this.$pop.loading().show()
      setTimeout(() => {
        AJ_DOTTING.getAjdata('LEFT_LANG')
        this.$store.commit('base/SET_LANGUAGE_TYPE', lang)
        this.$store.commit('base/SET_DATA_VERSION', 0)

        this.$pop.loading().hide()
      }, 200)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.comp.lang_dialog {
  width: 100%;
  cursor: pointer;
  user-select: none;
  .lang_dialog_box {
    width: 100%;
    height: 60%;
    padding: 0 16px;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: $lang_dialog_box_app_bg;
    border-radius: 12px 12px 0 0;
    @include setBox(column, flex-start, flex-start);
    animation: currency-dialog-show 0.2s linear 1 both;
    @keyframes currency-dialog-show {
      0% {
        transform: translateY(100%);
      }
      50% {
        transform: translateY(20%);
      }
      100% {
        transform: translateY(0);
      }
    }
    &.hide {
      animation-name: currency-dialog-hide;
      @keyframes currency-dialog-hide {
        0% {
          transform: translateY(0);
        }
        50% {
          transform: translateY(80%);
        }
        100% {
          transform: translateY(100%);
        }
      }
    }
    .lang_dialog_title {
      flex-grow: 0;
      flex-shrink: 0;
      width: 100%;
      padding: 0 0 20px 0;
      .lang_title {
        width: 100%;
        padding: 15px 0;
        @include setBox(row, center, center);
        gap: 0 10px;
        .title_text {
          flex-shrink: 1;
          flex-grow: 1;
          font-size: 16px;
          font-weight: 600;
          color: $lang_dialog_box_title_text_app_color;
          line-height: 1;
        }
        .title_text_close {
          flex-shrink: 0;
          flex-grow: 0;
          font-size: 0;
        }
      }
      .lang_search {
        width: 100%;
        position: relative;
        input {
          width: 100%;
          height: 48px;
          padding: 0 38px;
          border-radius: 8px;
          background: $lang_dialog_box_input_app_bg;
          font-size: 14px;
          font-weight: 400;
          color: $lang_dialog_box_input_app_color;
          line-height: 1;
          &:focus-visible {
            border: none;
            outline: none;
          }
          &::placeholder {
            color: $lang_dialog_box_input_placeholder_app_color;
          }
        }
        .search_befter_icon,
        .search_after_icon {
          font-size: 0;
          position: absolute;
          left: 12px;
          top: 50%;
          transform: translateY(-50%);
        }
        .search_after_icon {
          left: unset;
          right: 12px;
        }
      }
    }
    .lang_list {
      flex-grow: 1;
      flex-shrink: 1;
      width: 100%;
      padding: 0 6px 0 0;
      overflow-y: auto;
      &::-webkit-scrollbar {
        display: block;
        width: 4px;
        background: none;
      }
      .lang_item {
        width: 100%;
        height: 48px;
        padding: 0 16px;
        border-radius: 8px;
        @include setBox(row, center, center);
        gap: 0 8px;
        &.active {
          background: $lang_dialog_box_list_active_hover_app_bg;
          &:hover {
            background: $lang_dialog_box_list_active_hover_app_bg;
          }
          .lt .lang_item_text {
            color: $lang_dialog_box_list_active_hover_text_app_color;
          }
        }
        .lt {
          flex-shrink: 1;
          flex-grow: 1;
          overflow: hidden;
          @include setBox(row, flex-start, center);
          gap: 0 8px;
          .lang_item_icon {
            flex-shrink: 0;
            flex-grow: 0;
            width: 18px;
            height: 18px;
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
          .lang_item_text {
            flex-shrink: 1;
            flex-grow: 1;
            font-size: 14px;
            font-weight: 400;
            color: $lang_dialog_box_list_text_app_color;
            line-height: 1.1;
          }
        }
        .rt {
          flex-shrink: 0;
          flex-grow: 0;
          font-size: 0;
        }
      }
    }
    .lang_empty {
      flex-grow: 1;
      flex-shrink: 1;
      width: 100%;
      @include setBox(row, center, center);
    }
  }
}
// 针对pc样式
@include breakpoint-only-up() {
  .comp.lang_dialog {
    width: auto;
    .wrapper-mask {
      background: transparent;
    }
    .lang_dialog_box {
      position: absolute;
      width: unset;
      padding: 16px;
      left: 0;
      top: 60px;
      right: 0;
      bottom: unset;
      height: 260px;
      background: $lang_dialog_box_pc_bg;
      border-radius: 4px;
      box-shadow: $lang_dialog_box_pc_shadow;
      animation: currency-dialog-show 0.2s linear 1 both;
      @keyframes currency-dialog-show {
        0% {
          transform: translateY(-20px);
          opacity: 0;
        }
        100% {
          transform: translateY(0);
          opacity: 1;
        }
      }
      &.hide {
        animation-name: currency-dialog-hide;
        @keyframes currency-dialog-hide {
          0% {
            transform: translateY(0);
            opacity: 1;
          }
          100% {
            transform: translateY(-20px);
            opacity: 0;
          }
        }
      }
      .lang_dialog_title {
        padding: 0 0 10px 0;
        .lang_search {
          input {
            height: 40px;
            padding: 0 38px;
            border-radius: 4px;
            background: $lang_dialog_box_currency_search_pc_bg;
          }
        }
      }
      .lang_list {
        flex-grow: 1;
        flex-shrink: 1;
        width: 100%;
        overflow-y: auto;
        .lang_item {
          height: 40px;
          padding: 0 10px;
          border-radius: 4px;
          &:hover {
            background: $lang_dialog_box_list_hover_pc_bg;
          }
        }
      }
      .lang_empty {
        margin: 20px 0 0 0;
      }
    }
  }
}
</style>
