<template>
  <div class="skeleton pesquisa_list column_flex">
    <div v-for="(s, i) in 9" :key="i" class="pesquisa_list_item flex_row">
      <v-skeleton-loader class="pesquisa_list_item_lt" :width="imgSize.width" :height="imgSize.height" type="image" />
      <div class="pesquisa_list_item_rt column_flex">
        <v-skeleton-loader class="pesquisa_list_item_text" type="text"></v-skeleton-loader>
        <v-skeleton-loader class="pesquisa_list_item_text" type="text"></v-skeleton-loader>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  props: {
    // 样式
    styleOpt: {
      type: Object,
      default() {
        return {}
      }
    },
    attrs: {
      type: Object,
      default() {
        return {
          boilerplate: true,
          elevation: 2
        }
      }
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp']),
    imgSize() {
      if (this.isApp) {
        return {
          width: 88,
          height: 121
        }
      } else {
        return {
          width: 128,
          height: 176
        }
      }
    }
  }
}
</script>

<style lang="scss" scope>
@import '@style/components/skeleton.scss';

.skeleton.pesquisa_list {
  padding: $pesquisa_list_padding;
  .pesquisa_list_item {
    margin: $pesquisa_list_item_margin;
    .pesquisa_list_item_lt {
      flex-shrink: 0;
      flex-grow: 0;
      margin: $pesquisa_list_item_img_margin;
      &.theme--dark.v-skeleton-loader .v-skeleton-loader__text {
        width: 100%;
        height: 100%;
      }
    }
    .pesquisa_list_item_rt {
      flex-shrink: 1;
      flex-grow: 1;
      .pesquisa_list_item_text {
        width: $pesquisa_list_item_text_width;
        height: $pesquisa_list_item_text_height;
        margin: $pesquisa_list_item_text_margin;
        &:first-child {
          margin-top: 0;
        }
        &.theme--dark.v-skeleton-loader .v-skeleton-loader__text {
          width: 100% !important;
          height: 100% !important;
        }
      }
    }
  }
}
</style>
