<template>
  <div v-if="visible" v-zinx="{ page: zinx }" class="comp site_dialog">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="currency_dialog_box" :class="{ hide: visibleAnim }">
      <div class="currency_dialog_title">
        <template v-if="isApp">
          <div class="currency_title">
            <div class="title_text text_1">{{ $t('UI.Currency_Site_Title') }}</div>
            <div class="title_text_close" @click="hide">
              <icon-font
                name="icon-Icon_Cloose"
                size="20"
                :dark="commonStyle('currency_dialog_box_title_close_icon_app_color')"
              />
            </div>
          </div>
        </template>
        <div class="currency_search">
          <div class="search_befter_icon">
            <icon-font
              name="icon-Icon_Search"
              size="20"
              :dark="commonStyle('currency_dialog_box_input_search_icon_app_color')"
            />
          </div>
          <input v-model="searchText" type="text" :placeholder="$t('UI.Currency_Site_Input_Place')" />
          <template v-if="searchText">
            <div class="search_after_icon" @click="searchText = ''">
              <icon-font
                name="icon-close"
                size="14"
                :dark="commonStyle('currency_dialog_box_input_close_icon_app_color')"
              />
            </div>
          </template>
        </div>
      </div>
      <!-- 列表 -->
      <template v-if="multiCurrencyList.length">
        <div class="currency_list">
          <div
            v-for="item in siteList"
            :key="item.id"
            class="currency_item"
            :class="{ active: item.id == getSelectSite.id }"
            @click="onSelectCurrency(item)"
          >
            <div class="lt">
              <div class="currency_icon">
                <img :src="item.icon" alt="" />
              </div>
              <div class="currency_text text_1">{{ item.currency || '' }}&nbsp;&nbsp;{{ item.country || '' }}</div>
            </div>
            <template v-if="item.id == getSelectSite.id">
              <div class="rt">
                <icon-font
                  name="icon-select"
                  :size="isApp ? 24 : 20"
                  :dark="commonStyle('currency_dialog_box_success_icon_app_color')"
                />
              </div>
            </template>
          </div>
        </div>
      </template>
      <!-- 缺省 -->
      <template v-else>
        <div class="currency_empty">
          <empty margin-top="0" />
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import Empty from '@/components/ui/empty'
import _common from '@style/components/ui.modules.scss'

export default {
  components: {
    Empty
  },
  props: {
    // 层级显示调整
    zinx: {
      type: String,
      default: 'custom'
    }
  },
  data() {
    return {
      // 下拉框显示隐藏
      visible: false,
      // 下拉框是否执行关闭动画
      visibleAnim: false,
      // 搜索
      searchText: '',
      // 当前选择的站点
      selectSite: null
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'multiCurrencyList']),
    ...mapGetters('base', ['getCurSiteInfo']),
    // 站点显示列表
    siteList() {
      const list = this.multiCurrencyList.filter(item => {
        const { country = '', currency = '' } = item
        const countryText = country ? country.toLowerCase() : ''
        const currencyText = currency ? currency.toLowerCase() : ''
        const searchText = this.searchText ? this.searchText.toLowerCase() : ''

        return countryText.includes(searchText) || currencyText.includes(searchText)
      })

      return list
    },
    // 当前选择的站点显示
    getSelectSite() {
      return this.selectSite || this.getCurSiteInfo
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 打开列表
    show() {
      this.visibleAnim = false
      this.visible = !this.visible
      // this.$nextTick(() => {
      //   const body = document.querySelectorAll('body')[0]
      //   const site_dialog = document.querySelectorAll('.comp.site_dialog')[0]
      //   if (body && site_dialog) body.appendChild(site_dialog)
      // })
    },
    // 关闭列表
    hide() {
      this.visibleAnim = true
      this.$emit('close')
      setTimeout(() => {
        this.visible = !this.visible
      }, 200)
    },
    // 选择站点
    onSelectCurrency(item = {}) {
      if (this.getSelectSite.id == item.id) return

      this.selectSite = item
      this.$emit('select-site', item)
      this.hide()
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.comp.site_dialog {
  width: 100%;
  cursor: pointer;
  user-select: none;
  .currency_dialog_box {
    width: 100%;
    height: 60%;
    padding: 0 16px;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: $currency_dialog_box_app_bg;
    border-radius: 12px 12px 0 0;
    @include setBox(column, flex-start, flex-start);
    animation: currency-dialog-show 0.2s linear 1 both;
    @keyframes currency-dialog-show {
      0% {
        transform: translateY(100%);
      }
      50% {
        transform: translateY(20%);
      }
      100% {
        transform: translateY(0);
      }
    }
    &.hide {
      animation-name: currency-dialog-hide;
      @keyframes currency-dialog-hide {
        0% {
          transform: translateY(0);
        }
        50% {
          transform: translateY(80%);
        }
        100% {
          transform: translateY(100%);
        }
      }
    }
    .currency_dialog_title {
      flex-grow: 0;
      flex-shrink: 0;
      width: 100%;
      padding: 0 0 20px 0;
      .currency_title {
        width: 100%;
        padding: 15px 0;
        @include setBox(row, center, center);
        gap: 0 10px;
        .title_text {
          flex-shrink: 1;
          flex-grow: 1;
          font-size: 16px;
          font-weight: 600;
          color: $currency_dialog_box_title_text_app_color;
          line-height: 1;
        }
        .title_text_close {
          flex-shrink: 0;
          flex-grow: 0;
          font-size: 0;
        }
      }
      .currency_search {
        width: 100%;
        position: relative;
        input {
          width: 100%;
          height: 48px;
          padding: 0 38px;
          border-radius: 8px;
          background: $currency_dialog_box_input_app_bg;
          font-size: 14px;
          font-weight: 400;
          color: $currency_dialog_box_input_app_color;
          line-height: 1;
          &:focus-visible {
            border: none;
            outline: none;
          }
          &::placeholder {
            color: $currency_dialog_box_input_placeholder_app_color;
          }
        }
        .search_befter_icon,
        .search_after_icon {
          font-size: 0;
          position: absolute;
          left: 12px;
          top: 50%;
          transform: translateY(-50%);
        }
        .search_after_icon {
          left: unset;
          right: 12px;
        }
      }
    }
    .currency_list {
      flex-grow: 1;
      flex-shrink: 1;
      width: 100%;
      padding: 0 6px 0 0;
      overflow-y: auto;
      &::-webkit-scrollbar {
        display: block;
        width: 4px;
        background: none;
      }
      .currency_item {
        width: 100%;
        height: 48px;
        padding: 0 16px;
        border-radius: 8px;
        @include setBox(row, center, center);
        gap: 0 8px;
        &.active {
          background: $currency_dialog_box_list_active_hover_app_bg;
          &:hover {
            background: $currency_dialog_box_list_active_hover_app_bg;
          }
          .lt .currency_text {
            color: $currency_dialog_box_list_active_hover_text_app_color;
          }
        }
        .lt {
          flex-shrink: 1;
          flex-grow: 1;
          overflow: hidden;
          @include setBox(row, flex-start, center);
          gap: 0 8px;
          .currency_icon {
            flex-shrink: 0;
            flex-grow: 0;
            width: 18px;
            height: 18px;
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
          .currency_text {
            flex-shrink: 1;
            flex-grow: 1;
            font-size: 14px;
            font-weight: 400;
            color: $currency_dialog_box_list_text_app_color;
            line-height: 1.1;
          }
        }
        .rt {
          flex-shrink: 0;
          flex-grow: 0;
          font-size: 0;
        }
      }
    }
    .currency_empty {
      flex-grow: 1;
      flex-shrink: 1;
      width: 100%;
      @include setBox(row, center, center);
    }
  }
}
// 针对pc样式
@include breakpoint-only-up() {
  .comp.site_dialog {
    width: auto;
    .wrapper-mask {
      background: transparent;
    }
    .currency_dialog_box {
      position: absolute;
      width: unset;
      padding: 16px;
      left: 0;
      top: 60px;
      right: 0;
      bottom: unset;
      height: 260px;
      background: $currency_dialog_box_pc_bg;
      border-radius: 4px;
      box-shadow: $currency_dialog_box_pc_shadow;
      animation: currency-dialog-show 0.2s linear 1 both;
      @keyframes currency-dialog-show {
        0% {
          transform: translateY(-20px);
          opacity: 0;
        }
        100% {
          transform: translateY(0);
          opacity: 1;
        }
      }
      &.hide {
        animation-name: currency-dialog-hide;
        @keyframes currency-dialog-hide {
          0% {
            transform: translateY(0);
            opacity: 1;
          }
          100% {
            transform: translateY(-20px);
            opacity: 0;
          }
        }
      }
      .currency_dialog_title {
        padding: 0 0 10px 0;
        .currency_search {
          input {
            height: 40px;
            padding: 0 38px;
            border-radius: 4px;
            background: $currency_dialog_box_currency_search_pc_bg;
          }
        }
      }
      .currency_list {
        flex-grow: 1;
        flex-shrink: 1;
        width: 100%;
        overflow-y: auto;
        .currency_item {
          height: 40px;
          padding: 0 10px;
          border-radius: 4px;
          &:hover {
            background: $currency_dialog_box_list_hover_pc_bg;
          }
        }
      }
      .currency_empty {
        margin: 20px 0 0 0;
      }
    }
  }
}
</style>
