<template>
  <div class="scroll_container">
    <vue-seamless-scroll :data="list" :class-option="classOption" class="refer_table">
      <div class="table_item" v-for="(item, inx) in list" :key="inx">
        <div class="table_item_key">{{ item.username }}</div>
        <div class="table_item_nub">{{ currencySymbol }}{{ item.amount }}</div>
      </div>
    </vue-seamless-scroll>
  </div>
</template>
<script>
import vueSeamlessScroll from 'vue-seamless-scroll'
import { mapState } from 'vuex'
export default {
  name: 'referTable',
  props: {
    tableStyle: {
      type: Object,
      default: () => {
        return {}
      }
    },
    list: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  components: {
    vueSeamlessScroll
  },
  data() {
    return {
      classOption: {
        step: 2,
        hoverStop: false
      }
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    }
  },
  mounted() {},
  methods: {}
}
</script>
<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';
.scroll_container {
  margin-top: $refer_table_scroll_container_margin;
  overflow: hidden;
  white-space: nowrap;
  height: $refer_table_scroll_container_height;
}
.refer_table {
  width: $refer_table_width;
  overflow: hidden;
  height: $refer_table_height;
  .table_item {
    @include setBox(row, center, center);
    padding: $refer_table_table_item_padding;
    width: $refer_table_table_item_width;
    color: $refer_table_table_item_color;
    &:nth-child(odd) {
      background-color: $refer_table_table_item_odd_bg; /* 奇数行背景色 */
    }
    &:nth-child(even) {
      background-color: $refer_table_table_item_even_bg; /* 奇数行背景色 */
    }
    div {
      flex: 1;
      @include setBox(row, center, center);
    }
  }
}
</style>
