<template>
  <div class="comp activit_item">
    <template v-if="item.key == 'secondary_recharge'">
      <div class="second_box">
        <img :src="loadIcon(imgUrl['secondary_recharge'])" alt="" />
      </div>
    </template>
    <template v-if="['fg_championship_activity', 'secondary_recharge'].includes(item.key)">
      <div class="time_box" :class="{ secondary_recharge_box: item.key == 'secondary_recharge' }">
        <div class="time_box_view">
          <div v-if="dayData && dayData != 0">{{ `${dayData}${$t('UI_VIP_DATE_Days')}` }}</div>
          <div>{{ timeData }}</div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { getResour } from '@/utils/loadServer.js'
import loadIcon from '@/assets/icons'

export default {
  props: {
    item: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      count: 0, // 倒计时
      gePlayer: null, // 活动动画实例
      timer: null,
      dayData: '',
      timeData: '',
      imgUrl: {
        secondary_recharge: 'second_icon'
      }
    }
  },
  watch: {
    item: {
      handler(n = {}) {
        if (!Object.keys(n).length) return

        this.countDown()
      },
      immediate: true
    }
  },
  mounted() {
    // 加载活动动画
    this.$nextTick(() => {
      setTimeout(() => {
        if (!['secondary_recharge'].includes(this.item.key)) this.init(this.$el)
      }, 1000)
    })
  },
  methods: {
    loadIcon: (k, bool = false) => loadIcon(k, bool),
    init($el) {
      if (!$el) return
      const { key = '' } = this.item
      const activit_name = {
        fc_red_envelope: 'activit_fc',
        fg_free_rotate: 'activit_fg_rotate',
        fg_red_envelope: 'activit_fg_red',
        fg_championship_activity: 'activit_fg_ship'
      }[key]
      if (!activit_name) return

      const { libsPath = '', galaceanPath = '' } = JSON.parse(process.env.VUE_APP_PATH)
      getResour('galacean', [`${libsPath}/ge_v1.6.5.js`], r => {
        if (!r) return
        if (!window.ge) return

        this.gePlayer = new window.ge.Player({
          loop: true,
          container: $el,
          pixelRatio: window.devicePixelRatio || 2
        })
        this.gePlayer.loadScene(`${galaceanPath}/${activit_name}.json`).catch(err => {
          this.gePlayer.dispose()
        })
      })
    },
    // 倒计时
    countDown() {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      const currentTime = parseInt(new Date().getTime() / 1000)
      const { end_time = 0 } = this.item
      const surplusTime = end_time - currentTime
      this.count = surplusTime <= 0 ? 0 : surplusTime
      this.dayFn()
      // 手动销毁组件
      if (!this.count) {
        this.$destroy()
        if (this.$el) this.$el.remove()
        return
      }
      this.timer = setTimeout(() => {
        this.countDown()
      }, 1 * 1000)
    },
    // 时间转换
    formatTime(seco = 0) {
      return this.$u.datetime.getFormatTimeThree(seco)
    },
    dayFn() {
      const timeCount = this.$u.datetime.getFormatTimeThree(this.count)
      this.dayData = timeCount.days
      let hours = this.$u.datetime.strpad(timeCount.hours)
      let minutes = this.$u.datetime.strpad(timeCount.minutes)
      let seconds = this.$u.datetime.strpad(timeCount.seconds)
      let time = `${hours}:${minutes}:${seconds}`
      this.timeData = time
    }
  },
  beforeDestroy() {
    if (this.gePlayer) {
      this.gePlayer.dispose()
      this.gePlayer = null
    }
    if (this.timer) {
      clearTimeout(this.timer)
      this.timer = null
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.comp.activit_item {
  position: relative;
  .second_box {
    @include setBox(row, center, center);
    img {
      width: $activit_item_second_box_img_width_app;
      height: $activit_item_second_box_img_height_app;
    }
  }
  .time_box {
    display: inline-block;
    font-size: $float_activit_item_time_app_size;
    font-weight: $float_activit_item_time_app_weight;
    color: $float_activit_item_time_app_color;
    border-radius: $float_activit_item_time_app_radius;
    position: absolute;
    left: $float_activit_item_time_app_left;
    bottom: $float_activit_item_time_app_bottom;
    transform: $float_activit_item_time_app_transform;
    background: $float_activit_item_time_app_bg;
    padding: $float_activit_item_time_app_padding;
    width: $float_activit_item_time_viwe_app_width;
    @include setBox(column, center, center);
    .time_box_view {
      background: $float_activit_item_time_viwe_app_bg;
      border-radius: $float_activit_item_time_app_radius;
      @include setBox(column, center, center);
      padding: $float_activit_item_time_view_app_padding;
      width: 100%;
      div {
        line-height: $float_activit_item_time_app_line;
      }
    }
    &.secondary_recharge_box {
      left: $float_activit_item_secondary_recharge_box_app_left;
      bottom: $float_activit_item_secondary_recharge_box_app_bottom;
    }
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .comp.activit_item {
    .second_box {
      img {
        width: $activit_item_second_box_img_width_pc;
        height: $activit_item_second_box_img_height_pc;
      }
    }
    .time_box {
      font-size: $float_activit_item_time_pc_size;
      padding: $float_activit_item_time_pc_padding;
      zoom: $float_activit_item_time_pc_zoom;
      border-radius: $float_activit_item_time_pc_radius;
      bottom: $float_activit_item_time_pc_bottom;
      width: $float_activit_item_time_viwe_pc_width;
      &.secondary_recharge_box {
        left: $float_activit_item_secondary_recharge_box_pc_left;
        bottom: $float_activit_item_secondary_recharge_box_pc_bottom;
      }
    }
  }
}
</style>
