<template>
  <v-menu
    v-model="show"
    :min-width="width"
    :close-on-content-click="false"
    transition="scale-transition"
    absolutev
    offset-y
    left
    :nudge-bottom="nudgeBottom"
  >
    <template v-slot:activator="{ on, attrs }">
      <div class="btn_flex" ref="menu" @click="showPicker" v-zoom>
        <span v-if="isShowDate" class="date_item_title">{{ { getDate, range } | dayTime }}</span>
        <span v-else class="date_item_title">{{ placeholder }}</span>
        <slot></slot>
      </div>
    </template>
    <v-date-picker
      @change="change"
      color="t_primary_dark_color"
      v-model="date"
      :width="width"
      :range="range"
      :allowedDates="allowedDates"
      no-title
      scrollable
    ></v-date-picker>
  </v-menu>
</template>

<script>
import { getFormatDate } from '@/utils/CommonJs'
export default {
  props: {
    // 默认时间
    dateTime: [Array, String],
    // 是否允许选择日期范围
    range: {
      type: Boolean,
      default: true
    },
    nudgeBottom: {
      type: [Number, String],
      default: 0
    },
    width: {
      type: [Number, String],
      default: 'auto'
    },
    allowedDates: {
      type: Function,
      default: () => true
    },
    placeholder: {
      type: String,
      default: ''
    }
  },
  watch: {
    dateTime: {
      immediate: true,
      handler(arr) {
        if (arr) this.date = arr
        if (Array.isArray(this.date)) {
          this.isShowDate = !!(this.date[0] || this.date[1])
          return
        }
        this.isShowDate = !!this.date
      }
    }
  },
  data() {
    return {
      show: false,
      date: null, // 时间
      isShowDate: true
    }
  },
  computed: {
    // 处理时间
    getDate() {
      if (this.range) {
        const [startTime = '', endTime = ''] = this.date && Array.isArray(this.date) ? this.date : []
        return [startTime, endTime]
      } else {
        return this.date || ''
      }
    }
  },
  filters: {
    // 转换时间格式
    dayTime(obj = {}) {
      const { getDate, range } = obj
      if (range) {
        let [startTime = 0, endTime = 0] = getDate && Array.isArray(getDate) ? getDate : []
        return getFormatDate({ date: startTime, pattern: 1 }) + '~' + getFormatDate({ date: endTime, pattern: 1 })
      } else {
        const time = getDate && !Array.isArray(getDate) ? getDate : new Date()
        return getFormatDate({ date: time, pattern: 1 })
      }
    }
  },
  mounted() {
    // 选择时间弹窗位置
    this.$nextTick(() => {
      if (this.$refs['menu']) {
        const { x, y } = this.$refs['menu'].getBoundingClientRect()
        this.pickerLeft = x
      }
    })
  },
  methods: {
    // 回调日期
    change(time) {
      this.date = time
      this.show = false
      if (time && Array.isArray(time) && this.range) this.date = this.$u.datetime.compareDate(time[0], time[1])
      this.$emit('changeDate', this.date)
    },
    // 打开弹窗
    showPicker() {
      this.show = true
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';
.btn_flex {
  width: 100%;
  @include setBox(row, center, center);
  margin: $date_btn_flex_margin;
}
.date_item_title {
  font-size: $date_item_title_font_size;
}
::v-deep .v-date-picker-table {
  min-height: 242px;
  height: auto;
}
::v-deep .v-date-picker-table--date .v-btn {
  margin-top: $date_item_btn_mt;
}
::v-deep .theme--dark.v-picker__body {
  background: $date_item_picker_bg;
}
</style>
