<template>
  <div class="column_flex modle_history">
    <back-bar
      class="history_back"
      :leftArrow="isApp"
      :rightArrow="!isApp"
      :backText="backTitle"
      @back="$emit('close')"
      @close="$emit('close')"
    />

    <div class="scroll_box" :class="isApp ? 'scroll_box_app' : 'scroll_box_pc'">
      <div class="column_flex h_100">
        <div class="history_btn_tab w_100">
          <div class="history_tabs_box">
            <div class="history_tabs">
              <div
                v-for="item in rechargeTabs"
                :key="item.value"
                class="history_item_tab"
                :class="{ active: item.value == rechargeType }"
                @click="changeTab(item)"
              >
                {{ item.name }}
              </div>
            </div>
          </div>
          <div class="date_picker_wrap">
            <date-picker
              :time="daterange"
              :width="width"
              size="medium"
              :radius="parseInt(loadVariables('history_picker_radius'))"
              :nudgeBottom="loadVariables('history_picker_nudge_bottom')"
              @change="changeDate"
            />
          </div>
        </div>
        <div class="history_table">
          <table-temp :table-head="tableHead" :table-list="list" :load-more-state="loadMoreState" @nextPage="loadTable">
            <template slot="status" slot-scope="{ row }">
              <div
                class="break_word"
                :class="[statusFun(row), rechargeType === 3 ? '' : 'text_decor cursor_point']"
                @click="onBtn(row.id)"
              >
                {{ row.status }}
              </div>
            </template>
            <template slot="gold" slot-scope="{ row }">
              {{ isFormatMillageAndNumber(row.gold, true) }}
            </template>
            <template slot="pump_amount" slot-scope="{ row }">
              {{ isFormatMillageAndNumber(row.pump_amount, true) }}
            </template>
            <template slot="send_gold" slot-scope="{ row }">
              {{ isFormatMillageAndNumber(row.send_gold, true) }}
            </template>
            <template slot="createTime" slot-scope="{ row }">
              <div class="column_flex" v-html="dateTime(row.create_time)"></div>
            </template>
            <template slot="typeId" slot-scope="{ row }">
              <div class="type_name break_word">
                {{ $t(`UI_Account_GoldHist_Type_${row.type_id}`) }}
              </div>
            </template>
            <template slot="orderNum" slot-scope="{ row }">
              <div @click="onCopy(row.out_trade_no)">
                <span>{{ row.out_trade_no }}</span>
                <icon-font
                  class="order_num_btn cursor_point"
                  name="icon-icon_copy"
                  :size="loadVariables('history_table_icon_copy_size')"
                />
              </div>
            </template>
          </table-temp>
        </div>
      </div>
    </div>

    <!-- 详情 -->
    <custom-dialog v-model="detailsState">
      <div :style="{ width: isApp ? variables.withdraw_details_width_app : variables.withdraw_details_width_pc }">
        <withdrawal-recharg-details
          :rechargeType="rechargeType"
          :detailsData="detailsData"
          @previewHall="previewHall"
          @close="detailsState = false"
        />
      </div>
    </custom-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import DatePicker from '@/components/ui/datepicker/date-picker'
import loadIcon from '@/assets/icons.js'
import BackBar from '@/components/ui/back-bar'
import tableTemp from '@/components/ui/table-temp'
import { getUserInfoAndOrder, getTxRecordDetails, getGoldRecordList } from '@/api/modules/config.js'
import _common from '@style/_global/_color.module.scss'
import variables from '@style/components/ui.modules.scss'
import copy from 'copy-to-clipboard'
import WithdrawalRechargDetails from './withdrawal-recharg-details'
import { isFormatMillageAndNumber, datetime } from '@/utils/helper'

export default {
  components: {
    DatePicker,
    BackBar,
    tableTemp,
    WithdrawalRechargDetails
  },
  props: {
    // 顶部tab切换
    tab: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      daterange: [],
      list: [],
      limit: 20,
      page: 1,
      rechargeType: 3,
      userInfo: {},
      btnIndex: 3,
      loadMoreState: true, // 是否有分页
      emptyType: true,
      width: 290,
      backTitle: '',
      variables,
      success_screenshot_path: '',
      show_order_status: '0',
      detailsState: false,
      detailsData: {}
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    // tab 切换
    rechargeTabs() {
      return [
        {
          name: this.$t('UI_recHis'),
          value: 3
        },
        {
          name: this.$t('UI.Common_History'),
          value: 2
        },
        {
          name: this.$t('UI_Account_GoldHist_BonusHist'),
          value: 99
        }
      ]
    },
    // 表头
    tableHead() {
      const {
        history_table_size,
        history_table_bold,
        history_table_app_radius,
        history_table_pc_radius,
        history_table_zebra,
        history_table_is_scroll,
        history_table_head_bg,
        history_table_head_radius,
        history_table_head_text_color,
        history_table_head_size,
        history_table_head_bold,
        history_table_head_align,
        history_table_head_margin,
        history_table_head_padding_app,
        history_table_head_padding_pc,
        history_table_head_fixed,
        history_table_hor_bg,
        history_table_hor_app_padding,
        history_table_hor_pc_padding,
        history_table_hor_border_t,
        history_table_hor_border_r,
        history_table_hor_border_b,
        history_table_hor_border_l,
        history_table_hor_margin,
        history_table_hor_color,
        history_table_hor_size,
        history_table_hor_bold,
        history_table_hor_align,
        history_table_bouns_columns_create_time_app_width,
        history_table_bouns_columns_create_time_pc_width,
        history_table_bouns_columns_order_num_app_width,
        history_table_bouns_columns_order_num_pc_width
      } = variables

      // 金币记录表头
      const bounsColumns = [
        {
          label: this.$t('UI_Invite_Date'),
          prop: 'create_time',
          align: 'center',
          slot: 'createTime',
          width: this.isApp
            ? history_table_bouns_columns_create_time_app_width
            : history_table_bouns_columns_create_time_pc_width
        },
        {
          label: this.$t('UI_Account_GoldHist_Type'),
          prop: 'type_id',
          align: 'center',
          slot: 'typeId'
        },
        {
          label: this.$t('UI_Bouns'),
          prop: 'change_gold',
          align: 'center'
        }
      ]
      // 提现记录表头
      const retiradaColumns = [
        {
          label: this.$t('UI_History_Order_Recharge'),
          prop: 'out_trade_no',
          align: 'center',
          slot: 'orderNum',
          width: this.isApp
            ? history_table_bouns_columns_order_num_app_width
            : history_table_bouns_columns_order_num_pc_width
        },
        {
          label: this.$t('UI_Date'),
          prop: 'create_time',
          align: 'center',
          slot: 'createTime',
          width: this.isApp
            ? history_table_bouns_columns_create_time_app_width
            : history_table_bouns_columns_create_time_pc_width
        },
        {
          label: this.$t('UI_Valor_do'),
          prop: 'gold',
          align: 'center',
          slot: 'gold'
        },
        {
          label: this.$t('UI_Taxa_da_retirada'),
          prop: 'pump_amount',
          align: 'center',
          slot: 'pump_amount'
        },
        {
          label: this.$t('UI.MakeMoney_5_Withdrawal_6'),
          prop: 'status',
          align: 'center',
          slot: 'status'
        }
        // {
        //     label: this.$t('UI_Directly_under'),
        //     prop: 'team_num',
        //     align: "center",
        // },
      ]
      // 充值历史记录表头
      const rechargeColumns = [
        {
          label: this.$t('UI_History_Order_Recharge'),
          prop: 'out_trade_no',
          align: 'center',
          slot: 'orderNum',
          width: this.isApp
            ? history_table_bouns_columns_order_num_app_width
            : history_table_bouns_columns_order_num_pc_width
        },
        {
          label: this.$t('UI_Date'),
          prop: 'create_time',
          align: 'center',
          slot: 'createTime',
          width: this.isApp
            ? history_table_bouns_columns_create_time_app_width
            : history_table_bouns_columns_create_time_pc_width
        },
        {
          label: this.$t('UI_Valor_do'),
          prop: 'gold',
          align: 'center',
          slot: 'gold'
        },
        {
          label: this.$t('UI_Code_Bonus'),
          prop: 'send_gold',
          align: 'center',
          slot: 'send_gold'
        },
        {
          label: this.$t('UI.MakeMoney_5_Withdrawal_6'),
          prop: 'status',
          align: 'center',
          slot: 'status'
        }
      ]

      return {
        size: {
          fontSize: history_table_size,
          fontWeight: history_table_bold
        },
        radius: this.isApp ? history_table_app_radius : history_table_pc_radius,
        zebra: history_table_zebra,
        isScroll: history_table_is_scroll,
        head: {
          bg: history_table_head_bg,
          color: history_table_head_text_color,
          radius: history_table_head_radius,
          size: {
            fontSize: history_table_head_size,
            fontWeight: history_table_head_bold
          },
          margin: history_table_head_margin,
          padding: this.isApp ? history_table_head_padding_app : history_table_head_padding_pc,
          fixed: history_table_head_fixed
        },
        hor: {
          bg: history_table_hor_bg,
          padding: this.isApp ? history_table_hor_app_padding : history_table_hor_pc_padding, // 表格 -- 列表 -- 行 左边框
          border_t: history_table_hor_border_t,
          border_r: history_table_hor_border_r,
          border_b: history_table_hor_border_b,
          border_l: history_table_hor_border_l,
          margin: history_table_hor_margin,
          color: history_table_hor_color,
          size: {
            fontSize: history_table_hor_size,
            fontWeight: history_table_hor_bold,
            textAlign: history_table_hor_align
          }
        },
        columns: this.rechargeType == 99 ? bounsColumns : this.rechargeType == 3 ? rechargeColumns : retiradaColumns
      }
    }
  },
  watch: {
    tab: {
      immediate: true,
      handler(i) {
        this.rechargeType = i
      }
    }
  },
  created() {
    this.width = this.isApp ? this.containerBox.width - 26 : 616
    this.daterange = [
      this.$u.getFormatDate({ date: new Date().getTime() - 1000 * 60 * 60 * 24 * 2 }), // 半个月前 的日期
      this.$u.getFormatDate({ date: new Date() }) // 今天的日期
    ]

    //初次加载列表，type=0,默认全部, 3表示充值deposi, 2表示提款withdraw
    this.loadList(this.rechargeType)
    this.rechargeTabsTextFun()
  },
  methods: {
    loadVariables: name => variables[name],
    loadIcon: key => loadIcon(key, false),
    rechargeTabsTextFun() {
      const list = this.rechargeTabs.find(v => v.value == this.rechargeType)
      if (list) return (this.backTitle = list.name)
      this.backTitle = this.rechargeTabs[0].name
    },
    statusFun(row) {
      switch (row.color_type) {
        case 1: {
          return ' color_green'
        }
        case 4: {
          return ' color_red'
        }
        case 2: {
          return ' color_red'
        }
        default:
          return ' color_fff'
      }
    },
    // 获取接口数据
    async loadList() {
      let state = true
      const params = {
        type: this.rechargeType,
        token: this.user.token,
        language: this.languageType,
        page: this.page,
        limit: this.limit
      }
      //添加日期
      if (this.daterange.length > 0) {
        params.start_date = this.daterange[0]
        params.end_date = this.daterange[1]
      }

      this.timer = setTimeout(() => {
        if (state) this.$pop.loading().show()
      }, 2000)
      const getInterface = this.rechargeType == 99 ? getGoldRecordList : getUserInfoAndOrder
      const { code, data = {}, msg = '' } = await getInterface(params).catch(e => e)
      state = false
      if (this.timer) clearTimeout(this.timer)
      this.$pop.loading().hide()

      const list =
        this.rechargeType == 99 ? data.list : this.rechargeType == 3 ? data.recharge_list : data.withdraw_list
      if (this.page == 0) this.setDefaultData(data)

      this.loadMoreState = list && list.length < this.limit ? false : true
      this.list = this.list.concat(list)
      if (!this.list.length) this.emptyType = true
    },
    // 切换tab
    changeTab(item = {}) {
      if (this.rechargeType == item.value) return

      this.page = 1
      this.list = []
      this.rechargeType = item.value
      this.rechargeTabsTextFun()
      this.loadList()
    },
    // 表格分页
    loadTable() {
      this.page += 1
      this.loadList(this.pageType)
    },
    // 这个我也不知道干嘛的
    setDefaultData({ user_info }) {
      this.userInfo = user_info
    },
    // 选择日期
    changeDate(time) {
      this.page = 1
      this.daterange = time
      const dateNab = datetime.getDateDiff(this.daterange[0], this.daterange[1])
      // 实际有提现记录，需要手动修改时间起点为最近15天才能查看，需要进行处理调整,搜索时间范围超过15天时进行提示
      if (dateNab + 1 > 15) {
        this.$pop.message().show({ content: this.$t('UI_Withdrawal_History_Msg') })
        return
      }
      this.list = []
      this.loadList()
    },
    // 点击详情
    async onBtn(id) {
      this.detailsData = {}

      if (this.rechargeType != 3) {
        const par = {
          id,
          language: this.languageType,
          token: this.user.token
        }

        this.$pop.loading().show()
        const res = await getTxRecordDetails(par).catch(e => e)
        this.$pop.loading().hide()
        if (res.code != 0) return this.$pop.message().show({ content: res.msg })

        this.detailsData = res
        const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
        if (resData.prepay_fileds_map || resData.prepay_fileds_show) {
          const { success_screenshot_path = '' } = resData
          this.success_screenshot_path = success_screenshot_path
        }
      }

      this.detailsState = true
    },
    // 处理时间格式
    dateTime(time = '') {
      if (!time) return ['--', '--']

      const value = this.$u.useTimestamp(time, 'yyyy-MM-dd HH:mm') || ''
      const [first, last] = value.split(' ')
      return `<span>${first}</span><span>${last}</span>`
    },
    // 复制到剪贴板
    onCopy(orderNum = '') {
      if (!orderNum) return
      const isCopy = copy(orderNum)
      if (isCopy) this.$pop.message().show({ content: this.$t('UI_Copied') })
    },
    // 千分比转换
    isFormatMillageAndNumber(key, isState) {
      return isFormatMillageAndNumber(key, isState, this.systemConfig?.thousand_mark || 0)
    },
    previewHall() {
      open(this.success_screenshot_path, '_blank')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.modle_history {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: $history_bg;
  user-select: none;
  .scroll_box {
    margin-top: $history_scroll_box_mt;
    width: $history_scroll_box_pc_width;
    height: $history_scroll_box_pc_height;
    overflow-y: auto;
    .history_btn_tab {
      flex-grow: 0;
      flex-shrink: 0;
      margin-bottom: $history_btn_tab_mb;
      @include setBox(column, center, center);
      .history_tabs_box {
        width: $history_tabs_box_app_width;
        padding: $history_tabs_box_app_padding;
        @include setBox(row, center, center);
        .history_tabs {
          width: $history_tabs_app_width;
          height: $history_tabs_app_height;
          border-radius: $history_tabs_app_radius;
          background: $history_tabs_app_bg;
          @include setBox(row, center, center);
          .history_item_tab {
            width: $history_item_tab_app_width;
            height: $history_item_tab_app_height;
            padding: $history_item_tab_app_padding;
            border-radius: $history_item_tab_app_radius;
            font-size: $history_item_tab_app_size;
            font-weight: $history_item_tab_app_weight;
            color: $history_item_tab_app_color;
            line-height: $history_item_tab_app_line;
            text-align: $history_item_tab_app_align;
            cursor: pointer;
            @include setBox(row, center, center);
            &.active {
              background: $history_item_tab_active_app_bg;
            }
          }
        }
      }
      .date_picker_wrap {
        width: $history_date_picker_wrap_width;
        margin-top: $history_date_picker_wrap_mt;
        padding: $history_date_picker_wrap_pa;
      }
    }
    .history_table {
      flex-grow: 1;
      flex-shrink: 1;
      padding: $history_table_pa;
      width: $history_table_width;
      .color_green {
        color: $deposit_withdraw_record_success;
        text-align: $deposit_withdraw_record_success_align;
      }
      .color_red {
        color: $deposit_withdraw_record_error;
        text-align: $deposit_withdraw_record_error_align;
      }
      .color_fff {
        color: $deposit_withdraw_record_white;
        text-align: $deposit_withdraw_record_white_align;
      }
      .text_decor {
        text-decoration: underline;
        text-align: $deposit_withdraw_record_text_decor_align;
      }
      .order_num_btn {
        position: relative;
        top: $order_num_btn_offset_top;
        left: $order_num_btn_offset_left;
        text-align: $order_num_btn_offset_left_align;
      }
      .type_name {
        text-align: $deposit_withdraw_type_name_align;
      }
    }
  }
}

@include breakpoint-only-xs() {
  .modle_history {
    .scroll_box {
      width: 100%;
      flex-grow: 1;
      flex-shrink: 1;
      .history_table {
        padding: $xs_history_table_pa;
      }
    }
  }
}
</style>
