<template>
  <div class="picker_box flex_row" :style="{ height: height, 'border-radius': `${radius}px` }" @click="imgDate">
    <date-item
      :width="width"
      :date-time="[startDate, endDate]"
      ref="dateRef"
      :height="height"
      :nudgeBottom="nudgeBottom"
      :range="range"
      :allowedDates="allowedDates"
      @changeDate="setDate"
      :placeholder="placeholder"
    >
      <slot></slot>
    </date-item>
    <div class="right_icon d-flex align-center">
      <icon-font name="icon-icon_rili" dark="t_text_color_2" :size="iconRightSize"></icon-font>
    </div>
  </div>
</template>

<script>
import loadIcon from '@/assets/icons'
import DateItem from './date-item'
import variables from '@style/components/ui.modules.scss'
export default {
  components: {
    DateItem
  },
  props: {
    // 圆角
    radius: {
      type: Number,
      default: variables.picker_box_default_radius
    },
    // 时间
    time: {
      type: Array,
      default: () => []
    },
    // 大小模式
    size: {
      type: String,
      default: 'small'
    },
    // 是否允许选择日期范围
    range: {
      type: Boolean,
      default: true
    },
    nudgeBottom: {
      type: [Number, String],
      default: 0
    },
    width: {
      type: Number,
      default: parseInt(variables.picker_box_default_width)
    },
    allowedDates: {
      type: Function,
      default: () => true
    },
    placeholder: {
      type: String,
      default: ''
    }
  },
  watch: {
    size: {
      immediate: true,
      handler(size) {
        if (size == 'large') this.height = variables.picker_box_default_lg_height
        if (size == 'medium') this.height = variables.picker_box_default_md_height
        if (size == 'small') this.height = variables.picker_box_default_xs_height
      }
    },
    time: {
      immediate: true,
      deep: true,
      handler(arr) {
        if (!this.placeholder) {
          const [startTime = '', endTime = ''] = arr && Array.isArray(arr) ? arr : []
          this.startDate =
            startTime || this.$u.getFormatDate({ date: new Date().getTime() - 1000 * 60 * 60 * 24 * 15, pattern: 1 })
          this.endDate = endTime || this.$u.getFormatDate({ date: new Date(), pattern: 1 })
        }
      }
    }
  },
  data() {
    return {
      iconRightSize: variables.picker_box_right_icon_size,
      height: variables.picker_box_default_xs_height,
      startDate: '', // 初始时间
      endDate: '' // 结束时间
    }
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    // 日期回调
    setDate(time) {
      this.$emit('change', time)
    },
    // 出现弹窗
    imgDate() {
      this.$refs.dateRef.showPicker()
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';
.picker_box {
  width: 100%;
  border: 1px solid $picker_box_border_color;
  background-color: $picker_box_bg;
  position: relative;

  .right_icon {
    position: absolute;
    right: $picker_box_right_icon_offset_right;
  }
}
</style>
