<template>
  <div class="wrapper">
    <div class="invite_gift_view">
      <div ref="inviteGiftRef" class="invite_view" :style="inviteStyFn">
        <img :src="getCacheIcon('invite_act')" :style="inviteStyImgFn" v-if="invitePreload" alt="" />
        <div class="invite_view_turntable">
          <div id="my-lucky" class="my_lucky">
            <div
              id="lottery-handler"
              class="lottery_handler"
              :class="{ disabled: btnIsDisabled }"
              :style="lotteryFn"
              ref="lotteryhandler"
            ></div>
            <canvas :width="canvasWidth" :height="canvasHeight" id="lottery" ref="lottery"></canvas>
          </div>
          <gold-box
            ref="goldBoxRef"
            :plusSizeLeft="plusSizeLeft"
            :plusSizeTop="plusSizeTop"
            :isGoldState="isGoldState"
            :goldAnimationClass="goldAnimationClass"
          ></gold-box>
        </div>
        <div class="invite_view_bottom" :style="inviteBottomStyFn">
          <div
            class="invite_view_bottom_tip"
            v-if="isBottomBg"
            :style="{
              background: `url(${loadIcon(`caidai_${this.languageType}`, false)}) no-repeat center / 100% 100%`
            }"
          >
            <div class="turntable_first">
              <div
                class="turntable_first_title"
                :data-content="systemConfig.currency_symbol"
                :style="turntableFirstTitle"
              ></div>
              <div class="turntable_first_content" :data-content="total_amount || ''" :style="turntableFirstText"></div>
            </div>
          </div>
          <div v-else class="invite_view_bottom_title">
            <div class="turntable_text">
              <div class="turntable_text_title" :style="turntableTextTitleFn">
                {{ $t('UIWheelExpires') }}
              </div>
              <div class="turntable_text_content" :style="turntableTxtContentFn">
                {{ countdownTime }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getToFixed } from '@/utils/helper'
import { getResour } from '@/utils/loadServer.js'
import variables from '@style/components/ui.modules.scss'
import { getActivityLottery } from '@/api/modules/agent.js'
import { datetime } from '@/utils/helper.js'
import goldBox from './gold-box'
import { mapState, mapGetters } from 'vuex'
import { getCacheIcon } from '@/assets/imgCache.js'
import './Lottery.js'
import loadIcon from '@/assets/icons'
let referActivitySpine = null
let pixiApp = null
export default {
  name: 'turble',
  components: {
    goldBox
  },
  props: {
    //转盘的设计比例是1116算的
    size: {
      // 转盘大小
      type: Number,
      default: 375
    },
    isBottomBg: {
      // 底部是否需要标题背景色
      type: Boolean,
      default: true
    },
    turntableBg: {
      // 转盘背景色
      type: Array,
      default: () => {
        return []
      }
    },
    result: {
      // 数据结果
      type: Object,
      default: () => {
        return {}
      }
    },
    goldAnimationClass: {
      // 金币动画效果锚点
      type: String,
      default: ''
    },
    isGoldState: {
      // 是否开金币动画开关
      type: Boolean,
      default: false
    },
    plusSizeLeft: {
      // 金币便宜向左右移动位置
      type: Number,
      default: 10
    },
    plusSizeTop: {
      // 金币便宜向上下移动位置
      type: Number,
      default: 10
    }
  },
  data() {
    return {
      variables,
      state: false,
      animationWidth: '375px',
      animationHeight: '395px',
      rewardsIconList: {}, // 转盘奖励图标
      myLucky: null, // 转盘插件挂载
      list: [], // 转盘数组
      configInfo: null, // 配置信息
      timeLottery: 2000, // 抽奖时间倒计时
      countdownTime: '',
      time: null, //倒计时时间
      canvasWidth: '100%',
      canvasHeight: '100%',
      lottery: null,
      invitePreload: true // pixi预加载图片，等pixi加载完之后再渲染
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig', 'systemTimeDiff']),
    ...mapState('source', ['activityInvite']),
    ...mapState('switchState', ['currencyShow']),
    ...mapGetters('base', ['isTourist']),
    inviteStyImgFn() {
      return {
        background: `url(${getCacheIcon('invite_act')?.value?.currentSrc || ''})`,
        backgroundSize: '100% 100%',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
        width: this.size * 1.2 + 'px',
        height: this.size * 1.2 + 'px',
        position: 'absolute',
        top: '46%',
        left: '50%',
        transform: 'translate(-50%, -50%)'
      }
    },
    inviteStyFn() {
      return {
        width: this.size + 'px',
        height: this.size + 'px'
      }
    },
    // 底部文字显示位置
    inviteBottomStyFn() {
      return {
        width: this.size * 0.75 + 'px',
        height: this.size / 7.8 + 'px',
        bottom: this.size / 32 + 'px'
      }
    },
    //底部标题
    turntableTextTitleFn() {
      return {
        fontSize: this.size / 26 + 'px',
        lineHeight: this.size / 26 + 'px'
      }
    },
    //底部倒计时
    turntableTxtContentFn() {
      return {
        fontSize: this.size / 26 + 'px',
        lineHeight: this.size / 26 + 'px'
      }
    },
    // 显示金额
    turntableFirstTitle() {
      return {
        fontSize: this.size / 22 + 'px',
        bottom: this.size / 8 + 'px'
      }
    },
    // 首次显示金币类型字体大小
    turntableFirstText() {
      return {
        fontSize: this.size / 14 + 'px',
        bottom: this.size / 7 + 'px'
      }
    },
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 奖励金币
    total_amount() {
      return this.activityInvite?.activity_info?.total_amount || ''
    },
    lotteryFn() {
      const result = getCacheIcon('referGift_turntable_pointer')
      let src = ''
      if (result) {
        src = typeof result === 'string' ? result : result.value.src
      }
      return {
        background: `url(${src})`,
        backgroundSize: '100% 100%',
        backgroundRepeat: 'no-repeat',
        height: this.size / 2 / 1.6 + 'px',
        width: this.size / 2 / 1.6 + 'px',
        marginTop: -(this.size / 2 / 1.6 / 10) + 'px'
      }
    },
    // 是否可以转（用户并且拥有抽奖次数）
    isLotteryDisable() {
      const draw_num = this.activityInvite?.activity_info?.draw_num || 0
      if (parseInt(draw_num) > 0) return true
      return false
    },
    //抽奖按钮是否置灰(游客点击弹出登录不置灰)
    btnIsDisabled() {
      if (this.isTourist) return false
      const draw_num = this.activityInvite?.activity_info?.draw_num || 0
      if (parseInt(draw_num) == 0) return true

      return false
    }
  },
  watch: {
    size: {
      immediate: true,
      handler(e) {
        this.pixiResizeFn()
      }
    },
    //监听语言切换、刷新接口数据
    languageType() {
      this.getActivityConfigFn()
    },
    //监听转盘接口数据变更，重置转盘
    activityInvite() {
      if (this.lottery) {
        this.lottery.reset({ disable: this.isLotteryDisable }, false)
      }
    }
  },
  async created() {
    this.rewardsIconList[0] = getCacheIcon('referGift_turntable_gold')?.value?.currentSrc || ''
    this.rewardsIconList[1] = getCacheIcon('referGift_turntable_miss')?.value?.currentSrc || ''
    this.rewardsIconList[2] = getCacheIcon('referGift_turntable_money')?.value?.currentSrc || ''
    // 初始化pixi
    this.$nextTick(() => {
      const { libsPath = '' } = JSON.parse(process.env.VUE_APP_PATH)
      getResour('pixi', [`${libsPath}/pixi.min.js`, `${libsPath}/pixi-spine.js`], r => {
        setTimeout(_ => {
          this.initAnimationSize().then(() => {
            this.getPerform()
          })
        }, 207)
      })
    })
  },
  mounted() {
    this.$nextTick(() => {
      let curTime = new Date().getTime() + this.systemTimeDiff //转换成时间戳
      this.setActivityCountdown(curTime)
      this.getActivityConfigFn()
    })
  },
  beforeDestroy() {
    this.destroy()
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    // 初始化动画
    initAnimationSize() {
      return new Promise(resolve => {
        this.$nextTick(() => {
          const inviteGiftRef = this.$refs['inviteGiftRef']
          if (inviteGiftRef) {
            // 动画比例
            const rast = this.size / this.size
            const w = (inviteGiftRef.clientWidth || inviteGiftRef.offsetWidth || '375') * 1.2
            const h = w / rast

            this.animationWidth = w + 'px'
            this.animationHeight = h + 'px'
          }
          resolve()
        })
      })
    },
    // 动画运行
    getPerform() {
      const _this = this
      if (!this.$refs['inviteGiftRef']) return

      const containerX = parseInt(this.animationWidth) // 容器宽
      const containerY = parseInt(this.animationHeight) // 容器高

      pixiApp = new PIXI.Application({
        width: containerX,
        height: containerY,
        transparent: true,
        autoResize: true,
        autoDensity: true
      })
      this.$refs['inviteGiftRef'].appendChild(pixiApp.view)

      const { spinePath = '' } = JSON.parse(process.env.VUE_APP_PATH)
      if (!referActivitySpine) referActivitySpine = pixiApp.loader.add('container', `${spinePath}/invite_act.json`)
      referActivitySpine.load((loader, resources) => {
        this.invitePreload = false
        let dt = resources.container?.spineData
        var animation = new PIXI.spine.Spine(dt)
        var animation = new PIXI.spine.Spine(dt)
        // let w = document.body.clientWidth * 0.6
        animation.width = containerX
        animation.height = containerY
        animation.position.set(containerX / 2, containerY / 2)
        if (animation.state.hasAnimation('animation')) {
          animation.state.setAnimation(0, 'animation', true)
          animation.state.timeScale = 1
        }

        pixiApp.start()
        pixiApp.stage.addChild(animation)

        // 动画结束后关闭弹窗
        animation.state.tracks[0].listener = {
          complete: function (trackEntry, count) {
            // app.stop();
            pixiApp.start()
          }
        }
      })
    },
    // 转盘组件
    paint() {
      this.canvasWidth = this.size / 2 - 4
      this.canvasHeight = this.size / 2 - 4
      let _this = this
      _this.$nextTick(() => {
        _this.lottery = new Lottery(_this.$refs.lottery, {
          handler: _this.$refs.lotteryhandler,
          handlerCallback: function (_interface) {
            _this.getActivityLotteryFn(function (index, data) {
              /*指定停止的位置:索引*/
              _interface.stop(index, function () {
                _this.stopResult(data)
              })
            })
          },
          disableCallback: () => {
            _this.getAuthority()
          },
          /*循环填充数组颜色*/
          fillStyle: [_this.turntableBg[0], _this.turntableBg[1]],
          images: {
            width: this.size / 8.5,
            height: this.size / 8.5,
            y: '98%'
          },
          font: {
            y: '65%',
            color: _this.variables.movable_turntable_color,
            /*循环填充字体颜色*/
            // color: ['#f00', '#ee6500'],
            style: 'normal',
            weight: 400,
            size: _this.size / 14.5 + 'px',
            lineHeight: 1
          },
          strokeStyle: {
            color: '#F1A400',
            lineWidth: 0
          },
          products: _this.getPrizes(),
          speed: 18,
          duration: 2000,
          disable: _this.isLotteryDisable
        })
      })
    },
    // 转盘每一片数据
    getPrizes() {
      let list = this.list.map((v, inx) => {
        let itemObj = {}
        if (['0', '2'].includes(v.type)) {
          itemObj.name = ''
          if (v.type == '2') itemObj.imgUrl = v.first ? this.rewardsIconList[0] : this.rewardsIconList[2]
          if (v.type == '0') itemObj.imgUrl = this.rewardsIconList[1]
        }
        if (['1'].includes(v.type)) {
          itemObj.name = v.value
          itemObj.imgUrl = ''
        }
        return {
          ...itemObj
        }
      })
      return list
    },
    stopResult(data) {
      let activityInvite = JSON.parse(JSON.stringify(this.activityInvite))
      // data.value 奖励数额（type=1为中奖金币，type=2为初始金额）
      if (data?.type == 2) {
        if (activityInvite && activityInvite.activity_user_task) {
          data.progress_amount = this.getToFixed(
            parseFloat(activityInvite.activity_user_task.progress_amount) + parseFloat(data.value)
          )
        } else {
          data.progress_amount = this.getToFixed(parseFloat(data.value))
        }
        activityInvite.activity_user_task = { ...activityInvite.activity_user_task, ...data }
      }
      activityInvite.activity_info.draw_num = activityInvite.activity_info.draw_num - 1
      this.$emit('stopLucky', data)

      // data.type  奖励类型 0:未中奖 1:直接金币 2:分享金币  isGoldState 不显示动画
      if (data?.type == 1 || (data?.type == 2 && this.isGoldState)) {
        for (let i = 0; i < 5; i++) {
          setTimeout(() => {
            this.$refs.goldBoxRef.showGoldAnimation()
          }, 55 * i)
        }
      }
      setTimeout(() => {
        this.$store.commit('source/SET_ACTIVITY_INVITE', activityInvite)

        this.$nextTick(() => {
          this.lottery && this.lottery.reset({ disable: this.isLotteryDisable }, false)
        })
      }, 1000)
    },
    // 获取活动转盘配置信息
    async getActivityConfigFn() {
      const par = {
        token: this.user.token,
        language: this.languageType
      }
      this.$store.dispatch('source/getActivityConfigFn', par).then(res => {
        const { code = 0, data = {} } = res
        if (code == 0) {
          // activity_user_task 任务信息
          const { activity_info = {}, activity_user_task = null } = data
          this.configInfo = data
          const { item_config = [] } = activity_info
          this.list = item_config.map(v => {
            return {
              ...v,
              //弹窗情况显示图标状态（也是没有任务情况）first
              first: !activity_user_task ? false : true
            }
          })

          //多语言切换-先销毁再重绘转盘
          if (!this.lottery) {
            console.log('init pinit')
            this.paint()
          } else {
            console.log('reset pinit')

            this.lottery.reset({ disable: this.isLotteryDisable }, false)
          }
        }
      })
    },
    // 转盘旋转调用接口
    async getActivityLotteryFn(callback) {
      const par = {
        token: this.user.token,
        language: this.languageType,
        version: this.configInfo?.activity_info?.version
      }
      // 点击转盘禁止更新user中的金币更新开关
      this.$store.commit('base/SET_IS_REFRESH_USER', false)
      const res = await getActivityLottery(par).catch(e => e)
      const { code = 0, data = {} } = res
      if (code == 0) {
        const index = this.list.findIndex(item => item.item_id == data?.item_id)
        // 停止旋转
        // index 中奖第几个
        // lottery 中奖信息
        callback && callback(index, data)
      } else {
        // 接口失败的话打开金币更新开关
        this.$store.commit('base/SET_IS_REFRESH_USER', true)
        // 停止旋转
        // index 中奖第几个
        // lottery 中奖信息
        callback && callback(0, data)
      }
    },
    // 时间倒计时
    setActivityCountdown(curTime) {
      clearInterval(this.time)
      this.countdownTime = this.countdownFn(curTime)
      // 初始为空时候
      if (!this.countdownTime) {
        this.time = null
        return false
      }
      // 倒计时 时间结束
      if (this.countdownTime == '00:00:00') {
        clearInterval(this.time)
        this.time = null
        this.$router.push({ name: 'menu' })
        return
      }
      this.time = setInterval(() => {
        curTime = parseInt(curTime) + 1000
        this.setActivityCountdown(curTime)
      }, 1000)
    },
    // 倒计时转换
    countdownFn(curTime) {
      let endTime = this.activityInvite?.activity_user_task?.withdraw_expire_time
      if (this.activityInvite?.activity_user_task?.task_finish_state == 0)
        endTime = this.activityInvite.activity_user_task.task_expire_time
      const timeValue = datetime.getDifferTime(curTime, new Date(endTime * 1000))
      const { day = 0, hh = 0, mm = 0, ss = 0 } = timeValue
      if (!Number(day) && !Number(hh) && !Number(mm) && !Number(ss)) return null

      const dayVal = !Number(day) ? '' : `${Number(day)}d `
      return dayVal + `${hh}:` + `${mm}:` + `${ss}`
    },
    // true: 当前账户属于游客身份，查看首页外，需要做登陆操作
    // false: 当前账户属于注册身份,显示登录弹窗
    getAuthority() {
      if (this.isTourist) {
        this.$store.dispatch('modal/setLoginRegistShow')
        return true
      } else {
        return false
      }
    },
    //  是否存在小数点，如果存在小数点，则保留num位小数，如果是整数，则不保留小数
    getToFixed(str, nub = 2) {
      return getToFixed(str, nub)
    },
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    },
    pixiResizeFn() {
      this.initAnimationSize().then(() => {
        const containerX = parseInt(this.animationWidth) // 容器宽
        const containerY = parseInt(this.animationHeight) // 容器高
        this.canvasWidth = this.size / 2 - 4
        this.canvasHeight = this.size / 2 - 4
        pixiApp && pixiApp.renderer && pixiApp.renderer.resize(containerX, containerY)
        if (pixiApp) {
          pixiApp.stage.width = containerX
          pixiApp.stage.height = containerY
        }
        this.$nextTick(() => {
          this.lottery &&
            this.lottery.reset(
              {
                /*循环填充数组颜色*/
                fillStyle: [this.turntableBg[0], this.turntableBg[1]],
                images: {
                  width: this.size / 8.5,
                  height: this.size / 8.5,
                  y: '98%'
                },
                font: {
                  y: '65%',
                  color: this.variables.movable_turntable_color,
                  /*循环填充字体颜色*/
                  // color: ['#f00', '#ee6500'],
                  style: 'normal',
                  weight: 400,
                  size: this.size / 14.5 + 'px',
                  lineHeight: 1
                },
                strokeStyle: {
                  color: '#F1A400',
                  lineWidth: 0
                },
                products: this.getPrizes(),
                speed: 18,
                duration: 2000,
                disable: this.isLotteryDisable
              },
              true
            )
        })
        // console.log(containerX,containerY, "this.animationWidth")
        // console.log(pixiApp && pixiApp.renderer,"34234")

        //
      }, 0)
    },
    // 销毁
    destroy() {
      clearInterval(this.time)
      this.time = null
      this.myLucky = null
      this.lottery = null
      pixiApp && pixiApp.stop()
    }
  }
}
</script>
