      position: relative;
      .invite_view_turntable {
        position: absolute;
        top: $movable_turntable_invite_view_turntable_top;
        left: $movable_turntable_invite_view_turntable_left;
        transform: $movable_turntable_invite_view_turntable_transform;
        @include setBox(row, center, center);
        margin: $movable_turntable_invite_view_turntable_margin;
        .my_lucky {
          position: relative;
          cursor: pointer;
          .lottery_handler {
            position: absolute;
            z-index: 13;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
          }
          .disabled {
            pointer-events: none;
            filter: saturate(0);
          }
        }
      }
      .invite_view_bottom {
        position: absolute;
        bottom: $movable_turntable_invite_view_bottom_bottom;
        left: $movable_turntable_invite_view_bottom_left;
        transform: $movable_turntable_invite_view_bottom_transform;
        background: $movable_turntable_invite_view_bottom_bg;
        @include setBox(column, center, center);
        .invite_view_bottom_tip {
          height: $movable_turntable_invite_view_bottom_tip_height;
          width: $movable_turntable_invite_view_bottom_tip_width;
          .turntable_first {
            @include setBox(column, center, center);
            position: relative;
            height: $movable_turntable_turntable_first_height;
            width: $movable_turntable_turntable_first_width;
            .turntable_first_title {
              font-weight: $movable_turntable_turntable_first_title_font_weight;
              font-size: $movable_turntable_turntable_first_title_font_size;
              color: $movable_turntable_turntable_first_title_color;
              position: absolute;
              bottom: $movable_turntable_turntable_first_title_bottom;
              left: $movable_turntable_turntable_first_title_left;
              transform: $movable_turntable_turntable_first_title_transform;
              &:after {
                content: attr(data-content);
                position: absolute;
                left: $movable_turntable_turntable_first_title_after_left;
                text-shadow: $movable_turntable_turntable_first_title_after_text_shadow;
              }
              &:before {
                content: attr(data-content);
                position: absolute;
                left: $movable_turntable_turntable_first_title_before_left;
                text-shadow: $movable_turntable_turntable_first_title_before_text_shadow;
              }
            }
            .turntable_first_content {
              font-weight: $movable_turntable_turntable_first_content_font_weight;
              font-size: $movable_turntable_turntable_first_content_font_size;
              color: $movable_turntable_turntable_first_content_color;
              position: absolute;
              left: $movable_turntable_turntable_first_content_left;
              transform: $movable_turntable_turntable_first_content_transform;
              &:after {
                content: attr(data-content);
                position: absolute;
                left: $movable_turntable_turntable_first_content_after_left;
                text-shadow: $movable_turntable_turntable_first_content_after_text-shadow;
              }
              &:before {
