<template>
  <div class="skeleton pesquisa_tabs">
    <v-skeleton-loader class="pesquisa_tab" type="text" :style="{ ...styleOpt }"></v-skeleton-loader>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  props: {
    // 样式
    styleOpt: {
      type: Object,
      default() {
        return {}
      }
    },
    attrs: {
      type: Object,
      default() {
        return {
          boilerplate: true,
          elevation: 2
        }
      }
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp'])
  }
}
</script>

<style lang="scss" scope>
@import '@style/components/skeleton.scss';

.skeleton.pesquisa_tabs {
  .pesquisa_tab {
    margin: $pesquisa_tab_margin;
    width: $pesquisa_tab_width;
    height: $pesquisa_tab_height;

    &.theme--dark.v-skeleton-loader .v-skeleton-loader__text {
      height: 100% !important;
    }
  }
}
</style>
