<template>
  <div class="fab_title" v-if="apkActivityGold.apk_show_switch && apkActivityGold.apk_send_gold > 0">
    <div class="fab_title_box">
      <div class="fab_title_size">
        {{ `+${apkActivityGold.apk_send_gold}` }}
      </div>
      <icon-font class="gold_icon" name="icon-icon_email_chip" :size="16"></icon-font>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  computed: {
    ...mapState('base', ['apkActivityGold'])
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.fab_title {
  padding: $fab_title_padding;
  position: relative;
  background: $fab_title_bg;
  border-radius: $fab_title_border_radius;
  animation-name: scaleDraw;
  /*关键帧名称*/
  animation-timing-function: ease-in-out;
  /*动画的速度曲线*/
  animation-iteration-count: infinite;
  /*动画播放的次数*/
  animation-duration: 2s;

  /*动画所花费的时间*/
  &::after {
    content: '';
    display: inline-block;
    width: 0;
    height: 0;
    border: $fab_title_after_border solid transparent;
    border-top: $fab_title_after_border_top solid $fab_title_bg;
    position: absolute;
    bottom: $fab_title_after_bottom;
    left: $fab_title_after_left;
    transform: translate($fab_title_after_left_translate, 0);
  }

  .fab_title_box {
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;

    .fab_title_size {
      user-select: none;
      font-size: $fab_title_size_font_size;
      font-weight: $fab_title_size_font_weight;
      color: $fab_title_size_font_color;
    }

    .gold_icon {
      margin: $gold_icon_margin;
    }
  }
}

@keyframes scaleDraw {
  /*定义关键帧、scaleDrew是需要绑定到选择器的关键帧名称*/
  0% {
    transform: scale(1);
    /*开始为原始大小*/
  }

  25% {
    transform: scale(1.08);
    /*放大1.1倍*/
  }

  50% {
    transform: scale(1);
  }

  75% {
    transform: scale(1.08);
  }
}
</style>
