<template>
  <div class="refer_progress">
    <div class="refer_progress_outer">
      <div class="refer_progress_inner">
        <div
          :class="['refer_progress_bg', { refer_progress_success_bg: progress >= 100 }]"
          :style="`width: ${progress >= 100 ? 100 : progress}%; height: ${strokeHeight}px;`"
        ></div>
      </div>
      <div class="refer_progress_text">{{ progress >= 100 ? 100 : progress }}%</div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Progress',
  props: {
    width: {
      // 进度条总宽度
      type: Number,
      default: 100
    },
    progress: {
      // 当前进度
      type: [Number, String],
      default: 36
    },
    strokeHeight: {
      // 进度条线的宽度
      type: Number,
      default: 8
    },
    showInfo: {
      // 是否显示进度数值或状态图标
      type: Boolean,
      default: true
    }
  }
}
</script>
<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';
.refer_progress {
  margin: $refer_progress_margin;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  width: $refer_progress_width;
  .refer_progress_outer {
    width: $refer_progress_outer_width;
    display: flex;
    align-items: center;
    .refer_progress_inner {
      display: inline-block;
      width: $refer_progress_inner_width;
      background: $refer_progress_inner_bg;
      border-radius: $refer_progress_inner_border_radius;
      .refer_progress_bg {
        position: relative;
        background: $refer_progress_bg_bg;
        border-radius: $refer_progress_bg_radius;
        transition: width 0.8s ease-in-out; /* 这里设置了动画效果 */
        overflow: hidden;
        &::before {
          content: ' ';
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 100%;
          background-color: #f12c4c;
          background-image: linear-gradient(to right, #f12c4c, #ffce5c 20%, #f12c4c 50%, #ffce5c 78%, #f12c4c 100%);
          background-size: 200% 100%;
          animation: masked-animation 2s infinite linear;
        }
        @keyframes masked-animation {
          0% {
            background-position: 100% 0;
          }
          50% {
            background-position: 0 0;
          }
          100% {
            background-position: -100%, 0;
          }
        }
      }
      /* 进度条动画 */
      @keyframes progress-animation {
        0% {
          width: 0%;
        }
        100% {
          width: 100%;
        }
      }
      .refer_progress_success_bg {
        background: $refer_progress_success_bg;
      }
    }
    .refer_progress_text {
      margin-left: auto;
      color: $refer_progress_text_color;
    }
  }
}
</style>
