<template>
  <div :class="['my-checkbox', { disabled: disabled }]" @click="!disabled && (isChecked = !isChecked)">
    <icon-font :name="!isChecked ? offIcon : onIcon" v-bind="$attrs" class="flex-shrink-0" />
    <slot></slot>
  </div>
</template>

<script>
/**
 * checkbox 复选框
 * @property {String Number} size 图标大小，单位px（默认16）
 * @property {String} offIcon 不活动时使用的图标
 * @property {String} onIcon 激活时使用的图标
 * @property {Boolean} disabled 是否禁用
 * @example <check-box v-model="checked" disabled>哈哈哈哈哈</check-box>
 */
export default {
  props: {
    // 不活动时使用的图标
    offIcon: {
      type: String,
      default: 'icon-checkoff1'
    },
    // 激活时使用的图标
    onIcon: {
      type: String,
      default: 'icon-checkon'
    },
    // 是否为选中状态
    value: {
      type: Boolean,
      default: false
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isChecked: this.value
    }
  },
  watch: {
    isChecked(newVal) {
      this.$emit('input', newVal)
    },
    value(newVal) {
      this.isChecked = newVal
    }
  }
}
</script>

<style>
.my-checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.my-checkbox.disabled {
  opacity: 0.3;
  cursor: not-allowed;
}
</style>
