<template>
  <custom-dialog v-model="visible" :full="true">
    <div class="comp aws_face">
      <div ref="awsFaceRootRef" class="aws_face_root"></div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { getResour } from '@/utils/loadServer.js'
import { createFaceLivenessSession, indexFaces, deleteFaces, compareFaces } from '@/api/modules/face.js'

export default {
  data() {
    return {
      visible: false,
      compareType: 1, // 1=绑定，2=解绑，3=校验人脸
      rekognition_code: '' // 用户临时凭证
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig'])
  },
  methods: {
    /**
     * 打开
     * @param {*} compare_type 1=绑定，2=解绑，3=校验人脸
     * @param {*} rekognition_code 用户临时凭证
     */
    open({ compare_type = 1, rekognition_code = '' } = {}) {
      return new Promise(async (resolve, reject) => {
        this.faceResolve = resolve
        this.faceReject = reject
        // 当前是否可以使用媒体
        if (!navigator.mediaDevices) return this.close(1)

        this.compareType = compare_type
        this.rekognition_code = rekognition_code
        this.visible = true
        this.initPrepare()
      })
    },
    /**
     * 关闭
     * 1:浏览器不支持 2:手动关闭 3:找不到设备 4:失败 5:成功
     */
    close(status = 1, msg = '') {
      this.visible = false
      this.faceResolve({ code: status, msg })
    },
    /**
     * 初始化准备信息
     * 1. 数据接口
     * 2. sdk
     */
    async initPrepare() {
      const params = {
        token: this.user?.token,
        language: this.languageType,
        rekognition_code: this.rekognition_code
      }
      this.$pop.loading().show()
      const res = await createFaceLivenessSession(params).catch(e => e)
      if (!res || res.code != 0) {
        this.close(4)
        this.$pop.loading().hide()
        return
      }

      const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      const { clientRequestToken = '', identityPoolId = '', region = '', sessionId = '' } = resData
      const { libsPath = '' } = JSON.parse(process.env.VUE_APP_PATH)
      getResour('awsface', [`${libsPath}/aws-face.js`, `${libsPath}/aws-face.css`], r => {
        this.$pop.loading().hide()
        if (!r) return this.close(4)

        this.$nextTick(() => {
          const awsFaceRootRef = this.$refs['awsFaceRootRef']
          if (
            awsFaceRootRef &&
            window.FACE_LIVENESS_DETECTOR &&
            Object.keys(window.FACE_LIVENESS_DETECTOR).length > 0
          ) {
            const poolId = atob(atob(identityPoolId))
            const config = {
              aws_project_region: region,
              aws_cognito_identity_pool_id: poolId,
              aws_cognito_region: region,
              oauth: {},
              aws_cognito_username_attributes: [],
              aws_cognito_social_providers: [],
              aws_cognito_signup_attributes: ['EMAIL'],
              aws_cognito_mfa_configuration: 'OFF',
              aws_cognito_mfa_types: ['SMS'],
              aws_cognito_password_protection_settings: {
                passwordPolicyMinLength: 8,
                passwordPolicyCharacters: []
              },
              aws_cognito_verification_mechanisms: ['EMAIL']
            }

            window.FACE_LIVENESS_DETECTOR.initFace(
              awsFaceRootRef,
              {
                config,
                sessionId,
                region,
                language: this.languageType,
                templateColor: {
                  // // 录入背景色
                  // faceBg: "",
                  // // 录入椭圆边框宽度
                  // circularBorderWidth: "",
                  // // 录入椭圆边框色值
                  // circularBorderColor: "",
                  // // 校验椭圆边框宽度
                  // checkCircularBorderWidth: "",
                  // // 校验椭圆边框色值
                  // checkCircularBorderColor: "",
                  // // 录入/校验椭圆缩放 标准为2 大于2放大 小于2缩小
                  // faceCircularScale: 2,
                  // // 校验背景变化色值 ['rgb(0,0,0)','rgb(255,255,0)','rgb(255,0,0)','rgb(255,0,255)','rgb(0,255,255)','rgb(0,255,0)','rgb(255,0,0)','rgb(255,0,255)']
                  // checkCircularBgArr: "",
                  // // 全局文字颜色
                  // faceGlobalTextColor: "",
                  // // 全局文字粗细
                  // faceGlobalTextWeight: "",
                  // // 关闭icon色值
                  // faceCloseIconColor: "",
                  // // 提示距离顶部的距离
                  // faceMsgMarginTop: "10px",
                  // // 提示背景色
                  // faceMsgBgColor: "",
                  // // 进度条高度
                  // faceProgressBarHeight: "",
                  // // 进度条背景色
                  // faceProgressBarBgColor: "",
                  // // 进度条边框色
                  // faceProgressBarBorderColor: "",
                  // // 进度条块色值
                  // faceProgressPieceColor: "",
                }
              },
              data => this.successCallback(data),
              data => this.errorCallback(data),
              data => this.closeCallback(data)
            )
          } else {
            this.close(4)
          }
        })
      })
    },
    // 人脸成功回调
    async successCallback({ id = '' }) {
      const params = {
        token: this.user?.token,
        session_id: id,
        rekognition_code: this.rekognition_code
      }
      // 1=绑定，2=解绑，3=校验人脸
      const getInterface = this.compareType == 1 ? indexFaces : this.compareType == 2 ? deleteFaces : compareFaces
      this.$pop.loading().show()
      const res = await getInterface(params).catch(e => e)
      this.$pop.loading().hide()
      if (!res || res.code != 0) return this.close(4)
      this.close(5)
    },
    // 人脸失败回调
    errorCallback(errorData = {}) {
      let msg = ''
      if (typeof errorData === 'string') {
        msg = errorData
      } else if (typeof errorData === 'object') {
        const { state = '', error = {} } = errorData
        console.log('error...', error.message)
        // 错误信息
        let message = this.$t(`FACE_${state}`)
        message = message ? message : this.$t('FACE_DEFAULT_ERROR')
        msg = message
      }

      this.close(4, msg)
    },
    // 关闭人脸识别
    closeCallback() {
      this.close(2)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

// 针对移动端样式
.comp.aws_face {
  width: $aws_face_app_width;
  height: $aws_face_app_height;
  background: $aws_face_app_bg;
  .aws_face_root {
    width: $aws_face_root_app_width;
    height: $aws_face_root_app_height;
  }
}
// 针对pc样式
@include breakpoint-only-up() {
  .comp.aws_face {
    width: $aws_face_root_pc_width;
    height: $aws_face_root_pc_height;
  }
}
</style>
