<template>
  <div class="comp menu_head">
    <user-sprite-image :value="user.headimg" :size="commonStyle('menu_head_logo_size')" @click.native="onJump" />
    <div class="account_name">{{ user.nickname }}</div>
    <div class="account_user">
      <span class="account_user_id">ID:{{ user.uid }}</span>
      <icon-font
        v-zoom
        name="icon-icon_account_copy"
        :size="commonStyle('menu_head_account_icon_copy_size')"
        @click="onCopy"
      />
    </div>
    <!-- 多货币/站点 -->
    <site-lang-head v-if="isApp" />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import copy from 'copy-to-clipboard'
import UserSpriteImage from '@/components/ui/userSpriteImage/user-sprite-image'
import SiteLangHead from '@/components/ui/site-lang-head'
import _common from '@style/components/ui.modules.scss'
import { AJ_DOTTING } from '@/utils/dotting.js'

export default {
  components: {
    UserSpriteImage,
    SiteLangHead
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'user'])
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    // 复制到剪贴板
    onCopy() {
      AJ_DOTTING.getAjdata('USER_DOWN_COPY')

      const { uid = '' } = this.user
      const isCopy = copy(uid)
      if (isCopy) this.$pop.message().show({ content: this.$t('UI_Copied') })
    },
    // 跳转
    onJump() {
      this.$emit('action', { value: 9 })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.comp.menu_head {
  @include setBox(column, center, center);
  padding: $menu_head_padding;
  border-bottom: $menu_head_border;
  .account_name {
    width: 100%;
    word-break: normal;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: $menu_head_account_name_size;
    color: $menu_head_account_name_color;
    text-align: $menu_head_account_name_align;
    margin: $menu_head_account_name_margin;
  }
  .account_user {
    width: 100%;
    @include setBox(row, center, center);
    margin: $menu_head_account_user_margin;
    font-size: $menu_head_account_user_size;
    color: $menu_head_account_user_color;
    .account_user_id {
      margin: $menu_head_account_user_id_margin;
    }
  }
}
</style>
