<template>
  <div
    ref="wrapper_text"
    :id="uuid"
    class="wrapper_text"
    :style="{
      width: w,
      height: h,
      'line-height': h,
      'text-align': textAlign,
      'font-size': `${parseInt(maxFont)}px`,
      'font-weight': fontWeight
    }"
  ></div>
</template>

<script>
import AdaptableText from './AdaptableText.js'
export default {
  props: {
    text: String,
    uuid: String,
    level: {
      type: Number,
      default: 1
    },
    step: {
      type: Number,
      default: 2
    },
    textAlign: {
      type: String,
      default: 'center'
    },
    maxFont: {
      type: [String, Number],
      default: 14
    },
    fontWeight: {
      type: [String, Number],
      default: 400
    }
  },
  watch: {
    text(val) {
      this.resetText(val)
    }
  },
  data: () => ({
    w: {},
    h: {},
    textToAdapt: {}
  }),
  mounted() {
    this.$nextTick(() => {
      this.setFont()
    })
  },
  methods: {
    setFont() {
      let el = this.$refs['wrapper_text']
      // let el = document.querySelector(`#${this.uuid}`);

      let eventLevel = el
      for (let i = 0; i < this.level; i++) {
        eventLevel = eventLevel ? eventLevel.parentNode : el
      }
      if (eventLevel) {
        this.w = `${eventLevel.clientWidth}px`
        this.h = `${eventLevel.clientHeight}px`
      }

      let options = {
        el: this.uuid,
        step: this.step,
        minFontSize: 6,
        maxFont: parseInt(this.maxFont),
        width: null
      }
      const textToAdapt = new AdaptableText(el, options)
      this.textToAdapt = textToAdapt
      el.innerHTML = this.text
      setTimeout(() => {
        textToAdapt.init()
        setTimeout(() => {
          textToAdapt.adapt()
        }, 36)
      }, 67)
    },
    resetText(val) {
      if (!this.textToAdapt.adapt) return
      // let el = document.querySelector(`#${this.uuid}`);
      let el = this.$refs['wrapper_text']
      el.innerHTML = val
      this.textToAdapt.adapt()
    }
  }
}
</script>
