<template>
  <div :class="['time_picker', isApp ? 'time_picker_app' : 'time_picker_pc']">
    <div class="input_box">
      <div class="input_item" @click="onShow">{{ value[0] }}</div>
      <span>:</span>
      <div class="input_item" @click="onShow">{{ value[1] }}</div>
    </div>
    <div
      v-if="show"
      :class="['select_box', expandAin ? 'expand' : 'hide']"
      v-click-outside="{ handler: onClickOutside }"
      :style="optionsStyle"
    >
      <div class="options left" :id="`hourbox_${prefixId}`">
        <div
          v-for="(item, index) in hourList"
          :key="index"
          :id="`hour_${prefixId}_${item.label}`"
          :class="[
            'options_item',
            value[0] === item.label && 'active',
            disabledHour(item.label, value[1]) && 'disabled'
          ]"
          @click="onChangeHour(item, index)"
        >
          {{ item.label }}
        </div>
      </div>
      <div class="options right" :id="`minbox_${prefixId}`">
        <div
          v-for="(item, index) in minuteList"
          :key="index"
          :id="`min_${prefixId}_${item.label}`"
          :class="[
            'options_item',
            value[1] === item.label && 'active',
            disabledMinute(value[0], item.label) && 'disabled'
          ]"
          @click="onChangeMinute(item, index)"
        >
          {{ item.label }}
        </div>
      </div>
      <div class="btn-confirm" @click="onConfirm">{{ $t('UI.Resp_Remind_Confirm') }}</div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  props: {
    value: {
      type: Array,
      default: () => ['', '']
    },
    optionsStyle: {
      type: Object,
      default: () => {
        return {}
      }
    },
    disabledHour: {
      type: Function,
      default() {
        return false
      }
    },
    disabledMinute: {
      type: Function,
      default() {
        return false
      }
    }
  },
  data() {
    return {
      hourList: [],
      minuteList: [],
      show: false,
      expandAin: false,
      hourIndex: -1,
      minuteIndex: -1,
      prefixId: ''
    }
  },
  created() {
    this.prefixId = this.uuid()
    this.hourList = this.createList(24)
    this.minuteList = this.createList(60)
  },
  mounted() {},
  computed: {
    ...mapState('base', ['isApp'])
  },
  watch: {
    value: {
      handler(val) {},
      deep: true
    }
  },
  methods: {
    uuid() {
      function S4() {
        return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)
      }
      return S4() + S4() + '-' + S4() + '-' + S4() + '-' + S4() + '-' + S4() + S4() + S4()
    },
    createList(len, isSupplyZero = true) {
      const arr = []
      for (let i = 0; i < len; i++) {
        const label = i < 10 && isSupplyZero ? `0${i}` : `${i}`
        arr.push({ label, value: i })
      }
      return arr
    },
    onClickOutside() {
      this.expandAin = false
      setTimeout(() => {
        this.show = false
      }, 210)
    },
    onChangeMinute(data, index) {
      const m = data.label
      let flag = this.disabledMinute(this.value[0], m)
      if (flag) return
      this.$emit('change', [this.value[0], m])
      this.$forceUpdate()
    },
    onChangeHour(data, index) {
      const h = data.label
      let flag = this.disabledHour(h, this.value[1])
      if (flag) return
      this.hourIndex = index
      this.$emit('change', [h, this.value[1]])
      this.$forceUpdate()
    },
    onShow() {
      this.show = true
      this.expandAin = true
      this.$nextTick(() => {
        const hBox = document.getElementById(`hour_${this.prefixId}_${this.value[0]}`)
        hBox.scrollIntoView({ behavior: 'instant' })
        const mBox = document.getElementById(`min_${this.prefixId}_${this.value[1]}`)
        mBox.scrollIntoView({ behavior: 'instant' })
      })
    },
    onConfirm() {
      this.onClickOutside()
      this.$emit('confirm', [this.value[0], this.value[1]])
      this.$forceUpdate()
    }
  }
}
</script>
<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';
.time_picker_app {
  position: relative;
  .input_box {
    @include setBox(flex, flex-start, center);
    gap: $time_picker_input_gap_app;
    .input_item {
      @include setBox(flex, center, center);
      width: $time_picker_input_item_width_app;
      height: $time_picker_input_item_height_app;
      background: $time_picker_input_item_bg_app;
      border-radius: $time_picker_input_item_radius_app;
      color: $time_picker_input_item_color_app;
      font-size: $time_picker_input_item_size_app;
      font-weight: $time_picker_input_item_weight_app;
    }
    span {
      color: $time_picker_input_item_color_app;
      font-size: $time_picker_input_item_size_app;
      font-weight: $time_picker_input_item_weight_app;
    }
  }
  .select_box {
    position: absolute;
    @include setBox(flex, center, center);
    gap: $time_picker_select_box_gap_app;
    width: $time_picker_select_box_width_app;
    height: $time_picker_select_box_height_app;
    background: $time_picker_select_box_bg_app;
    border-radius: $time_picker_select_box_radius_app;
    padding: $time_picker_select_box_padding_app;
    box-sizing: border-box;
    z-index: 1;
    animation-duration: 0.2s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-fill-mode: both;
    .btn-confirm {
      position: $time_picker_btn_confirm_pos_app;
      top: $time_picker_btn_confirm_top_app;
      left: $time_picker_btn_confirm_left_app;
      width: $time_picker_btn_confirm_width_app;
      height: $time_picker_btn_confirm_height_app;
      text-align: $time_picker_btn_confirm_text_align_app;
      line-height: $time_picker_btn_confirm_line_height_app;
      background: $time_picker_btn_confirm_background_app;
      border-radius: $time_picker_btn_confirm_border_radius_app;
      font-weight: $time_picker_options_item_weight_app;
      font-size: $time_picker_btn_confirm_size_app;
      color: $time_picker_btn_confirm_color_app;
      cursor: pointer;
    }
    .options {
      width: $time_picker_options_width_app;
      height: $time_picker_options_height_app;
      overflow: auto;
      &.left {
        text-align: $time_picker_options_left_card_align_app;
      }
      &.right {
        text-align: $time_picker_options_right_card_align_app;
      }
      .options_item {
        @include setBox(flex, center, center);
        width: $time_picker_options_item_width_app;
        height: $time_picker_options_item_height_app;
        border-radius: $time_picker_options_item_radius_app;
        font-weight: $time_picker_options_item_weight_app;
        font-size: $time_picker_options_item_size_app;
        color: $time_picker_options_item_color_app;
        transition: all 0.3s;
        &.active {
          background: $time_picker_options_item_active_bg_app;
        }
      }
      .disabled {
        color: $time_picker_input_item_color_app;
      }
    }
    &.hide {
      animation-name: hideAni;
    }
    &.expand {
      animation-name: expandAni;
    }
    @keyframes expandAni {
      0% {
        opacity: 0;
        height: 0;
      }
      100% {
        opacity: 1;
        height: $time_picker_select_box_height_pc;
      }
    }
    @keyframes hideAni {
      0% {
        opacity: 1;
        height: $time_picker_select_box_height_pc;
      }
      100% {
        opacity: 0;
        height: 0;
      }
    }
  }
}
