  }
  .select_box {
    position: absolute;
    @include setBox(flex, center, center);
    gap: $time_picker_select_box_gap_pc;
    width: $time_picker_select_box_width_pc;
    height: $time_picker_select_box_height_pc;
    background: $time_picker_select_box_bg_pc;
    border-radius: $time_picker_select_box_radius_pc;
    padding: $time_picker_select_box_padding_pc;
    box-sizing: border-box;
    z-index: 1;
    animation-duration: 0.2s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-fill-mode: both;
    .btn-confirm {
      position: $time_picker_btn_confirm_pos_pc;
      top: $time_picker_btn_confirm_top_pc;
      left: $time_picker_btn_confirm_left_pc;
      width: $time_picker_btn_confirm_width_pc;
      height: $time_picker_btn_confirm_height_pc;
      text-align: $time_picker_btn_confirm_text_align_pc;
      line-height: $time_picker_btn_confirm_line_height_pc;
      background: $time_picker_btn_confirm_background_pc;
      border-radius: $time_picker_btn_confirm_border_radius_pc;
      font-weight: $time_picker_options_item_weight_pc;
      font-size: $time_picker_btn_confirm_size_pc;
      color: $time_picker_btn_confirm_color_pc;
      cursor: pointer;
    }
    .options {
      width: $time_picker_options_width_pc;
      height: $time_picker_options_height_pc;
      overflow: auto;
      &.left {
        text-align: $time_picker_options_left_card_align_pc;
      }
      &.right {
        text-align: $time_picker_options_right_card_align_pc;
      }
      .options_item {
        @include setBox(flex, center, center);
        width: $time_picker_options_item_width_pc;
        height: $time_picker_options_item_height_pc;
        border-radius: $time_picker_options_item_radius_pc;
        font-weight: $time_picker_options_item_weight_pc;
        font-size: $time_picker_options_item_size_pc;
        color: $time_picker_options_item_color_pc;
        cursor: pointer;
        transition: all 0.3s;
        &.active {
          background: $time_picker_options_item_active_bg_pc;
        }
      }
      .disabled {
        color: $time_picker_input_item_color_app;
        cursor: not-allowed;
      }
    }
    &.hide {
      animation-name: hideAni;
    }
    &.expand {
      animation-name: expandAni;
    }
    @keyframes expandAni {
      0% {
        opacity: 0;
        height: 0;
      }
      100% {
        opacity: 1;
        height: $time_picker_select_box_height_pc;
      }
    }
    @keyframes hideAni {
      0% {
        opacity: 1;
        height: $time_picker_select_box_height_pc;
      }
      100% {
        opacity: 0;
        height: 0;
      }
    }
  }
}
</style>
