<template>
  <div class="movable_gold"></div>
</template>
<script>
import loadIcon from '@/assets/icons'
import { getCacheIcon } from '@/assets/imgCache.js'
export default {
  name: 'goldBox',
  props: {
    goldAnimationClass: {
      // 金币动画效果锚点
      type: String,
      default: ''
    },
    isGoldState: {
      // 是否开金币动画开关
      type: Boolean,
      default: false
    },
    plusSizeLeft: {
      // 金币便宜向左右移动位置
      type: Number,
      default: 10
    },
    plusSizeTop: {
      // 金币便宜向上下移动位置
      type: Number,
      default: 10
    }
  },
  data() {
    return {}
  },
  mounted() {},
  methods: {
    loadIcon: key => loadIcon(key, false),
    showGoldAnimation() {
      if (!this.isGoldState) return
      const bankAccountBtnText = document.querySelector(`.${this.goldAnimationClass}`)
      const movableGold = document.querySelector('.movable_gold')
      let box = document.createElement('div')

      // const result = getCacheIcon("referGift_turntable_gold");
      // let src = '';
      // if (result) {
      // 	src = typeof result === "string" ? result : result.value.src;
      // }
      box.className = 'plus'
      box.innerHTML = `<div class="plus-icon" ></div>`

      const btnRect = movableGold.getBoundingClientRect()
      const left = btnRect.left + btnRect.width / 2 - this.plusSizeLeft / 2
      const top = btnRect.top - this.plusSizeTop

      const carRect = bankAccountBtnText.getBoundingClientRect()
      const x = carRect.left + carRect.width / 2 - this.plusSizeLeft / 2 - left
      const y = carRect.top - this.plusSizeTop - top

      box.style.setProperty('--left', `${left}px`)
      box.style.setProperty('--top', `${top}px`)
      box.style.setProperty('--x', `${x}px`)
      box.style.setProperty('--y', `${y}px`)
      box.addEventListener('animationend', () => {
        box.remove()
      })
      document.body.appendChild(box)
    },
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    }
  }
}
</script>
<style lang="scss" scoped>
.movable_gold {
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  .movable_gold_img {
    width: 30px;
    height: 30px;
  }
}
</style>
