<template>
  <div ref="tableTempRef" class="comp table_temp">
    <div
      ref="tableTempListRef"
      class="table_temp_list"
      :class="tableClass"
      :style="[tableStyle]"
      @touchmove.stop
      v-horizontal-scroll
    >
      <!-- 表头 -->
      <div ref="tableHeadRef" class="table_head" :style="[headStyle, fixedHidden]">
        <div
          v-for="head in headList"
          :key="head.prop"
          class="vertical altitude break_word"
          :style="[defaultWidth, headItemStyle, angeStyle(head, 'head')]"
        >
          {{ head.label }}
        </div>
      </div>
      <!-- 列表 -->
      <div
        v-if="tableList.length > 0"
        ref="tableListRef"
        class="table_list"
        :style="`height: ${tebleHeight > 0 ? tebleHeight + 'px' : 'auto'};`"
      >
        <div ref="tableRowRef" class="table_row" v-for="(item, index) in tableList" :key="index">
          <div
            v-for="head in headList"
            :key="head.prop"
            class="table_cell altitude"
            :style="[defaultWidth, horStyle, angeStyle(head, 'row')]"
          >
            <!-- 仿 element 插槽 -->
            <template v-if="head.slot">
              <slot :name="head.slot" :row="item" :index="index"></slot>
            </template>
            <template v-else>
              {{ item[head.prop] }}
            </template>
          </div>
        </div>
        <div class="table_temp_next c_box" :style="`width: ${tebleWidth}px;`" v-if="loadMoreState">
          <div @click="nextPage" class="remain_box btn_radius">
            {{ loadMore || $t('General.General_8') }}
          </div>
        </div>
      </div>
      <div v-else class="table_empty">
        <empty />
      </div>
    </div>
    <!-- 横向滚动条 -->
    <div v-if="isScrollX" class="scroll_bar" :style="[scrollStyleTrackX]">
      <div
        ref="scrollPieceRef"
        class="scroll_bar_piece"
        :style="[scrollStyleTrackPieceX, { left: `${scrollXLeft}px` }]"
        @mousedown="onScrollDown"
      ></div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Empty from '@/components/ui/empty'

export default {
  components: {
    Empty
  },
  props: {
    // 表头设置 + 表头列表
    tableHead: {
      type: Object,
      default() {
        return {
          // size: {                          // 整个表的字体样式
          //     fontSize: '16px',
          //     fontWeight: 600
          // },
          // radius: '5px',                           // 整个表的圆角
          // zebra: 'zebra_1',                        // 行是否出现配套斑马纹，样式写在css里，这里只写class
          // isScroll: false,                         // 表是否滚动，配合父级设置高度使用
          // head: {                          // 表头样式
          //     bg: "#ffffff",                       // 表头的背景颜色（颜色值）
          //     color: "#ffffff",                    // 表头文字颜色（颜色值）
          //     size: {                              // 表头文字字体样式
          //         fontSize: '16px',
          //         fontWeight: 600
          //         textAlign: 'center'
          //     },
          //     radius: '5px',                       // 表头圆角
          //     margin: '10px 10px 10px 10px',       // 表头 margin
          //     padding: '10px 10px 10px 10px',      // 表头 padding
          //     fixed: true,                         // 表头是否固定
          // },
          // hor: {                           // 每行的样式
          //     bg: "#ffffff",                       // 行的背景颜色（颜色值）
          //     border_t: '#ffffff',                 // 行边框 --> （颜色值）
          //     border_r: '#ffffff',                 // 行边框 --> （颜色值）
          //     border_b: '#ffffff',                 // 行边框 --> （颜色值）
          //     border_l: '#ffffff',                 // 行边框 --> （颜色值）
          //     margin: '10px 10px 10px 10px',       // 行 margin 这里只写class
          //     padding: '10px 10px 10px 10px',      // 行 padding 这里只写class
          //     color: '#fff',                       // 行 color --> 样式（#fff）style
          //     size: {                              // 行 size
          //         fontSize: '16px',
          //         fontWeight: 600
          //     }
          // },
          // columns: [                              // 对应列
          //     {
          //         label: 'Level',                     // 名称
          //         prop: "name",                       // 对应数据的字段名
          //         width: '200px',                     // 单列设置宽度
          //         align: "center",                    // 单列文字排列
          //         minWidth: '100px',                  // 单列最小宽度
          //         maxWidth: '300px',                  // 单列最大宽度
          //         fixed: 'left',                      // 单列是否定位（left:左边，right:右边）
          //         slot: "name",                       // 单列插槽（使用方法和 element 一样）---> 父组件插槽里 <template slot="..." slot-scope="{row, index}"></template>
          //     },
          // ]
        }
      }
    },
    // 表列表
    tableList: {
      type: Array,
      default() {
        return []
      }
    },
    // 是否显示下一页按钮
    loadMoreState: {
      type: Boolean,
      default: false
    },
    // 下一页按钮文案
    loadMore: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      width_app: 70, // 最小默认宽度 app
      width_pc: 100, // 最小默认宽度 pc
      tebleWidth: 0, // 表格的总宽度
      tebleHeight: 0, // 表格的总高度
      headHeight: 0, // 表头的高
      scrollWidth: 0, // 表格的滚动条总宽度
      scrollXLeft: 0, // 横向滚动条距离左边的宽度
      isScrollX: false, // 是否存在 x轴 滚动条
      isScrollY: false // 是否存在 y轴 滚动条
    }
  },
  watch: {
    tableList: {
      handler() {
        this.$nextTick(() => {
          if (this.$refs['tableHeadRef']) {
            this.altitude(this.$refs['tableHeadRef'])

            const { isScroll = false } = this.tableHead

            const h = this.$refs['tableHeadRef'].offsetHeight || this.$refs['tableHeadRef'].clientHeight
            const scrollWidth = this.$refs['tableHeadRef'].scrollWidth

            this.headHeight = h
            this.scrollWidth = scrollWidth
            /**
             * 表格高度 = 总高度 - 表头高度
             */
            const tableTempRef = this.$refs['tableTempRef']
            // 获取表格父级的高
            const tableTempParent = tableTempRef.parentNode || tableTempRef.parentNode
            const tableTempParentNodStyle = getComputedStyle(tableTempParent) || {}
            const tableTempRef_h = parseInt(tableTempParentNodStyle.height)
            // 获取表格的高 = 表格的总高 - 获取表格父级的padding
            const py = parseInt(tableTempParentNodStyle.paddingTop) + parseInt(tableTempParentNodStyle.paddingBottom)
            const parentNodeHeight = tableTempRef_h - py
            // 表格高 = 表格的高 - 表头高
            if (isScroll) this.tebleHeight = parentNodeHeight - h
          }

          if (this.$refs['tableRowRef']) {
            const tableRowArr = this.$refs['tableRowRef'] || []
            tableRowArr.forEach(element => {
              this.altitude(element)
            })
          }

          this.countTebleWidth()
        })
      },
      deep: true,
      immediate: true
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('layout', ['containerBox']),
    // 表头列表
    headList() {
      const { columns = [] } = this.tableHead

      let newList = []
      const leftList = columns.filter(f => f.fixed == 'left')
      const rightList = columns.filter(f => f.fixed == 'right')
      newList = columns.filter(f => !f.fixed)

      newList.unshift(...leftList)
      newList = newList.concat(rightList)

      return newList
    },
    // 表格是否有定位,无定位设置 overflow: hidden;
    fixedHidden() {
      const isFixed = this.headList.find(f => f.fixed)
      return !isFixed ? { overflow: 'hidden' } : {}
    },
    // 每列默认的宽度, 除去设定的值
    defaultWidth() {
      const { columns = [] } = this.tableHead
      const m_w = this.isApp ? this.width_app : this.width_pc

      const col = columns.filter(f => f.width || f.minWidth)
      // 已经设过宽度的
      const col_h = col.reduce((pre, obj) => {
        const wid = parseInt(obj.width || 0)
        const min_w = parseInt(obj.minWidth || 0)
        return pre + (wid > min_w ? wid : min_w)
      }, 0)

      let w = this.tebleWidth - col_h
      w = w / (columns.length - col.length) == Infinity ? 0 : w / (columns.length - col.length)

      return {
        width: `${w > m_w ? w : m_w}px`
      }
    },
    // 整个表格的样式 class
    tableClass() {
      const { zebra = '' } = this.tableHead
      return `${zebra}`
    },
    // 整个表格的样式 style
    tableStyle() {
      const { size = {}, radius = '' } = this.tableHead
      let sty = {}

      if (size && Object.keys(size).length > 0) sty = { ...sty, ...size }
      if (radius) sty.borderRadius = radius

      return sty
    },
    // 整个表头样式 style
    headStyle() {
      const { head = {} } = this.tableHead
      const { bg = '', radius = '', margin = '', fixed = false } = head
      let sty = {}

      if (bg) sty.background = bg
      if (margin) sty.margin = margin
      if (radius) sty.borderRadius = radius
      if (fixed) {
        sty.position = 'sticky'
        sty.zIndex = 2
        sty.top = 0
        sty.left = 0
      }

      return sty
    },
    // 单个表头样式 style
    headItemStyle() {
      const { head = {} } = this.tableHead
      const { bg = '', color = '', size = {}, padding = '' } = head
      let sty = {}

      if (bg) sty.background = bg
      if (color) sty.color = color
      if (size && Object.keys(size).length > 0) sty = { ...sty, ...size }
      if (padding) sty.padding = padding

      return sty
    },
    // 行样式 style
    horStyle() {
      const { hor = {} } = this.tableHead
      const {
        bg = '',
        border_t = '',
        border_r = '',
        border_b = '',
        border_l = '',
        margin = '',
        padding = '',
        color = '',
        size = {}
      } = hor
      let sty = {}

      if (bg) sty.background = bg
      if (border_t) sty.borderTop = `1px solid ${border_t}`
      if (border_r) sty.borderRight = `1px solid ${border_r}`
      if (border_b) sty.borderBottom = `1px solid ${border_b}`
      if (border_l) sty.borderLeft = `1px solid ${border_l}`
      if (margin) sty.margin = margin
      if (padding) sty.padding = padding
      if (color) sty.color = color
      if (size) sty = { ...sty, ...size }

      return sty
    },
    // 初始化 -- 自定义横向滚动条样式 -- 外层轨道
    scrollStyleTrackX() {
      const leftList = this.headList.filter(f => f.fixed == 'left')
      const left_w = leftList.reduce((pre, r) => pre + parseInt(r.width || this.defaultWidth.width), 0)

      const rightList = this.headList.filter(f => f.fixed == 'right')
      const right_w = rightList.reduce((pre, r) => pre + parseInt(r.width || this.defaultWidth.width), 0)

      return {
        width: `${this.tebleWidth - left_w - right_w}px`,
        top: `${this.headHeight}px`,
        left: `${left_w}px`,
        right: `${right_w}px`
      }
    },
    // 初始化 -- 自定义横向滚动条样式 -- 内层滑块
    scrollStyleTrackPieceX() {
      // 计算出初始化时候滑块的宽度比例 -- (表格宽度 / 表格横向滚动条总宽度)
      const piece_w = (this.tebleWidth < this.scrollWidth ? this.tebleWidth / this.scrollWidth : 1) * 100
      return {
        width: `${piece_w}%`
      }
    }
  },
  methods: {
    // 统一行最大高度
    altitude(el) {
      if (!el) return

      this.$nextTick(() => {
        setTimeout(() => {
          const { margin = '' } = this.tableHead.hor || {}
          const marginArr = margin && margin.length > 3 ? margin.split(' ') : []
          const mar = parseInt(marginArr[0] || 0) + parseInt(marginArr[2] || 0)

          const h = el.clientHeight || el.offsetHeight
          const altitude = el.querySelectorAll('.altitude')
          altitude.forEach(cell => {
            cell.style.height = (el.getAttribute('class') == 'table_head' ? h : h - mar) + 'px'
          })
        }, 40)
      })
    },
    // 获取表格的父节点，计算表格的总宽度
    countTebleWidth() {
      this.$nextTick(() => {
        const temp = this.$refs['tableTempRef']
        if (!temp) return

        // 获取表格的父节点, parentNode是w3c的标准。parentElement是ie的标准。
        const tempParent = temp.parentNode || temp.parentElement
        const w = tempParent.getBoundingClientRect().width || 0

        const style = getComputedStyle(tempParent, false) || {}
        const paddingLeft = style?.paddingLeft.replace(/\s+|px/gi, '')
        const paddingRight = style?.paddingRight.replace(/\s+|px/gi, '')
        const padding = parseInt(paddingLeft) + parseInt(paddingRight)

        this.tebleWidth = w - padding

        // 判断是否有 y 轴滚动条, 有：剪滚动条宽度
        const tableListRef = this.$refs['tableListRef']
        if (tableListRef && this.tebleHeight) {
          const isScroll = tableListRef.scrollHeight > this.tebleHeight
          this.isScrollY = isScroll
          // if (isScroll) this.tebleWidth = this.tebleWidth - 5;
        }

        // 判断是否有 x 轴滚动条, 有：剪滚动条高度
        const tableHeadRef = this.$refs['tableHeadRef']
        if (tableHeadRef) {
          const isScroll = tableHeadRef.scrollWidth > this.tebleWidth
          this.isScrollX = isScroll
          // if (isScroll) this.tebleHeight = this.tebleHeight - 5;
        }

        // 监听横向滚动条
        if (this.$refs['tableTempListRef'])
          this.$refs['tableTempListRef'].addEventListener('scroll', e => this.scrollHorizontal(e.target.scrollLeft))
      })
    },
    /**
     * 列样式 style
     * @param {*} item 表头设置的样式
     * @param {*} sign 标识是表头还是表格
     */
    angeStyle(item = {}, sign = 'row') {
      let styleSty = {}
      const { width = '', align = '', minWidth = '', maxWidth = '', fixed = '', prop = '' } = item

      if (width) styleSty.width = `${width} !important`
      if (align) styleSty.justifyContent = `${align} !important`
      if (minWidth) styleSty.minWidth = `${minWidth} !important`
      if (maxWidth) styleSty.maxWidth = `${maxWidth} !important`

      if (fixed) {
        styleSty.position = 'sticky'
        styleSty.zIndex = sign == 'head' ? 3 : 1

        const leftList = this.headList.filter(f => f.fixed == 'left')
        const rightList = this.headList.filter(f => f.fixed == 'right')
        if (fixed == 'left') {
          const i = leftList.findIndex(f => f.prop == prop)
          let w = 0

          if (i != -1) {
            const list = leftList.slice(0, i)
            w = list.reduce((pre, r) => pre + parseInt(r.width || this.defaultWidth.width), 0)
          }

          styleSty.left = `${w}px`
          if (i + 1 == leftList.length) styleSty.boxShadow = '3px -1px 3px -1px rgba(0, 0, 0, 0.3) !important'
        }
        if (fixed == 'right') {
          const i = rightList.findIndex(f => f.prop == prop)
          let w = 0

          if (i != -1) {
            const right_h = rightList.length - 1
            const list = rightList.slice(0, right_h - i)
            w = list.reduce((pre, r) => pre + parseInt(r.width || this.defaultWidth.width), 0)
          }

          styleSty.right = `${w}px`
          if (i == 0) styleSty.boxShadow = '-3px -1px 3px -1px rgba(0, 0, 0, 0.3) !important'
        }
      }

      return styleSty
    },
    // 下一页
    nextPage() {
      this.$emit('nextPage')
    },

    /**
     * 监听横向滚动条的方法
     * @param {*} left 滚动条距离左边的宽度
     * @param {*} sign 1:默认滚动条距离左边的宽度转为自定义滚动条距离左边的宽度 2: 自定义滚动条距离左边的宽度转为默认滚动条距离左边的宽度
     */
    scrollHorizontal(left = 0, sign = 1) {
      if (sign == 1) {
        // 自带滚动条剩余空间的比例
        const scale = left / (this.scrollWidth - this.tebleWidth)
        // 自定义滚动条的剩余空间
        const piece_w = parseInt(this.scrollStyleTrackX.width) * (this.tebleWidth / this.scrollWidth)
        const surplus_w = parseInt(this.scrollStyleTrackX.width) - piece_w

        this.scrollXLeft = Number(surplus_w * scale).toFixed(2)
      } else {
        // 自定义滚动条的剩余空间
        const piece_w = parseInt(this.scrollStyleTrackX.width) * (this.tebleWidth / this.scrollWidth)
        const surplus_w = parseInt(this.scrollStyleTrackX.width) - piece_w
        // 自定义滚动条剩余空间的比例
        const scale = left / surplus_w
        // 默认滚动条剩余空间
        const surplusWidth = this.scrollWidth - this.tebleWidth

        this.$refs['tableTempListRef'].scrollLeft = surplusWidth * scale
      }
    },
    // 监听自定义滚动条鼠标滑动事件 -- 鼠标按下 --> 鼠标滑动 --> 鼠标抬起卸载全部事件
    onScrollDown(down) {
      if (this.isApp) return

      // 自定义滚动条的剩余空间
      const piece_w = parseInt(this.scrollStyleTrackX.width) * (this.tebleWidth / this.scrollWidth)
      const surplus_w = parseInt(this.scrollStyleTrackX.width) - piece_w

      // 滑动初始化值
      const initX = down.clientX || down.clientx || 0
      const left = this.$refs['tableTempListRef'].scrollLeft

      const mouseMove = e => {
        const x = e.clientX || e.clientx || 0

        const shiftX = left > x - initX ? left + (x - initX) : x - initX
        if (shiftX < 0 || shiftX > surplus_w) return

        this.scrollXLeft = shiftX
        this.scrollHorizontal(shiftX, 2)
      }

      // 监听自定义滚动条鼠标滑动事件 -- 鼠标滑动
      this.$refs['tableTempRef'].addEventListener('mousemove', mouseMove)

      // 监听全局鼠标滑动事件 -- 鼠标抬起
      document.onmouseup = () => {
        this.$refs['tableTempRef'].removeEventListener('mousemove', mouseMove)
        document.onmouseup = null
      }
    }
  },
  beforeDestroy() {
    if (!this.$refs['tableTempListRef']) return
    this.$refs['tableTempListRef'].removeEventListener('scroll', this.scrollHorizontal)
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.comp.table_temp {
  width: $table_temp_width;
  min-height: $table_temp_min_height;
  position: relative;
  user-select: none;

  .table_temp_list {
    width: $table_temp_list_width;
    @include setBox(column, flex-start, flex-start);
    overflow: auto;

    .table_head {
      @include setBox(row, flex-start, center);
      text-transform: $table_temp_head_text_transform;
      .vertical {
        word-break: break-all;
        @include setBox(row, left, center);
      }
    }

    .table_list {
      @include setBox(column, flex-start, flex-start);

      .table_row {
        @include setBox(row, center, center);

        .table_cell {
          word-break: break-all;
          @include setBox(row, left, center);
        }
      }

      .table_temp_next {
        position: sticky;
        z-index: 1;
        left: 0px;
      }
    }

    .table_empty {
      width: 100%;
      position: sticky;
      z-index: 1;
      left: 0px;
    }
  }

  .scroll_bar {
    position: absolute;
    height: $table_temp_scroll_height;
    background: $table_temp_scroll_bg;
    z-index: 9;
    border-radius: $table_temp_scroll_radius;

    .scroll_bar_piece {
      height: $table_temp_scroll_piece_height;
      background: $table_temp_scroll_piece_bg;
      position: relative;
      border-radius: $table_temp_scroll_piece_radius;
    }
  }

  .remain_box {
    color: $table_temp_scroll_next_btn_color;
    border: 1px solid $table_temp_scroll_next_btn_border;
    cursor: pointer;
    margin: $table_temp_scroll_next_btn_margin;
    font-size: $table_temp_scroll_next_btn_size;
    font-weight: $table_temp_scroll_next_btn_bold;
    padding: $table_temp_scroll_next_btn_padding;
  }
}

// 斑马纹
.zebra_1 {
  .table_row {
    background: $table_temp_zebra_1_bg_1 !important;

    .table_cell {
      background: $table_temp_zebra_1_bg_1 !important;
    }

    &:nth-child(2n) {
      background: $table_temp_zebra_1_bg_2 !important;

      .table_cell {
        background: $table_temp_zebra_1_bg_2 !important;
      }
    }
  }
}
</style>
