<template>
  <div class="comp user_menu">
    <!-- 金币剩余组件 -->
    <bank-account id="bankAccountId" @showAccount="showAccount" />
    <!-- 充值提现按钮 -->
    <v-btn
      v-zoom
      class="desposit_btn btn_radius"
      color="t_btn_host_color"
      :height="commonStyle('top_navigation_btn_height')"
      :ripple="false"
      @click="showAccount"
    >
      {{ $t('UI_Deposit_desposit') }}
    </v-btn>
    <!-- 多货币/站点 -->
    <site-lang-head v-if="!isApp" />
    <!-- 用户信息按钮，下拉列表 -->
    <v-menu
      v-model="accountMenu"
      offset-y
      :nudge-bottom="22"
      :nudge-left="isApp ? 0 : 80"
      :close-on-click="true"
      :close-on-content-click="false"
      :min-width="commonStyle('account_menu_width')"
      :z-index="commonStyle('account_menu_zindex')"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          v-zoom
          class="prolife_btn btn_radius t_btn_color_1"
          :width="commonStyle('account_menu_prolife_btn_width')"
          :min-width="commonStyle('account_menu_prolife_btn_width')"
          :height="commonStyle('top_navigation_btn_height')"
          :ripple="false"
          plain
          outlined
          v-bind="attrs"
          v-on="on"
        >
          <v-badge overlap dot color="t_red_drop" class="d-flex align-center" :value="userEntryDot">
            <icon-font name="icon-Icon_Prolife" :size="commonStyle('account_menu_btn_size')" />
          </v-badge>
        </v-btn>
      </template>
      <v-list dense light subheader class="account_menu_list">
        <menu-head @action="setAction" />
        <div class="account_menu_list_content">
          <v-list-item
            v-for="(item, index) in menuList"
            :key="index"
            class="list_item"
            :ripple="false"
            @click="setAction(item)"
          >
            <v-badge overlap dot color="t_red_drop" :value="item.showRedDot?.value" class="d-flex align-center">
              <icon-font
                class="gray_light_color_text"
                :name="item.icon"
                :size="commonStyle('account_menu_list_item_icon_size')"
              />
            </v-badge>
            <v-list-item-title class="list_item_text">
              {{ item.name }}
            </v-list-item-title>
            <v-list-item-action v-if="item.value === '14'">
              <v-switch v-model="soundSwitch" class="custom_switch" inset dense :ripple="false" color="#fff" />
            </v-list-item-action>
          </v-list-item>
        </div>
        <v-divider />
        <div class="text-center account_menu_list_footer">
          {{ VERSION }}
        </div>
      </v-list>
    </v-menu>
    <v-btn
      v-if="!isApp"
      v-zoom
      class="mail_btn btn_radius t_btn_color_1"
      :height="commonStyle('top_navigation_btn_height')"
      :width="commonStyle('account_menu_mail_btn_width')"
      :min-width="commonStyle('account_menu_mail_btn_width')"
      outlined
      :ripple="false"
      plain
      @click="onRightDrawer"
    >
      <v-badge overlap dot color="t_red_drop" class="d-flex align-center" :value="inboxEntryDot.value">
        <icon-font name="icon-Icon_InBox" :size="commonStyle('account_menu_btn_size')" />
      </v-badge>
    </v-btn>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import BankAccount from './bank-account'
import { loadWebsocket } from '@/server/ws.js'
import ROUTES from '@/router/routes'
import _common from '@style/components/game-container.top-navigation.modules.scss'
import menuHead from '@/components/ui/menu-head'
import SiteLangHead from '@/components/ui/site-lang-head'
import { AJ_DOTTING } from '@/utils/dotting.js'
import { REQUEST_ENUMS } from '@/api/interface/enums.js'

export default {
  components: {
    BankAccount,
    menuHead,
    SiteLangHead
  },
  data() {
    return {
      accountMenu: false, // 下拉列表显示
      VERSION: window.VUE_APP_VERSION,
      soundSwitch: false
    }
  },
  computed: {
    ...mapState('source', ['activityInvite']),
    ...mapState('layout', ['containerBox']),
    ...mapState('modal', ['emailState', 'rightMenu']),
    ...mapState('base', ['isApp', 'user', 'volume', 'showRedDot', 'systemConfig']),
    ...mapGetters('base', ['inboxEntryDot', 'getDotEntry']),
    ...mapGetters('base', ['isLuckShow', 'isLossShow', 'isTaskShow', 'isBetShow', 'isVipSignShow']),
    menuList() {
      // APP 体育赛事入口开关 开启时关闭vip
      const sportsSwitch = this.systemConfig.ext_game_sport_show == 1
      const defaultList = [
        {
          name: 'UI_Carteira',
          value: '1',
          icon: 'icon-Icon_Wallet',
          textAlign: 'left'
        },
        {
          name: 'UI_referGift',
          value: '2',
          icon: 'icon-a-icon_Invitecourtesy',
          textAlign: 'left',
          hidden: !this.isApp || !this.isInviteShow
        },
        {
          name: 'UI_LoginAccount',
          value: '3',
          icon: 'icon-Icon_info',
          textAlign: 'left',
          hidden: this.isApp
        },
        {
          name: 'UI_Despositar',
          value: '4',
          icon: 'icon-Icon_Deposlt',
          textAlign: 'left'
        },
        {
          name: 'UI.cashout_0_main_1',
          value: '5',
          icon: 'icon-Icon_Withdraw',
          textAlign: 'left'
        },
        {
          name: 'UI_Bet_Bonus',
          value: '6',
          icon: 'icon-Icon_betting',
          showRedDot: 'clean_bet',
          textAlign: 'left',
          hidden: !(this.isApp && this.isBetShow)
        },
        {
          name: 'UI_Account_Loss_Refund',
          value: '7',
          icon: 'icon-a-icon_Lossreturn',
          showRedDot: 'red_dot_bet_loss_gift',
          textAlign: 'left',
          hidden: !(this.isLossShow && this.isApp)
        },
        {
          name: 'UI_Wheel',
          value: '8',
          icon: 'icon-turntable',
          showRedDot: 'red_dot_wheel_draw_num',
          textAlign: 'left',
          hidden: !(this.isLuckShow && this.isApp)
        },
        {
          name: 'UI_VIP_TAB.Vip',
          value: '9',
          icon: 'icon-Icon_vip',
          textAlign: 'left',
          showRedDot: 'vip_cash_gift',
          hidden: !this.isApp || !sportsSwitch
        },
        {
          name: 'UI_Menu_Left_Regression_Text',
          value: '10',
          icon: 'icon-icon_task',
          textAlign: 'left',
          showRedDot: 'back_task',
          hidden: !(this.isTaskShow && this.isApp)
        },
        // {
        // 	name: "UI.Redeem_Code",
        // 	value: "11",
        // 	icon: "icon-Code",
        // 	textAlign: "left",
        // },
        {
          name: 'UI.Vip_Login_Sign_In_Small',
          value: '12',
          icon: 'icon-icon_vip_checkin',
          textAlign: 'left',
          showRedDot: 'sign_reward',
          hidden: !(this.isApp && this.isVipSignShow.show)
        },
        {
          name: 'UI_Kefu_TAB.Kefu',
          value: '13',
          icon: 'icon-Icon_Service1',
          textAlign: 'left'
        },
        {
          name: 'UI_Sounds',
          value: '14',
          icon: 'icon-Icon_Sounds',
          textAlign: 'left'
        },
        {
          name: 'UI.Settings_0_main_6',
          value: '15',
          textAlign: 'center',
          icon: 'icon-Icon_Exit'
        }
      ]

      const currentList = defaultList
        .map(item => {
          const { name, value, textAlign, icon, hidden = false, showRedDot } = item
          return {
            value,
            textAlign,
            icon,
            hidden,
            name: this.$t(name),
            showRedDot: showRedDot ? this.getDotEntry(showRedDot) : { value: false }
          }
        })
        .filter(item => !item.hidden)
      return currentList
    },
    // 用户头像红点信息
    userEntryDot({ menuList }) {
      if (!menuList.length) return false
      const ret = menuList.some(item => {
        const { showRedDot = false } = item
        return showRedDot.value === true
      })
      return ret
    },
    // 邀请好友pc左侧栏显隐判断
    isInviteShow() {
      const { activity_info = {} } = this.activityInvite
      const { status = 0 } = activity_info
      const { invite_new_has_gift_switch = 0 } = this.systemConfig
      return status == 1 && invite_new_has_gift_switch == 1
    }
  },
  mounted() {
    this.soundSwitch = this.volume > 0
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    // 点击按钮回调显示
    showAccount() {
      this.$emit('show', 'deposit')
    },
    async setAction({ value }) {
      if (!['14'].includes(value)) this.accountMenu = false

      // 钱包
      if (value == 1) this.$router.push('/carteira')

      // pdd转盘
      if (value == 2) {
        const { activity_user_task = null } = this.activityInvite
        // activity_user_task  判断是否有任务吗?如果没有任务直接弹出弹窗，有进入界面
        if (!activity_user_task) {
          this.$pop.inviteGift().show()
        } else {
          this.$router.push({ name: 'referGift' })
        }
      }

      // 个人中心
      if (value == 3) {
        AJ_DOTTING.getAjdata('USER_DOWN_MINE')
        this.$router.push('/mine')
      }

      // 充值流程
      if (value == 4) this.$emit('show', 'deposit')

      // 提现流程
      if (value == 5) this.$emit('show', 'withdraw')

      // 洗码
      if (value == 6) this.$router.push({ name: 'washCode' })

      // 亏损返还
      if (value == 7) this.$router.push({ name: 'lossReturn' })

      // 幸运转盘
      if (value == 8) this.$pop.lucky().show()

      // vip
      if (value == 9) {
        AJ_DOTTING.getAjdata('USER_DOWN_VIP')
        this.$router.push('/recompensas')
      }

      // 回归任务
      if (value == 10) this.$router.push({ name: 'regression' })

      // 红包兑换
      if (value == 11) {
        AJ_DOTTING.getAjdata('USER_DOWN_GIFTCODE')
        this.$pop.redeem().show()
      }

      // vip签到任务
      if (value == 12) this.$pop.vipSign().show()

      // 客服
      if (value == 13) {
        AJ_DOTTING.getAjdata('USER_DOWN_SERVICE')
        this.$pop.kefu().show()
      }

      // 声音开关
      if (value == 14) {
        AJ_DOTTING.getAjdata('USER_DOWN_SOUND')
        this.$store.commit('base/SET_VOLUME', this.soundSwitch ? 100 : 0)
      }

      // 退出
      if (value == 15) {
        AJ_DOTTING.getAjdata('USER_DOWN_LOGOUT')

        this.$pop.loading().show()
        const res = await this.$store.dispatch('base/logout').catch(e => e)
        this.$pop.loading().hide()
        if (res.code == 0) {
          loadWebsocket(this)
          this.$store.dispatch('modal/setLoginRegistShow')
          this.$router.push(ROUTES[0].path)
        } else if (res.code != REQUEST_ENUMS.TOKENINVALID) {
          location.reload()
        }
      }

      // // 游戏设置
      // if (value == 6) this.$router.push('/setting')
      // // 游戏历史
      // if (value == 5) this.$pop.message().show({ content: 'task is delayed!', timeout: 5000 })
      // // this.$router.push('/History');
      // // 公平、安全
      // if (value == 7) this.$router.push('/security')
      // // 邮箱
      // if (value == 9) {
      //   AJ_DOTTING.getAjdata('USER_DOWN_MAIL')
      //   this.$store.dispatch('modal/setEmailState', !this.emailState)
      // }
    },
    // 点击是否显示用户信息下拉列表
    showAccountMenu() {
      this.accountMenu = !this.accountMenu
    },
    // 点击邮箱显示右边抽屉
    onRightDrawer() {
      AJ_DOTTING.getAjdata('USER_DOWN_MAIL')
      this.$store.dispatch('modal/setRightMenu', !this.rightMenu)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.top-navigation.modules.scss';

.comp.user_menu {
  @include setBox($direction: row, $wrap: nowrap);
  gap: 0 8px;
  // overflow: hidden;
  .desposit_btn {
    padding: $account_menu_desposit_btn_padding;
    font-weight: $account_menu_btn_font_weight;
  }
  .prolife_btn {
    padding: $account_menu_prolife_btn_padding;
    border-color: $border_color !important;
    &::v-deep .v-btn__content {
      opacity: 1 !important;
    }
  }
  .mail_btn {
    padding: $account_menu_mail_btn_padding;
    border-color: $border_color !important;
    &::v-deep .v-btn__content {
      opacity: 1 !important;
    }
  }
}
.account_menu_list {
  border-radius: $account_menu_list_radius;
  padding: $account_menu_list_padding;
  background: $account_menu_list_background;
  .account_menu_list_content {
    width: 100%;
    max-height: 260px;
    overflow-y: auto;
    padding: $account_menu_list_content_padding;
    &::-webkit-scrollbar {
      display: block;
      width: 4px;
    }
    .list_item {
      min-height: $account_menu_list_item_height;
      .gray_light_color_text {
        color: $account_menu_list_item_text_color;
      }
      .list_item_text {
        font-size: $account_menu_list_item_text_font_size;
        font-weight: $account_menu_list_item_text_font_weight;
        margin: $account_menu_list_item_text_margin;
        color: $account_menu_list_item_text_color;
      }
      .custom_switch {
        width: 30px !important;
        height: 14px !important;
        &::v-deep {
          .v-input--switch__track,
          .v-input--selection-controls__input,
          input {
            width: 30px !important;
            height: 14px !important;
          }
          .v-input--switch__track {
            opacity: 1;
            background: $black_1;
            top: calc(50% - 7px) !important;
          }
          .v-input--switch__thumb {
            width: 10px;
            height: 10px;
            top: calc(50% - 5px) !important;
          }
          .v-input__slot {
            min-height: 14px !important;
          }
          &.v-input--is-label-active {
            .v-input--switch__thumb {
              transform: translate(15px, 0) !important;
            }
            .v-input--switch__track {
              background: $account_menu_list_track_color;
            }
          }
        }
      }
    }
  }
  .theme--light.v-divider {
    border-color: $account_menu_list_divider_color;
  }
  .account_menu_list_footer {
    padding: $account_menu_list_footer_padding;
    color: $account_menu_list_footer_color;
    font-size: $account_menu_list_footer_font_size;
  }
}
.v-list-item__action {
  margin: 0;
}
// 针对pc样式
@include breakpoint-only-up() {
  .account_menu_list {
    .account_menu_list_content {
      max-height: unset;
    }
  }
  .theme--light.v-list-item:hover {
    background: $account_menu_list_active_bg;
    .list_item_text,
    .icon {
      color: $primary_dark_color;
    }
  }
}
</style>
