<template>
  <div class="radio_group" :style="styFn">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'VRadioGroup',
  props: {
    value: {},
    styObj: {
      type: Object,
      default: () => {}
    }
  },
  provide() {
    return {
      radioGroup: this
    }
  },
  computed: {
    styFn() {
      return {
        ...this.styObj
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';
.radio_group {
  display: flex;
  align-items: center;
  gap: $radio_group_gap_app;
  justify-content: space-around;
  width: 100%;
}
// 针对pc样式
@include breakpoint-only-up() {
  .radio_group {
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: $radio_group_padding_pc;
  }
}
</style>
