<template>
  <div class="skeleton search" v-if="isApp">
    <v-skeleton-loader class="search_card" type="text" :style="{ ...styleOpt }"></v-skeleton-loader>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  props: {
    // 样式
    styleOpt: {
      type: Object,
      default() {
        return {}
      }
    },
    attrs: {
      type: Object,
      default() {
        return {
          boilerplate: true,
          elevation: 2
        }
      }
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp'])
  }
}
</script>

<style lang="scss" scope>
@import '@style/components/skeleton.scss';

.skeleton.search {
  .search_card {
    margin: $search_margin;
    width: $search_width;
    height: $search_height;
  }
}
</style>
