<template>
  <div class="menu_box">
    <!-- vip -->
    <v-row align="center" no-gutters class="menu_bonus_item">
      <v-col cols="12">
        <v-badge
          overlap
          dot
          color="t_red_drop"
          class="d-flex align-center"
          :value="(showRedDot && showRedDot.vip_cash_gift && showRedDot.vip_cash_gift.is_show) || 0"
        >
          <div
            class="menu_box_item cursor_point"
            :style="loadIcon('menu_bg_vip', true)"
            @click="changePage('recompensas', 'LEFT_VIP')"
          >
            <div class="menu_box_title">{{ $t('UI_Left_Vip_Bonus') }}</div>
          </div>
        </v-badge>
      </v-col>
    </v-row>
    <!-- 洗码 -->
    <template v-if="isBetShow">
      <v-row align="center" no-gutters class="menu_bonus_item">
        <v-badge
          overlap
          dot
          color="t_red_drop"
          class="d-flex align-center"
          :value="(showRedDot && showRedDot.clean_bet && showRedDot.clean_bet.is_show) || 0"
        >
          <div
            class="menu_box_item cursor_point"
            :style="loadIcon('menu_bg_bonus', true)"
            @click="changePage('washCode', 'LEFT_BET')"
          >
            <div class="menu_box_title">{{ $t('UI_Left_Bet_Bonus') }}</div>
          </div>
        </v-badge>
      </v-row>
    </template>
    <!-- 全民代 -->
    <v-row align="center" no-gutters class="menu_bonus_item">
      <v-col cols="12">
        <v-badge
          overlap
          dot
          color="t_red_drop"
          class="d-flex align-center"
          :value="(showRedDot && showRedDot.user_spread_way && showRedDot.user_spread_way.is_show) || 0"
        >
          <div
            class="menu_box_item cursor_point"
            :style="loadIcon('menu_bg_rerer', true)"
            @click="changePage('recomendar', 'LEFT_REFER')"
          >
            <div class="menu_box_title">{{ $t('UI_Left_Refer_Earn') }}</div>
          </div>
        </v-badge>
      </v-col>
    </v-row>
    <!-- 七日签到 -->
    <template v-if="sevenDaysShow">
      <v-row align="center" no-gutters class="menu_bonus_item">
        <v-col cols="12">
          <v-badge
            overlap
            dot
            color="t_red_drop"
            class="d-flex align-center"
            :value="
              (showRedDot && showRedDot.first_recharge_sign_dot && showRedDot.first_recharge_sign_dot.is_show) || 0
            "
          >
            <div
              class="menu_box_item cursor_point"
              :style="loadIcon('menu_bg_days', true)"
              @click="changePage('signIn')"
            >
              <div class="menu_box_title">{{ $t('Presente_7_day') }}</div>
            </div>
          </v-badge>
        </v-col>
      </v-row>
    </template>
    <!-- 转盘 -->
    <template v-if="isLuckShow">
      <v-row align="center" no-gutters class="menu_bonus_item">
        <v-col cols="12">
          <v-badge
            overlap
            dot
            color="t_red_drop"
            class="d-flex align-center"
            :value="(showRedDot && showRedDot.red_dot_wheel_draw_num && showRedDot.red_dot_wheel_draw_num.is_show) || 0"
          >
            <div
              class="menu_box_item cursor_point"
              :style="loadIcon('menu_bg_lucky', true)"
              @click="changePage('lucky', 'LEFT_LUCKY')"
            >
              <div class="menu_box_title">{{ $t('UI_Menu_Wheel') }}</div>
            </div>
          </v-badge>
        </v-col>
      </v-row>
    </template>
    <!-- 邀请活动 -->
    <template v-if="isInviteShow">
      <v-row align="center" no-gutters class="menu_bonus_item">
        <v-col cols="12">
          <v-badge overlap dot color="t_red_drop" class="d-flex align-center" :value="0">
            <div
              class="menu_box_item cursor_point"
              :style="loadIcon('menu_bg_invite', true)"
              @click="changePage('referGift')"
            >
              <div class="menu_box_title">{{ capitalizeFirstLetter($t('UI_referGift')) }}</div>
            </div>
          </v-badge>
        </v-col>
      </v-row>
    </template>
    <!-- 亏损返还 -->
    <template v-if="isLossShow">
      <v-row align="center" no-gutters class="menu_bonus_item">
        <v-badge
          overlap
          dot
          color="t_red_drop"
          class="d-flex align-center"
          :value="(showRedDot && showRedDot.red_dot_bet_loss_gift && showRedDot.red_dot_bet_loss_gift.is_show) || 0"
        >
          <div
            class="menu_box_item cursor_point"
            :style="loadIcon('menu_bg_loss', true)"
            @click="changePage('lossReturn', 'LEFT_LOSS')"
          >
            <div class="menu_box_title">{{ $t('UI_Left_Loss_Refund') }}</div>
          </div>
        </v-badge>
      </v-row>
    </template>
    <!-- 回归任务 -->
    <template v-if="isTaskShow">
      <v-row align="center" no-gutters class="menu_bonus_item">
        <v-badge
          overlap
          dot
          color="t_red_drop"
          class="d-flex align-center"
          :value="(showRedDot && showRedDot.back_task && showRedDot.back_task.is_show) || 0"
        >
          <div
            class="menu_box_item cursor_point"
            :style="loadIcon('menu_bg_task', true)"
            @click="changePage('regression', 'LEFT_REGRESSION')"
          >
            <div class="menu_box_title">{{ $t('UI_Menu_Left_Regression_Text') }}</div>
          </div>
        </v-badge>
      </v-row>
    </template>
    <!-- 回归任务-七日签到 -->
    <template v-if="isTaskSignShow">
      <v-row align="center" no-gutters class="menu_bonus_item">
        <v-badge
          overlap
          dot
          color="t_red_drop"
          class="d-flex align-center"
          :value="(showRedDot && showRedDot.weekcard_sign && showRedDot.weekcard_sign.is_show) || 0"
        >
          <div
            class="menu_box_item cursor_point"
            :style="loadIcon('menu_bg_task_sign', true)"
            @click="changePage('regressionSignIn', 'LEFT_REGRESSION_SIGN_IN')"
          >
            <div class="menu_box_title">{{ $t('UI_Menu_Left_Regression_SignIn_Text') }}</div>
          </div>
        </v-badge>
      </v-row>
    </template>
    <!-- vip签到 -->
    <template v-if="isVipSignShow.show">
      <v-row align="center" no-gutters class="menu_bonus_item">
        <v-badge
          overlap
          dot
          color="t_red_drop"
          class="d-flex align-center"
          :value="(showRedDot && showRedDot.sign_reward && showRedDot.sign_reward.is_show) || 0"
        >
          <div
            class="menu_box_item cursor_point"
            :style="loadIcon('menu_bg_vip_sign', true)"
            @click="changePage('vipSign', 'LEFT_VIP_SIGN')"
          >
            <div class="menu_box_title">{{ $t('UI.Vip_Login_Sign_In') }}</div>
          </div>
        </v-badge>
      </v-row>
    </template>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import variables from '@style/components/game-container.drawer-pannel.modules.scss'
import { capitalizeFirstLetter } from '@/utils/helper.js'
import { AJ_DOTTING } from '@/utils/dotting.js'

export default {
  data() {
    return {
      variables
    }
  },
  computed: {
    ...mapState('base', ['showRedDot', 'user', 'systemConfig']),
    ...mapState('source', ['activityInvite']),
    ...mapGetters('base', [
      'isLuckShow',
      'isLossShow',
      'isTaskShow',
      'isTaskSignShow',
      'isBetShow',
      'isVipSignShow',
      'isTourist'
    ]),
    // 普通七日签到显示
    sevenDaysShow() {
      const { first_recharge_sign_switch = 0 } = this.user
      return first_recharge_sign_switch == 1
    },
    // 邀请好友pc左侧栏显隐判断
    isInviteShow() {
      const { activity_info = {} } = this.activityInvite
      const { status = 0 } = activity_info
      const { invite_new_has_gift_switch = 0 } = this.systemConfig
      return status == 1 && invite_new_has_gift_switch == 1
    }
  },
  methods: {
    capitalizeFirstLetter,
    loadIcon: (key, bool = false) => loadIcon(key, bool),
    // 跳转路由
    changePage(name = '', aj = '') {
      if (!name) return
      if (aj) AJ_DOTTING.getAjdata(aj)

      switch (name) {
        // 幸运转盘
        case 'lucky':
          this.$pop.lucky().show()
          break
        // 邀请好友活动pdd
        case 'referGift':
          const { activity_user_task = null } = this.activityInvite
          // activity_user_task  判断是否有任务吗?如果没有任务直接弹出弹窗，有进入界面
          if (!activity_user_task) {
            this.$pop.inviteGift().show()
          } else {
            this.$router.push({ name })
          }
          break
        // vip签到
        case 'vipSign':
          if (this.isTourist) {
            this.$store.dispatch('modal/setLoginRegistShow')
            return
          }
          this.$pop.vipSign().show()
          break
        default:
          this.$router.push({ name })
          break
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.drawer-pannel.modules.scss';

.menu_bonus_item {
  & + .menu_bonus_item {
    margin: $menu_bonus_item_margin;
  }
}

.menu_box {
  padding: $menu_bonus_padding;

  .menu_box_item {
    position: relative;
    background-size: 100% 100%;
    width: $menu_bonus_item_width;
    height: $menu_bonus_item_height;
    border-radius: $menu_bonus_item_radius;
    @include setBox(row, flex-start, center);
    &:hover {
      transform: $menu_bonus_item_hover_transform;
    }

    .menu_box_title {
      font-size: $menu_bonus_item_title_font_size;
      font-weight: $menu_bonus_item_title_font_weight;
      margin: $menu_bonus_item_title_font_margin;
      width: $menu_bonus_item_title_font_width;
      text-transform: uppercase;
    }
  }
}
</style>
