<template>
  <div class="comp kefu_inlet" v-zoom @click="onJump">
    <v-badge overlap dot color="t_red_drop" class="d-flex align-center" :value="redDot">
      <img class="kefu_icon" :src="loadIcon('float_kefu')" alt="" />
    </v-badge>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { AJ_DOTTING } from '@/utils/dotting.js'
import loadIcon from '@/assets/icons'

export default {
  props: {
    icon: {
      type: String,
      default: 'icon-Icon_Service2'
    },
    iconSize: {
      type: [String, Number],
      default: 24
    },
    color: {
      type: String,
      default: 't_white'
    }
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['systemConfig', 'KfNoticeData']),
    // 红点
    redDot() {
      // ai_chat_robot_switch 0:旧版 1:智能客服 2:工单客服 3：AI客服
      const { ai_chat_robot_switch = 0 } = this.systemConfig
      // 工单红点
      if (ai_chat_robot_switch == 2) {
        const { processed = 0, processing = 0, waiting = 0 } = this.KfNoticeData
        return processed + processing + waiting > 0 ? true : false
      }

      return false
    }
  },
  methods: {
    loadIcon: (k = '', bool = false) => loadIcon(k, bool),
    onJump() {
      this.$pop.kefu().show()
      AJ_DOTTING.getAjdata('OTHER_BTN_FLOAT_SERVICE')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.comp.kefu_inlet {
  z-index: $menu_right_float_zindex;
  .kefu_icon {
    width: $kefu_btn_width;
    height: $kefu_btn_height;
    border-radius: 50%;
    outline: none;
    user-select: none;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
