<template>
  <div :class="['my-checkbox', { disabled: disabled }]" @click="toggle">
    <icon-font
      :name="!isChecked ? offIcon : onIcon"
      v-bind="$attrs"
      :dark="isChecked ? 't_primary_dark_color' : 't_text_color_2'"
      class="flex-shrink-0"
      :size="iconSize"
    />
    <slot></slot>
  </div>
</template>

<script>
/**
 * radioBox 单选选框
 * @property {String Number} size 图标大小，单位px（默认16）
 * @property {String} offIcon 不活动时使用的图标
 * @property {String} onIcon 激活时使用的图标
 * @property {Boolean} disabled 是否禁用
 * @example <radio-box v-model="checked" disabled>哈哈哈哈哈</radio-box>
 */
export default {
  props: {
    // 不活动时使用的图标
    offIcon: {
      type: String,
      default: 'icon-weixuanzhong'
    },
    // 激活时使用的图标
    onIcon: {
      type: String,
      default: 'icon-xuanzhong'
    },
    // 是否为选中状态
    value: {},
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    iconSize: {
      type: [String, Number],
      default: 24
    }
  },
  inject: ['radioGroup'],
  data() {
    return {
      isChecked: this.value === this.radioGroup.value
    }
  },
  watch: {
    isChecked(newVal) {
      if (newVal) {
        this.radioGroup.$emit('input', this.value)
      }
    },
    'radioGroup.value'(newVal) {
      this.isChecked = this.value === newVal
    }
  },
  methods: {
    toggle() {
      if (!this.disabled) {
        this.isChecked = true
      }
    }
  }
}
</script>

<style>
.my-checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.my-checkbox.disabled {
  opacity: 0.3;
  cursor: not-allowed;
}
</style>
