<template>
  <div class="face_modular_box">
    <custom-face ref="customFaceRef" />
    <aws-face ref="awsFaceRef" />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import CustomFace from './custom-face.vue'
import AwsFace from './aws-face.vue'

export default {
  components: {
    CustomFace,
    AwsFace
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    // 是否启用aws人脸识别
    isAwsFace() {
      return true
    },
    // 人脸识别绑定状态 0未认证 1已认证
    isFaceStatus() {
      const { rekognition_status = 0 } = this.user

      return rekognition_status == 1
    }
  },
  methods: {
    /**
     * 判断是否壳包
     * 判断壳包版本 < 3 不支持
     * 检查相机权限
     * 请求相机许可
     */
    isJurisdiction() {
      return new Promise((resolve, reject) => {
        // 是否壳包
        console.log('is_shell...', this.$u.isNativeClient())
        if (this.$u.isNativeClient()) {
          // 获取版本信息 小于3版本的不支持
          const version = this.$u.callJava('getApkVersion')
          console.log('shell_version...', version)
          if (version < 3) {
            this.$pop.message().show({ content: this.$t('UI_Human_Face_Version_Text') })
            return reject(false)
          }

          // 检查相机权限 0:关 1:开
          let isCamera = this.$u.callJava('checkCameraPermission')
          isCamera = !!Number(isCamera)
          console.log('shell_jurisdiction...', isCamera)
          if (isCamera) return resolve(true)

          // 请求相机许可
          this.$u.callJava('requestCameraPermission')
          this.$u.callScriptMethod('onRequestPermissionsResult', (val = {}) => {
            const { requestCode = 0, result = -1 } = val
            console.log('camera_license...', val)
            if (result == 0) {
              if (requestCode == 100) {
                resolve(true)
              } else {
                this.$pop.message().show({ content: this.$t('UI_Human_Face_Not_Text') })
                console.log(`onRequestPermissionsResult requestCode:${requestCode} result:${result}`)
                reject(false)
              }
            } else {
              this.$pop.message().show({ content: this.$t('UI_Human_Face_Not_Text') })
              reject(false)
            }
          })
        } else {
          resolve(true)
        }
      })
    },
    /**
     * 打开
     * @param {*} compare_type 1=绑定，2=解绑，3=校验人脸
     * @param {*} rekognition_code 用户临时凭证
     */
    show({ compare_type = 1, rekognition_code = '' } = {}) {
      return new Promise(async (resolve, reject) => {
        const isJu = await this.isJurisdiction().catch(e => e)
        if (!isJu) return reject('cancel')

        this.$nextTick(async () => {
          // 是否启用aws人脸识别 ? aws人脸识别 : 自定义人脸识别;
          const elRef = this.isAwsFace ? this.$refs['awsFaceRef'] : this.$refs['customFaceRef']
          if (!elRef) return reject('cancel')

          // res ==> { code: 1:浏览器不支持 2:手动关闭 3:找不到设备 4:失败 5:成功 }
          const res = await elRef.open({ compare_type, rekognition_code }).catch(e => e)
          console.log('res', res)
          switch (res.code) {
            case 1:
              this.$pop.message().show({ content: this.$t('UI.Face_Browser_Supported') })
              reject('cancel')
              break
            case 3:
              this.$pop.message().show({ content: this.$t('UI.Face_Equipment_Error') })
              reject('cancel')
              break
            case 4:
              this.$pop.message().show({ content: res.msg ? res.msg : this.$t('UI.Face_Error') })
              reject('cancel')
              break
            case 5:
              this.$pop.message().show({ content: this.$t('UI.Face_Success') })
              resolve('ok')
              break
            default:
              reject('cancel')
              break
          }
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped></style>
