<template>
  <div :style="profile" class="img_pro">
    <div class="" :style="bgPosition(cur || sid, size)"></div>
    <div v-show="activeShowFun(cur || sid)" class="img_pro_active" :style="activeImg">
      <div class="img_pro_active_icon">
        <icon-font name="icon-icon_account_xuanzhong" :size="commonStyle('icon_checked_size')" />
      </div>
    </div>
  </div>
</template>

<script>
import userSprite, { SPRITE_WIDTH, SPRITE_HEIGHT, SPRITE_ICON_WIDTH, SPRITE_ICON_HEIGHT } from './userSprite.js'
import loadIcon from '@/assets/icons'
import _common from '@style/components/ui.modules.scss'

export default {
  props: {
    value: {
      type: [String, Number],
      required: true
    },
    size: {
      type: [String, Number],
      default: 50
    },
    cur: {
      type: [String, Number]
      // required: true
    },
    isActive: {
      type: Boolean,
      default: false
    },
    bordrBorder: {
      type: [String],
      default: _common.default_border_color
    },
    borderRadius: {
      type: [String],
      default: '50%'
    }
  },
  data() {
    return {}
  },
  computed: {
    userSprite() {
      return userSprite
    },
    sid() {
      return Number(this.value || 1)
    },
    profile() {
      return {
        width: `${this.size}px`,
        height: `${this.size}px`
      }
    },
    activeImg() {
      const activeSize = parseInt(this.size) + 2
      return {
        width: `${activeSize}px`,
        height: `${activeSize}px`,
        border: `2px solid ${this.bordrBorder}`,
        borderRadius: this.borderRadius
      }
    }
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    loadIcon: key => loadIcon(key, false),
    activeShowFun(sid) {
      if (this.sid == sid && this.isActive) return true
      return false
    },
    bgPosition(sid, iconWidth = 146, iconHeight = iconWidth) {
      if (`${sid}`.indexOf('http') == -1) {
        let spriteArr = []
        if (this.sid && sid && this.userSprite[sid]) {
          spriteArr = this.sid == sid ? this.userSprite[sid].active : this.userSprite[sid].default
        }
        const [spriteIconX = 0, spriteIconY = 0] = spriteArr
        return {
          width: `${iconWidth}px`,
          height: `${iconHeight}px`,
          background: `url(${loadIcon('user_sprite', false)}) no-repeat`,
          borderRadius: this.borderRadius,
          'background-position': `${(spriteIconX / (SPRITE_WIDTH - SPRITE_ICON_WIDTH)) * 100}% ${
            (spriteIconY / (SPRITE_HEIGHT - SPRITE_ICON_HEIGHT)) * 100
          }%`,
          'background-size': `${SPRITE_WIDTH * (iconWidth / SPRITE_ICON_WIDTH)}px  ${SPRITE_HEIGHT * (iconHeight / SPRITE_ICON_HEIGHT)}px`
        }
      } else {
        return {
          width: `${iconWidth}px`,
          height: `${iconHeight}px`,
          background: `url('${this.sid}') no-repeat`,
          'background-size': `100% 100%`,
          'border-radius': this.borderRadius
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.img_pro {
  position: relative;
  cursor: pointer;
  .img_pro_active {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .img_pro_active_icon {
      display: flex;
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }
}
</style>
