<template>
  <custom-dialog v-model="$visible">
    <div class="comp_recaptcha_box">
      <div class="recaptcha_back">
        <div class="recaptcha_back_title">{{ titleText }}</div>
        <div class="close_right cursor_point" @click="onBack">
          <icon-font name="icon-Icon_Cloose" size="18" dark="t_text_color_2" />
        </div>
      </div>
      <div class="recaptcha_content">
        {{ reCaptchaSiteKey ? contentText : $t('UI.Login_Regist_Google') }}
      </div>
      <div class="recaptcha_box">
        <vue-recaptcha v-if="reCaptchaSiteKey" ref="recaptcha" @verify="onCaptchaVerify" :sitekey="reCaptchaSiteKey" />
      </div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { VueRecaptcha } from 'vue-recaptcha'
import { VUE_APP_RECAPTCHA_API } from '@/api/setting.js'

export default {
  components: {
    VueRecaptcha
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    titleText: {
      // 标题
      type: String,
      default: ''
    },
    contentText: {
      // 内容标签
      type: String,
      default: ''
    }
  },
  data() {
    return {
      reCaptchaSiteKey: ''
    }
  },
  created() {
    this.reCaptchaSiteKey = VUE_APP_RECAPTCHA_API
  },
  computed: {
    ...mapState('base', ['isApp']),
    $visible: {
      get() {
        return this.value
      },
      set(val) {
        this.$emit('input', val)
      }
    }
  },
  methods: {
    // 谷歌验证成功回调
    onCaptchaVerify(response) {
      this.$emit('onSuccess', response)
    },
    // 关闭窗口回调
    onBack() {
      this.$emit('onBack')
    }
  }
}
</script>
<style scoped lang="scss">
@import '@style/components/ui.modules.scss';

.comp_recaptcha_box {
  width: 100%;
  .recaptcha_back {
    height: $recaptcha_back_height;
    width: $recaptcha_back_width;
    position: relative;
    background: $recaptcha_back_bg;
    .recaptcha_back_title {
      height: $recaptcha_back_title_height;
      text-align: $recaptcha_back_title_text_align;
      @include setBox(row, center, center);
      color: $recaptcha_back_title_text_color;
      font-size: $recaptcha_back_title_text_font_size;
      font-weight: $recaptcha_back_title_text_font_weight;
    }
    .close_right {
      position: absolute;
      right: $recaptcha_close_right;
      top: $recaptcha_close_top;
    }
  }
  .recaptcha_content {
    background: $recaptcha_content_bg;
    color: $recaptcha_content_color;
    padding: $recaptcha_content_padding;
    font-size: $recaptcha_content_font_size;
    font-weight: $recaptcha_content_font_weight;
  }
  .recaptcha_box {
    background: $recaptcha_box_bg;
    padding: $recaptcha_box_padding;
    width: 100%;
    @include setBox(row, center, center);
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .comp_recaptcha_box {
    width: 360px;
  }
}
</style>
