<template>
  <v-app class="m_layout">
    <!-- 顶部导航栏 -->
    <top-navigation v-show="!isContentFullScreen" @touchmove="e => e.preventDefault()" />
    <!-- 左侧菜单抽屉弹窗 -->
    <component :is="drawerLeftName" v-model="drawerLeft" />
    <!-- 右侧菜单抽屉弹窗 -->
    <component :is="drawerRightName" v-model="drawerRight" />
    <!-- 邮箱弹窗 -->
    <component :is="emailModalName" />
    <!-- 登录注册弹窗 -->
    <custom-dialog v-model="loginRegist" :full="true">
      <component :is="loginModalName" />
    </custom-dialog>
    <!-- 充值提现弹窗 -->
    <deposit-withdraw />
    <!-- 内容区域 -->
    <div class="m_content_all">
      <!-- 游戏内容区域 -->
      <v-main class="theme--dark v-application m_content">
        <slot></slot>
      </v-main>
      <!-- 尾页 -->
      <v-main class="theme--dark v-application m_content_footer">
        <template v-if="isFooter">
          <my-footer />
        </template>
      </v-main>
    </div>
    <!-- APP底部导航栏 -->
    <bottom-navigation v-if="!isContentFullScreen && isApp" @touchmove="e => e.preventDefault()" />
    <!-- 悬浮按钮 -->
    <float-btn v-show="!drawerRight && $route.name == 'menu'" />
    <!-- 处理字体文件的加载 -->
    <div style="transform: scale(0.4); position: absolute; bottom: 0px">
      <span class="font-game" style="color: #181b2a; font-size: 14px">M</span>
    </div>
    <!-- 音频播放 -->
    <audio id="bgAudio" controls style="display: none" :muted="true">
      <source src="@source/music/sendcard.mp3" type="audio/mp3" />
    </audio>
  </v-app>
</template>

<script>
import Vue from 'vue'
import { mapState, mapGetters } from 'vuex'
import { SYSTEEM_ERROR_PAGEE } from '@/api/interface/enums.js'
import myFooter from '@/components/ui/my-footer'
import DialogManager from '@/utils/dialog/manager.js'
import FloatBtn from '@/components/ui/float-btn'
import TopNavigation from './top-navigation'
import BottomNavigation from './bottom-navigation'
import DepositWithdraw from '@/components/game-container/deposit-withdraw'

export default {
  components: {
    myFooter,
    FloatBtn,
    TopNavigation,
    BottomNavigation,
    DepositWithdraw
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'volume', 'languageType', 'systemConfig', 'user', 'showRedDot']),
    ...mapState('layout', ['topNavigationHeight', 'emailState']),
    ...mapState('modal', ['leftMenu', 'rightMenu', 'loginRegistShow']),
    ...mapGetters('base', ['getDotEntry', 'isTourist']),
    ...mapState('responsible', ['remindDialogShow']),
    ...mapState('stepLoader', ['drawerLeftName', 'drawerRightName', 'loginModalName', 'emailModalName']),
    ...mapState('source', ['isToGame']),
    ...mapState('game', ['openGameMode']),
    // 是否全屏
    isContentFullScreen() {
      const { meta = {} } = this.$route
      const { full = false } = meta
      return full
    },
    // 是否需要页尾
    isFooter() {
      const { meta = {} } = this.$route
      const { footer = false } = meta
      return footer
    },
    // 左侧抽屉
    drawerLeft: {
      get() {
        return this.leftMenu
      },
      set(bool = false) {
        this.$store.dispatch('modal/setLeftMenu', bool)
      }
    },
    // 右侧抽屉
    drawerRight: {
      get() {
        return this.rightMenu
      },
      set(bool = false) {
        this.$store.dispatch('modal/setRightMenu', bool)
      }
    },
    // 登录注册弹窗
    loginRegist: {
      get() {
        return this.loginRegistShow
      },
      set(bool = false) {
        this.$store.commit('modal/SET_LOGIN_REGIST_SHOW', bool)
      }
    }
  },
  watch: {
    // 监听是否是 app 移动端
    '$vuetify.breakpoint': {
      handler({ name: isAPPName }) {
        const is_app = ['xs'].includes(isAPPName)
        this.$store.commit('base/SET_IS_APP', is_app)
      },
      deep: true,
      immediate: true
    },
    // 监听是否是移动端
    isApp: {
      handler(n = false) {
        this.loadComp()
      },
      immediate: true
    },
    // 监听音量
    volume: {
      handler(cur) {
        setTimeout(() => {
          const bgAudio = document.getElementById('bgAudio')
          if (bgAudio) bgAudio.volume = cur / 100
        }, 300)
      },
      immediate: true
    },
    // 是否是游客
    isTourist(n) {
      if (n) this.drawerRight = false
    },
    // 监听防沉迷弹窗弹出
    remindDialogShow(n) {
      // 防沉迷状态不存在 || 正在游戏中
      if (!n || this.isToGame) return

      this.getReminRenewDialog()
    },
    // 监听是否在游戏中
    isToGame(n) {
      // 正在游戏中 || 防沉迷状态不存在
      if (n || !this.remindDialogShow) return

      this.getReminRenewDialog()
    },
    // 监听语言切换，获取服务端配置的多语言
    languageType() {
      this.$store.dispatch('common/getLanguageInfo')
    }
  },
  created() {
    // 初始化弹窗序列
    DialogManager.init(this)
  },
  mounted() {
    window.addEventListener('click', () => {
      const bgAudio = document.getElementById('bgAudio')
      bgAudio && bgAudio.play()
    })

    // 监听/卸载当前窗口切换状态变化
    document.removeEventListener('visibilitychange', this.windowVisibility)
    document.addEventListener('visibilitychange', this.windowVisibility)
  },
  methods: {
    // 加载组件
    loadComp() {
      // 左右侧栏/站内信弹窗 是否加载
      const { path = '' } = this.$route
      const errorPages = Object.values(SYSTEEM_ERROR_PAGEE)
      const isLoad = errorPages.includes(path)
      if (isLoad) return

      if (this.isApp) {
        // 加载的站内信弹窗
        if (!this.emailModalName) {
          import('@/components/email-modal/index.vue').then(result => {
            Vue.component('email-modal', result.default)
            this.$store.commit('stepLoader/SET_EMAIL_MODAL_NAME', 'email-modal')
          })
        }
      } else {
        // 加载左侧栏
        if (!this.drawerLeftName) {
          import('@/components/game-container/drawer-left').then(result => {
            Vue.component('drawer-left', result.default)
            this.$store.commit('stepLoader/SET_DRAWER_LEFT_NAME', 'drawer-left')

            this.drawerLeft = true
          })
        }
        // 加载右侧栏
        if (!this.drawerRightName) {
          import('@/components/game-container/drawer-right').then(result => {
            Vue.component('drawer-right', result.default)
            this.$store.commit('stepLoader/SET_DRAWER_RIGHT_NAME', 'drawer-right')

            this.drawerRight = false
          })
        }
      }
    },
    // 更新提醒功能倒计时本地缓存数据
    getReminRenew() {
      this.$store.commit('responsible/SET_REMIND_DIALOG_SHOW', false)

      this.$store.dispatch('responsible/renewRemindStorage', {
        sign: 'dialog',
        data: this.user,
        callback: () => {
          // 重新开始时间倒计时
          this.$store.dispatch('responsible/startCountDown')
        }
      })
    },
    // 弹出防沉迷弹窗
    getReminRenewDialog() {
      this.$pop
        .remindAlarm({
          setting: () => {
            this.$pop
              .responRemind({
                cancel: () => {
                  this.getReminRenew()
                },
                submit: () => {
                  this.getReminRenew()
                }
              })
              .show()
          },
          submit: () => {
            this.getReminRenew()
          }
        })
        .show()
    },
    // 监听当前窗口切换状态变化
    windowVisibility() {
      /**
       * 回到当前窗口
       * 在游戏中 && 诺亚游戏/9w/wickets9 ==> 就执行游戏下分操作
       * openGameMode 进入游戏的模式 0:默认（iframe内嵌） 1:诺亚游戏/9w/wickets9（打开新窗口）
       */
      if (document.visibilityState != 'hidden' && this.isToGame && this.openGameMode == 1) {
        this.$store.dispatch('game/getGemeDown')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.modules.scss';

.m_layout {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  font-family: $font-family-base;
  .m_content_all {
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow-y: auto;
    scrollbar-width: none;
    /* firefox */
    -ms-overflow-style: none;
    /* IE 10+ */
    .m_content {
      width: 100%;
      padding-bottom: $game_box_m_content_padding_b;
      &_skeleton {
        &_app {
          padding: $game_box_m_content_skeleton_app_padding;
        }
        &_pc {
          padding: $game_box_m_content_skeleton_pc_padding;
        }
      }
    }
    .m_content_footer {
      padding-top: 0 !important;
      width: 100%;
      background: $game_box_footer_background;
    }
  }
}

::v-deep {
  .v-navigation-drawer__border {
    background-color: $v_navigation_drawer_border_color !important;
  }
}
</style>
