<template>
  <div class="comp my_footer">
    <!-- app -->
    <div v-if="isApp" class="footer_box_app">
      <!-- 代理推广 -->
      <template v-if="agencyPromotion.switch">
        <div class="promotion_box" @click="onPromotion">
          <div>{{ agencyPromotion.title || '' }}</div>
          <icon-font
            name="icon-Icon_Right"
            :size="styleColor('footer_promotion_box_icon_app_size')"
            :dark="styleColor('footer_promotion_box_icon_app_color')"
          />
        </div>
      </template>
      <!-- logo + 语言选择框 -->
      <div class="logo_select">
        <img class="logo" :src="loadIcon('logo_lg')" alt="" />
        <!-- 语言选择框 -->
        <vue-select
          class="language_select"
          id="languageSelectId"
          :options="languageData"
          :value="languageType"
          @change="onLangChange"
        />
      </div>
      <!-- 合作伙伴图标 -->
      <template v-if="partners.iconArr.length > 0">
        <div class="partner_box">
          <img
            class="partner_img"
            :src="item.img_url"
            alt=""
            v-for="item in partners.iconArr"
            :key="item.name"
            @click="onPicClick(item)"
          />
        </div>
      </template>
      <!-- 分割线 -->
      <div class="divider_one"></div>
      <!-- helper说明列表 -->
      <template v-if="furyList.length > 0">
        <div class="helpList">
          <span class="help_title">
            {{ $i18n.t('UI.Settings_0_main_5') }}
          </span>
          <div class="help_text">
            <span v-for="item in furyList" :key="item.id" @click="onHelpInfo(item)">
              {{ item.title }}
            </span>
            <!-- 防沉迷文本 -->
            <template v-if="responsibleSwitch == 1">
              <span @click="$router.push({ name: 'Responsible' })">
                {{ $t('UI.Responsible_Helper') }}
              </span>
            </template>
          </div>
        </div>
      </template>
      <!-- Office_Channel 官方图标 -->
      <div class="channel_box">
        <div class="channel_title">{{ channelText }}</div>
        <div class="channel_list">
          <img
            class="channel_img"
            v-for="(item, i) in outLinks"
            :key="i"
            :src="item.icon_url"
            @click="onExternal(item)"
          />
        </div>
      </div>
      <!-- 分割线 -->
      <div class="divider_two"></div>
      <!-- 商家图标 -->
      <template v-if="partners.imgArr.length > 0">
        <div class="partners_business">
          <img
            class="item_img"
            v-for="item in partners.imgArr"
            :key="item.img_url"
            :src="item.img_url"
            alt=""
            @click="onPicClick(item)"
          />
        </div>
      </template>
      <!-- 分割线 -->
      <div class="divider_three"></div>
      <!-- 条款 -->
      <div class="term_box">
        <div class="term_text break_word">{{ footerText }}</div>
      </div>
      <div class="divider_four"></div>
      <div class="term_list">
        <img class="term_img" v-for="pic in footPics" :key="pic.img_url" :src="pic.img_url" @click="onPicClick(pic)" />
      </div>
    </div>
    <!-- pc -->
    <v-container v-else class="footer_box_pc">
      <div class="footer_start">
        <div class="lt">
          <!-- 代理推广 -->
          <template v-if="agencyPromotion.switch">
            <div class="promotion_box" @click="onPromotion">
              <div>{{ agencyPromotion.title || '' }}</div>
              <icon-font
                name="icon-Icon_Right"
                :size="styleColor('footer_promotion_box_icon_pc_size')"
                :dark="styleColor('footer_promotion_box_icon_pc_color')"
              />
            </div>
          </template>
          <!-- logo + 语言选择框 -->
          <div class="logo_select">
            <img class="logo" :src="loadIcon('logo_lg')" alt="" />
            <!-- 语言选择框 -->
            <vue-select
              class="language_select"
              id="languageSelectId"
              :options="languageData"
              :value="languageType"
              @change="onLangChange"
            />
          </div>
          <!-- 合作伙伴图标 -->
          <template v-if="partners.iconArr.length > 0">
            <div class="partner_box">
              <img
                class="partner_img"
                :src="item.img_url"
                alt=""
                v-for="item in partners.iconArr"
                :key="item.name"
                @click="onPicClick(item)"
              />
            </div>
          </template>
        </div>
        <div class="rt">
          <!-- helper说明列表 -->
          <template v-if="furyList.length > 0">
            <div class="helpList">
              <span class="help_title">
                {{ $i18n.t('UI.Settings_0_main_5') }}
              </span>
              <div class="help_text">
                <span v-for="item in furyList" :key="item.id" @click="onHelpInfo(item)">
                  {{ item.title }}
                </span>
                <!-- 防沉迷文本 -->
                <template v-if="responsibleSwitch == 1">
                  <span @click="$router.push({ name: 'Responsible' })">
                    {{ $t('UI.Responsible_Helper') }}
                  </span>
                </template>
              </div>
            </div>
          </template>
          <!-- Office_Channel 官方图标 -->
          <div class="channel_box">
            <div class="channel_title">{{ channelText }}</div>
            <div class="channel_list">
              <img
                class="channel_img"
                v-for="(item, i) in outLinks"
                :key="i"
                :src="item.icon_url"
                @click="onExternal(item)"
              />
            </div>
          </div>
        </div>
      </div>
      <!-- 分割线 -->
      <div class="divider_one"></div>
      <!-- 商家图标 -->
      <template v-if="partners.imgArr.length > 0">
        <div class="partners_business">
          <img
            class="item_img"
            v-for="item in partners.imgArr"
            :key="item.img_url"
            :src="item.img_url"
            alt=""
            @click="onPicClick(item)"
          />
        </div>
      </template>
      <!-- 分割线 -->
      <div class="divider_two"></div>
      <!-- 条款 -->
      <div class="term_box">
        <div class="term_text break_word">{{ footerText }}</div>
        <div class="term_list">
          <img
            class="term_img"
            v-for="pic in footPics"
            :key="pic.img_url"
            :src="pic.img_url"
            @click="onPicClick(pic)"
          />
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import store from '@/store'
import { mapState } from 'vuex'
import { lodash } from '@/utils/helper.js'
import { jump } from '@/utils/linkMap'
import { getFuryList, getFooterPics } from '@/api/modules/config'
import loadIcon from '@/assets/icons'
import loadImg from '@/components/ui/loadImg'
import vueSelect from '@/components/ui/vue-select'
import variables from '@style/components/ui.modules.scss'
import { AJ_DOTTING } from '@/utils/dotting.js'

export default {
  components: {
    loadImg,
    vueSelect
  },
  data() {
    return {
      furyList: [], // helper说明列表
      footPics: [] // 条款图片
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig', 'outLinks', 'langMoreInfo']),
    ...mapState('source', ['languageList', 'gameInfo', 'gameType', 'otherFactory', 'activity']),
    ...mapState('layout', ['containerBox']),
    ...mapState('responsible', ['responsibleSwitch']),
    // 语言选择列表
    languageData() {
      const list = this.languageList || []
      return list.map(v => {
        return {
          ...v,
          url: v.icon,
          label: v.name,
          value: v.lang
        }
      })
    },
    // 代理推广数据
    agencyPromotion() {
      const { agency_promotion = {} } = this.activity || {}
      return agency_promotion
    },
    // Office_Channel 标题文本
    channelText() {
      const item = this.langMoreInfo.find(f => f.name == 'web_cfg_title')
      // if (!item) return this.$t('UI_Office_Channel')

      return item?.value || ''
    },
    // 条款文本
    footerText() {
      const item = this.langMoreInfo.find(f => f.name == 'footer_notice_text')
      // if (!item) return this.$t('UI_BOTTOM_TXT')

      return item?.value || ''
    },
    /**
     * iconArr 合作伙伴图标
     * imgArr 商家图标
     */
    partners() {
      const { game_partners = {} } = this.systemConfig || {}
      let iconArr = game_partners[1] || []
      let imgArr = game_partners[2] || []

      return {
        iconArr,
        imgArr
      }
    },
    // 内部游戏
    externalList() {
      // 获取不重复类型的游戏
      const game_type_list = []
      const gameType = this.gameType || []
      // if (!this.gameType) this.gameType = [];
      gameType.forEach(item => {
        const list = item.game_list && lodash.isArray(item.game_list) ? item.game_list : []
        list.forEach(game => {
          const gameItem = game_type_list.find(f => f.gid == game.gid)
          if (!gameItem) game_type_list.push(game)
        })
      })

      // 获取与所有游戏信息相同 gid 的游戏
      const gameInfoList = this.gameInfo || []
      const list = gameInfoList
        .map(item => {
          if (item.game_type == 0) {
            const game = game_type_list.find(f => f.gid == item.gid)
            return game
          }
          return null
        })
        .filter(f => f)

      // 其他游戏处理
      const otherFactoryList = this.otherFactory || []
      const otherList = otherFactoryList.map(item => {
        item.game_name = item.name
        return item
      })
      return list.concat(otherList)
    },
    // 默认条款图片
    defaultPics() {
      return [
        {
          img_url: this.loadIcon('foot_siq'),
          is_jump: 0
        },
        {
          img_url: this.loadIcon('foot_gc'),
          is_jump: 1,
          jump_url: store?.state?.base?.systemConfig?.gc_url
        }
      ]
    }
  },
  watch: {
    //语言切换刷新数据
    languageType() {
      this.getHelperList()
    }
  },
  created() {
    this.getHelperList()
    this.getFooterPicsList()
  },
  methods: {
    loadIcon: k => (/^(http)/.test(k) ? k : loadIcon(k, false)),
    styleColor: k => variables[k],
    // 获取helper说明列表
    async getHelperList() {
      const res = await getFuryList({ language: this.languageType }).catch(e => e)
      if (res.code != 0) return

      const resData = lodash.isObject(res.data) ? res.data : {}
      const { index_list = [] } = resData
      // 添加 FAQ
      this.furyList = [...index_list]
      // 存入页尾说明
      this.$store.commit('source/SET_FURY_INFO', resData)
    },
    // 获取条款图片
    async getFooterPicsList() {
      const params = {
        token: this.user.token,
        language: this.languageType
      }
      const res = await getFooterPics(params).catch(e => e)
      if (res.code != 0) return

      const resData = res.data && Array.isArray(res.data) ? res.data : []
      this.footPics = resData.length > 0 ? resData : this.defaultPics
    },
    // 切换语言
    onLangChange(value = '') {
      AJ_DOTTING.getAjdata('LEFT_LANG')
      this.$store.commit('base/SET_LANGUAGE_TYPE', value)
      this.$store.commit('base/SET_DATA_VERSION', 0)
    },
    // Office_Channel 打开内链
    onExternal(item) {
      jump(item, this)
    },
    // 跳转服务信息页面
    onHelpInfo(item = {}) {
      const { name = '' } = item
      if (!name) return

      this.$router.push({ name: 'helpInfo', query: { name } })
    },
    // 代理推广跳转
    onPromotion() {
      this.$router.push({
        name: `activityDetail`,
        query: {
          act_id: 'agency_promotion'
        }
      })
    },
    // 合作伙伴，商家，条款，打开新页面
    onPicClick({ is_jump, jump_url }) {
      if (is_jump == 0) return
      if (jump_url) {
        // 兼容 移动端safari浏览器无法通过window.open在新标签打开链接
        setTimeout(() => window.open(jump_url))
      }
    }
  }
}
</script>

