<template>
  <div class="comp float_btn">
    <!-- 右侧悬浮 -->
    <div class="right_list_box">
      <!-- 活动入口 -->
      <template v-if="activitList.length > 0">
        <div class="right_list_activit" :class="{ bg: activitList.length > 2 }">
          <!-- 箭头 -->
          <template v-if="activitList.length > 2">
            <div class="right_list_activit_arrow" :class="{ down: activitShow }" @click="activitShow = !activitShow">
              <img :src="loadIcon('menu_float_arrow')" alt="" />
              <img :src="loadIcon('menu_float_arrow')" alt="" />
            </div>
          </template>
          <!-- 全部 -->
          <div
            class="right_list_item_all"
            :class="[!activitShow ? 'hide' : '', activitList.length <= 2 ? 'right_list_item_last' : '']"
          >
            <div
              class="right_list_item"
              :class="{
                fc_icon: item.key == 'fc_red_envelope',
                fg_icon: item.key == 'fg_red_envelope',
                fg_rotate_icon: item.key == 'fg_free_rotate',
                fg_ship_icon: item.key == 'fg_championship_activity',
                secondary_recharge_icon: item.key == 'secondary_recharge'
              }"
              v-for="item in activitList"
              :key="item.key"
            >
              <activit-item class="active_icon_box" :item="item" @click.native="onClickIcon(item)" />
            </div>
          </div>
        </div>
      </template>
      <!-- 其他入口 -->
      <div class="right_list_other">
        <div class="right_list_item" v-for="item in otherList" :key="item.sign">
          <!-- 回归任务-七日签到 -->
          <template v-if="item.sign == 'taskSign'">
            <div
              class="task_menu_sign"
              :class="{ menu_sign_btn: !isAndroidShow && !isIosShow }"
              @click="$router.push({ name: 'regressionSignIn' })"
            >
              <v-badge class="d-flex align-center" overlap dot right top color="t_red_drop" :value="taskSignInDot">
                <img class="sign_box" :src="otherLoadIcon('float_regression')" alt="" />
              </v-badge>
            </div>
          </template>
          <!-- 七日签到 -->
          <template v-if="item.sign == 'sevenDays'">
            <div
              class="menu_sign"
              :class="{ menu_sign_btn: !isAndroidShow && !isIosShow }"
              @click="$router.push({ name: 'signIn' })"
            >
              <v-badge class="d-flex align-center" overlap dot right top color="t_red_drop" :value="signDot">
                <img class="sign_box" :src="otherLoadIcon('float_sign')" alt="" />
              </v-badge>
            </div>
          </template>
          <template v-if="item.sign == 'download'">
            <!-- 下载 -- 安卓端 && 不是安卓壳包 -->
            <template v-if="isApp && isAndroidShow">
              <div id="fabDownloadAppId" class="android_download" @click="onDownload">
                <div class="android_download_box">
                  <img class="download_img" :src="otherLoadIcon('float_download')" alt="" />
                  <download-gold class="android_gold" />
                </div>
              </div>
            </template>
            <!-- 下载 -- ios && 不是ios桌面 -->
            <template v-if="isApp && isIosShow">
              <div id="fabDownloadAppId" class="ios_download" @click="onIosDownload">
                <div class="ios_download_box">
                  <img class="download_img" :src="otherLoadIcon('float_download')" alt="" />
                  <download-gold class="ios_gold" />
                </div>
              </div>
            </template>
          </template>
          <template v-if="item.sign == 'kefu'">
            <!-- 客服 -->
            <kefu-inlet v-if="!drawerLeft" class="menu_kefu" />
          </template>
          <!-- 自定义悬浮入口 -->
          <template v-if="item.sign === 'selfDefine'">
            <img :src="selfDefineIcon" class="self_define_icon" @click="onClickSelfDefineIcon" />
          </template>
        </div>
      </div>
    </div>
    <!-- 下载 -- pc端左侧 -->
    <template v-if="!isApp && drawerLeft">
      <div id="fabDownloadPcId" class="drawer_left" @click="onDownload">
        <icon-font
          name="icon-Icon_Download"
          :size="styleColor('drawer_left_icon_size')"
          :dark="styleColor('drawer_left_icon_color')"
        ></icon-font>
        <div class="drawer_left_title">{{ $t('UI_Invite_Download') }}</div>
        <div class="drawer_left_gold">
          <download-gold />
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { channelBurialPoint, AJ_DOTTING } from '@/utils/dotting.js'
import { postDownloadRecord } from '@/api/modules/distinguish.js'
import { hasLossLimit } from '@/utils/hasLossLimit'
import DownloadGold from '../download-gold'
import ActivitItem from './activit-item'
import kefuInlet from '../kefu-inlet'
import loadIcon from '@/assets/icons'
import _common from '@style/components/ui.modules.scss'

export default {
  components: {
    DownloadGold,
    ActivitItem,
    kefuInlet
  },
  props: {
    // 是否在左侧栏
    drawerLeft: {
      type: Boolean,
      default() {
        return false
      }
    }
  },
  data() {
    return {
      activitShow: false, // 活动是否全部显示
      fontShow: false // auto-font 字体显示
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig', 'user', 'languageType', 'showRedDot', 'gameActivityList']),
    ...mapGetters('base', ['isTourist', 'isTaskSignShow']),
    // 安卓端 && 不是安卓壳包 显示
    isAndroidShow() {
      // 安卓端
      const u = navigator.userAgent
      const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1
      // 不是安卓壳包
      const isAndroidStandalone = typeof android == 'undefined'

      return isAndroid && isAndroidStandalone
    },
    // ios && 不是ios桌面
    isIosShow() {
      const u = navigator.userAgent
      const isIos = !(u.indexOf('Android') > -1 || u.indexOf('Adr') > -1)
      const isIosStandalone = 'standalone' in window.navigator && window.navigator['standalone']

      return isIos && !isIosStandalone
    },

    // 七日签到显示
    sevenDaysShow() {
      const { first_recharge_sign_switch = 0 } = this.user
      return first_recharge_sign_switch == 1 && this.$route.name != 'signIn'
    },
    // 七日签到红点
    signDot() {
      const { first_recharge_sign_dot = {} } = this.showRedDot
      const { is_show = 0 } = first_recharge_sign_dot
      return !!Number(is_show)
    },
    // 回归任务-七日签到红点
    taskSignInDot() {
      const { weekcard_sign = {} } = this.showRedDot
      const { is_show = 0 } = weekcard_sign
      return !!Number(is_show)
    },
    // 活动入口列表
    activitList() {
      if (this.drawerLeft) return []
      const currentTime = parseInt(new Date().getTime() / 1000)
      let activitList = this.gameActivityList && Array.isArray(this.gameActivityList) ? this.gameActivityList : []
      // 二充追加活动second_recharge_ext_gift_switch == 1 开起来
      if (
        this.systemConfig?.second_recharge_ext_gift_switch == 1 &&
        this.$u.lodash.isObject(this.user?.secord_recharge_ext_gift)
      ) {
        const { start_time = 0, end_time = 0 } = this.user.secord_recharge_ext_gift
        if (start_time < currentTime || currentTime < end_time) {
          const someRecharge = activitList.some(v => v.key == 'secondary_recharge')
          if (!someRecharge) {
            activitList.push({
              ...this.user.secord_recharge_ext_gift,
              key: 'secondary_recharge'
            })
          }
        }
      }
      activitList = activitList.map(item => {
        const { start_time = 0, end_time = 0 } = item
        if (start_time >= currentTime || end_time <= currentTime) return null

        return item
      })
      activitList = activitList.filter(f => f)
      return activitList
    },
    // 其他入口列表
    otherList() {
      if (this.drawerLeft) return []
      const list = [
        // 回归任务-七日签到
        {
          sign: 'taskSign',
          hidden: this.isApp && this.isTaskSignShow
        },
        // 七日签到
        {
          sign: 'sevenDays',
          hidden: this.isApp && this.sevenDaysShow
        },
        // 下载 -- 安卓端 && 不是安卓壳包
        {
          sign: 'download',
          hidden: this.isApp
        },
        // 自定义悬浮入口
        {
          sign: 'selfDefine',
          hidden: this.systemConfig?.float_self_define_switch
        },
        // 客服悬浮
        {
          sign: 'kefu',
          hidden: true
        }
      ].filter(f => f.hidden)

      return list
    },
    // 自定义悬浮入口图标地址
    selfDefineIcon() {
      return this.systemConfig?.float_self_define_icon || loadIcon('float_self_tg')
    }
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.fontShow = true
      }, 1000)
    })
  },
  methods: {
    otherLoadIcon: (k = '', bool = false) => loadIcon(k, bool),
    loadIcon: (k, bool = false) => loadIcon(k, bool),
    // 触发 BI 观察，行为识别接口
    async downloadRecord() {
      const source_type = localStorage.getItem('source_type') || ''
      const agentid = localStorage.getItem('agentid') || ''
      const params = {
        agentid: agentid,
        language: this.languageType,
        source_type
      }
      const res = await postDownloadRecord(params).catch(e => e)
    },
    // 下载
    onDownload() {
      this.downloadRecord()
      // 渠道管理埋点
      channelBurialPoint(2)
      // AJ 打点
      AJ_DOTTING.getAjdata('OTHER_BTN_FLOAT_DOWN')

      const { down_urls = {} } = this.systemConfig
      const { android_download_url = '', ios_download_url = '' } = down_urls

      if (!android_download_url) return
      // 兼容 移动端safari浏览器无法通过window.open在新标签打开链接
      setTimeout(() => window.open(android_download_url))
    },
    // ios 弹出打开
    onIosDownload() {
      this.$pop.downloadDialog().show()
      // AJ 打点
      AJ_DOTTING.getAjdata('OTHER_BTN_BOOK_IOS')
    },
    // 活动点击
    async onClickIcon(item = {}) {
      if (this.isTourist) {
        this.$store.dispatch('modal/setLoginRegistShow')
        return
      }
      console.log(item, 'itemitemitem')
      const { activity_type = 1, gid = '', key = '' } = item
      if (key == 'secondary_recharge') {
        const { start_time = 0, end_time = 0 } = item
        const currentTime = parseInt(new Date().getTime() / 1000)
        if (start_time < currentTime || currentTime < end_time) {
          this.$pop.secondRecharge().show()
        }
        return
      }
      const params = {
        is_activity: activity_type,
        gid,
        gameid: 0
      }
      if (hasLossLimit(this)) return
      this.$pop.game().show(params)
    },
    styleColor: k => _common[k],
    // 点击自定义悬浮图标
    onClickSelfDefineIcon() {
      const url = this.systemConfig?.float_self_define_url
      url && window.open(url, '_blank')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.comp.float_btn {
  user-select: none;
  .right_list_box {
    position: fixed;
    bottom: $float_btn_app_bottom;
    bottom: calc(88px + env(safe-area-inset-bottom));
    right: $float_btn_app_right;
    z-index: $menu_right_float_zindex;
    @include setBox(column, center, center);
    .right_list_activit {
      width: $float_btn_right_list_activit_app_width;
      @include setBox(column, center, center);
      &.bg {
        padding: $float_btn_right_list_activit_bg_app_padding;
        background: $float_btn_right_list_activit_bg_app_bg;
        border-radius: $float_btn_right_list_activit_bg_app_radius;
        box-shadow: $float_btn_right_list_activit_bg_app_shadow;
        position: relative;
        .right_list_activit_arrow {
          width: $float_btn_right_list_activit_arrow_app_width;
          height: $float_btn_right_list_activit_arrow_app_height;
          border-radius: $float_btn_right_list_activit_arrow_app_radius;
          position: absolute;
          top: $float_btn_right_list_activit_arrow_app_top;
          left: $float_btn_right_list_activit_arrow_app_left;
          transform: $float_btn_right_list_activit_arrow_app_transform;
          background: $float_btn_right_list_activit_arrow_app_bg;
          box-shadow: $float_btn_right_list_activit_arrow_app_shadow;
          @include setBox(column, center, center);
          cursor: pointer;
          user-select: none;
          &.down {
            img {
              user-select: none;
              transform: rotate(180deg);
              &:first-child {
                animation-delay: -1.3s;
                -webkit-animation-delay: -1.3s;
              }
              &:nth-child(2) {
                animation-delay: -1s;
                -webkit-animation-delay: -1s;
              }
              @keyframes rightMove {
                0% {
                  opacity: 0;
                }
                50% {
                  opacity: 1;
                }
                100% {
                  opacity: 0;
                }
              }
            }
          }
          img {
            width: $float_btn_right_list_activit_arrow_img_app_width;
            margin: $float_btn_right_list_activit_arrow_img_app_margin;
            position: relative;
            top: $float_btn_right_list_activit_arrow_img_app_top;
            animation: rightMove 1.5s infinite;
            -webkit-animation: rightMove 1.5s infinite;
            user-select: none;
            &:first-child {
              animation-delay: -1s;
              -webkit-animation-delay: -1s;
            }
            &:nth-child(2) {
              animation-delay: -1.3s;
              -webkit-animation-delay: -1.3s;
            }
            @keyframes rightMove {
              0% {
                opacity: 1;
              }
              50% {
                opacity: 0;
              }
              100% {
                opacity: 1;
              }
            }
          }
        }
      }
      .right_list_item_all {
        width: $float_btn_right_list_activit_item_fu_app_width;
        @include setBox(column, center, center);
        gap: $float_btn_right_list_activit_item_fu_app_gap;
        height: 100%;
        overflow-y: auto;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        user-select: none;
        &.hide {
          height: 52px;
        }
        .right_list_item {
          width: $float_btn_right_list_item_app_width;
          cursor: pointer;
          @include setBox(row, center, center);
          user-select: none;
          &.fc_icon {
            .active_icon_box {
              width: $float_btn_right_list_item_fc_icon_active_app_width;
              height: $float_btn_right_list_item_fc_icon_active_app_height;
            }
          }
          &.fg_rotate_icon {
            .active_icon_box {
              width: $float_btn_right_list_item_fg_rotate_icon_active_app_width;
              height: $float_btn_right_list_item_fg_rotate_icon_active_app_height;
              position: relative;
              top: $float_btn_right_list_item_fg_rotate_icon_active_app_top;
            }
          }
          &.fg_ship_icon {
            .active_icon_box {
              width: $float_btn_right_list_item_fg_ship_icon_active_app_width;
              position: relative;
              left: $float_btn_right_list_item_fg_ship_icon_active_app_left;
            }
          }
          &.secondary_recharge_icon {
            .active_icon_box {
              width: $float_btn_right_list_item_secondary_recharge_icon_active_app_width;
              position: relative;
              left: $float_btn_right_list_item_secondary_recharge_icon_active_app_left;
            }
          }
          .active_icon_box {
            width: $float_btn_right_list_item_active_icon_app_width;
            height: $float_btn_right_list_item_active_icon_app_height;
          }
        }
      }
      .right_list_item_last {
        height: 100% !important;
      }
    }
    .right_list_other {
      width: $right_list_other_app_width;
      margin: $right_list_other_app_margin;
      @include setBox(column, center, center);
      gap: $right_list_other_app_gap;
      user-select: none;
      .right_list_item {
        width: $float_btn_right_list_item_app_width;
        cursor: pointer;
        @include setBox(row, center, center);
        .android_download {
          .android_download_box {
            width: $float_btn_android_download_box_app_width;
            height: $float_btn_android_download_box_app_height;
            border-radius: $float_btn_android_download_box_app_radius;
            position: relative;
            @include setBox(row, center, center);
            .download_img {
              width: 100%;
              height: 100%;
              user-select: none;
            }
            .android_gold {
              padding: $float_btn_android_gold_app_padding;
              position: absolute;
              top: $float_btn_android_download_box_gold_app_top;
              &::v-deep {
                .fab_title_box {
                  .fab_title_size {
                    user-select: none;
                    font-size: $float_btn_android_gold_size_app_size;
                    user-select: none;
                  }
                  .gold_icon {
                    width: $float_btn_android_gold_icon_app_width;
                    height: $float_btn_android_gold_icon_app_height;
                  }
                }
              }
            }
          }
        }
        .ios_download {
          z-index: 116;
          user-select: none;
          .ios_download_box {
            width: $float_btn_ios_download_box_app_width;
            height: $float_btn_ios_download_box_app_height;
            border-radius: $float_btn_ios_download_box_app_radius;
            position: relative;
            @include setBox(row, center, center);
            .download_img {
              width: 100%;
              height: 100%;
              user-select: none;
            }
            .ios_download_box_text {
              width: $ios_download_box_text_app_width;
              height: $ios_download_box_text_app_height;
              text-align: $ios_download_box_text_app_align;
              color: $ios_download_box_text_app_color;
              white-space: nowrap;
              user-select: none;
            }
            .ios_gold {
              position: absolute;
              top: $float_btn_ios_download_box_gold_app_top;
              right: $float_btn_ios_download_box_gold_app_right;
              user-select: none;
            }
          }
        }
        .second_menu_sign,
        .menu_sign,
        .task_menu_sign {
          z-index: 116;
          user-select: none;
          &.menu_sign_btn {
            bottom: $float_btn_menu_sign_btn_app_bottom;
          }

          .sign_box {
            width: $float_btn_sign_box_app_width;
            height: $float_btn_sign_box_app_height;
            position: relative;
            border-radius: $float_btn_sign_box_app_radius;
            @include setBox(row, center, center);
            user-select: none;
          }
        }

        .self_define_icon {
          width: $float_btn_self_define_icon_app_width;
          aspect-ratio: 1;
          object-fit: cover;
          border-radius: 50%;
        }
        .second_menu_sign {
          .second_box {
            img {
              width: $second_box_img_width;
              height: $second_box_img_height;
            }
          }
        }
      }
    }
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .comp.float_btn {
    width: $float_btn_pc_width;
    .right_list_box {
      bottom: $float_btn_right_list_box_pc_bottom;
      right: $float_btn_right_list_box_pc_right;
      .right_list_activit {
        width: $float_btn_right_list_activit_pc_width;
        &.bg {
          padding: $float_btn_right_list_activit_bg_pc_padding;
          .right_list_activit_arrow {
            width: $float_btn_right_list_activit_arrow_pc_width;
            height: $float_btn_right_list_activit_arrow_pc_height;
            img {
              width: $float_btn_right_list_activit_arrow_img_pc_width;
            }
          }
        }
        .right_list_item_all {
          gap: $float_btn_right_list_activit_item_fu_pc_gap;
          &.hide {
            height: $right_list_item_all_hide_height;
          }
          .right_list_item {
            &.fc_icon {
              .active_icon_box {
                width: $float_btn_right_list_item_fc_icon_active_pc_width;
                height: $float_btn_right_list_item_fc_icon_active_pc_height;
              }
            }
            &.fg_rotate_icon {
              .active_icon_box {
                width: $float_btn_right_list_item_fg_rotate_icon_active_pc_width;
                height: $float_btn_right_list_item_fg_rotate_icon_active_pc_height;
                top: $float_btn_right_list_item_fg_rotate_icon_active_pc_top;
              }
            }
            &.fg_icon {
              .active_icon_box {
                position: relative;
                top: $float_btn_right_list_item_fg_icon_active_pc_top;
              }
            }
            &.fg_ship_icon {
              .active_icon_box {
                width: $float_btn_right_list_item_fg_ship_icon_active_pc_width;
                top: $float_btn_right_list_item_fg_ship_icon_active_pc_top;
              }
            }
            &.secondary_recharge_icon {
              .active_icon_box {
                height: $float_btn_right_list_item_secondary_recharge_icon_active_pc_height;
                width: $float_btn_right_list_item_secondary_recharge_icon_active_pc_width;
                top: $float_btn_right_list_item_secondary_recharge_icon_active_pc_top;
              }
            }
            .active_icon_box {
              width: $float_btn_right_list_item_active_icon_pc_width;
              height: $float_btn_right_list_item_active_icon_pc_height;
            }
          }
        }
        .right_list_item_last {
          height: 100% !important;
        }
      }
      .right_list_other {
        .right_list_item {
          .self_define_icon {
            width: $float_btn_self_define_icon_app_width;
          }
        }
      }
    }
    .drawer_left {
      width: $float_btn_drawer_left_pc_width;
      padding: $float_btn_drawer_left_pc_padding;
      border-top: $float_btn_drawer_left_pc_border_top;
      border-bottom: $float_btn_drawer_left_pc_border_bottom;
      border-left: $float_btn_drawer_left_pc_border_left;
      border-right: $float_btn_drawer_left_pc_border_right;
      background: $float_btn_drawer_left_pc_border_bg;
      border-radius: $float_btn_drawer_left_pc_border_radius;
      cursor: pointer;
      position: relative;
      @include setBox(row, center, center);
      &:hover {
        border-top: $float_btn_drawer_left_hover_pc_border_top;
        border-bottom: $float_btn_drawer_left_hover_pc_border_bottom;
        border-left: $float_btn_drawer_left_hover_pc_border_left;
        border-right: $float_btn_drawer_left_hover_pc_border_right;
      }

      .drawer_left_title {
        color: $float_btn_drawer_left_text_pc_color;
        font-size: $float_btn_drawer_left_text_pc_size;
        font-weight: $float_btn_drawer_left_text_pc_weight;
