<template>
  <div
    ref="suffixWrapRef"
    :class="['input_autocomplete_suffix_wrap', isApp && 'input_autocomplete_suffix_wrap_app']"
    :style="{ '--wrapHeight': wrapHeight }"
  >
    <v-text-field v-model="text" v-bind="$attrs" :style="{ borderRadius: radius }" @focus="hasShowCard">
      <template v-for="(slot, name) in $slots" :slot="name">
        <slot :name="name"></slot>
      </template>
    </v-text-field>
    <div
      v-if="showSuffixCard"
      :class="['suffix_wrap', anim && 'exit_anim']"
      v-click-outside="{
        handler: closeCard,
        include: getEmailWrap
      }"
    >
      <div class="suffix_title">{{ title }}</div>
      <div class="suffix_scroll_wrap">
        <div v-for="(item, index) in emailOptions" :key="index" class="options_item" @click="onCheckSuffix(item)">
          <span class="first_suffix">{{ getOptionItemSlice(item)[0] }}</span>
          <span class="last_suffix">{{ getOptionItemSlice(item)[1] }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'inputAutocompleteSuffix',
  props: {
    value: {
      type: String,
      default: '',
      event: 'input'
    },
    radius: {
      type: '',
      default: '2px'
    },
    suffixList: {
      type: Array,
      default: () => [
        '@gmail.com',
        '@hotmail.com',
        '@outlook.com',
        '@live.com',
        '@icloud.com',
        '@yahoo.com',
        '@protonmail.com',
        '@gmx.com',
        '@mail.com'
      ]
    }, // 尾缀列表
    title: {
      type: String,
      default: 'Email suffix'
    }
  },
  data() {
    return {
      showSuffixCard: false,
      wrapHeight: '54px',
      anim: false
    }
  },
  created() {},
  mounted() {
    this.$nextTick(this.getWrapHeight)
  },
  watch: {
    value: {
      handler() {
        this.hasShowCard()
      }
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    emailOptions() {
      let arr = []
      if (!this.value) return arr
      const index = this.value.lastIndexOf('@')
      if (index == -1) {
        arr = this.suffixList.map(item => `${this.value}${item}`)
      }
      return arr
    },
    text: {
      get() {
        return this.value
      },
      set(val) {
        this.$emit('input', val)
        return val
      }
    }
  },
  methods: {
    hasShowCard() {
      if (typeof this.value === 'string' && this.value.length) {
        const index = this.value.indexOf('@')
        if (index >= 0) {
          this.closeCard()
          // this.showSuffixCard = false
        } else {
          this.showSuffixCard = true
        }
      } else {
        this.closeCard()
        // this.showSuffixCard = false
      }
    },
    closeCard() {
      this.anim = true
      setTimeout(() => {
        this.anim = false
        this.showSuffixCard = false
      }, 290)
    },
    getEmailWrap() {
      return [document.querySelector('.input_autocomplete_suffix_wrap')]
    },
    getWrapHeight() {
      this.wrapHeight = this.$refs.suffixWrapRef.getBoundingClientRect().height + 'px'
    },
    getOptionItemSlice(text) {
      if (typeof text === 'string') {
        const index = text.lastIndexOf('@')
        if (index > 0) {
          return [text.slice(0, index), text.slice(index)]
        } else {
          return [text, '']
        }
      } else {
        return ['', '']
      }
    },
    onCheckSuffix(str) {
      this.$emit('input', str)
      this.closeCard()
    }
  }
}
</script>

<style scoped lang="scss">
@import '@style/components/ui.modules.scss';
.input_autocomplete_suffix_wrap {
  position: relative;
  width: 100%;
  height: 100%;
  user-select: none;
  .suffix_wrap {
    position: absolute;
    top: calc(var(--wrapHeight) + 6px);
    right: 0;
    width: $input_autocomplete_suffix_card_width;
    height: $input_autocomplete_suffix_card_height;
    padding: $input_autocomplete_suffix_card_padding;
    border-radius: $input_autocomplete_suffix_card_radius;
    background-color: $input_autocomplete_suffix_card_bg;
    z-index: 99;
    overflow: auto;
    cursor: pointer;
    box-shadow: $input_autocomplete_suffix_card_shadow;
    animation: anim_suffix_card 0.3s ease-in-out;
    .suffix_title {
      text-align: $input_autocomplete_suffix_title_align;
      font-size: $input_autocomplete_suffix_title_size;
      color: $input_autocomplete_suffix_title_color;
    }
    .suffix_scroll_wrap {
      width: 100%;
      height: calc(100% - 22px);
      overflow: auto;
    }
    .options_item {
      display: flex;
      justify-content: $input_autocomplete_suffix_text_align;
      align-items: center;
      width: 100%;
      font-size: $input_autocomplete_suffix_text_size;
      color: $input_autocomplete_suffix_text_color;
      line-height: $input_autocomplete_suffix_text_line_height;
      .first_suffix {
        max-width: $input_autocomplete_suffix_text_first_suffix_width;
        text-align: right;
        @include set_text_ellipsis(1);
      }
    }
  }
  .exit_anim {
    animation: card_exit_anim 0.3s ease-in-out !important;
  }
  @keyframes anim_suffix_card {
    0% {
      opacity: 0;
      transform: translateY(-10px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  @keyframes card_exit_anim {
    0% {
      opacity: 1;
      transform: translateY(0);
    }
    100% {
      opacity: 0;
      transform: translateY(-10px);
    }
  }
}
.input_autocomplete_suffix_wrap_app {
  .suffix_wrap {
    width: $input_autocomplete_suffix_card_app_width !important;
    height: $input_autocomplete_suffix_card_app_height !important;
    padding: $input_autocomplete_suffix_card_app_padding !important;
    border-radius: $input_autocomplete_suffix_card_app_radius !important;
    background-color: $input_autocomplete_suffix_card_app_bg !important;
    box-shadow: $input_autocomplete_suffix_card_app_shadow !important;
    .suffix_title {
      text-align: $input_autocomplete_suffix_title_app_align !important;
      font-size: $input_autocomplete_suffix_title_app_size !important;
      color: $input_autocomplete_suffix_title_app_color !important;
    }
  }
  .options_item {
    justify-content: $input_autocomplete_suffix_text_app_align !important;
    font-size: $input_autocomplete_suffix_text_app_size !important;
    color: $input_autocomplete_suffix_text_app_color !important;
    line-height: $input_autocomplete_suffix_text_app_line_height !important;
  }
}
</style>
