<template>
  <div class="sm-dropdown-bg">
    <div
      ref="smSelectRef"
      class="sm-dropdown-select"
      :style="styleFun"
      tabindex="-1"
      @click.prevent="smSelect"
      @blur="smSelectBlur"
    >
      <div class="sm-dropdown-content">
        <img
          v-if="valueData[prop.url]"
          class="sm-dropdown-content-img"
          :src="valueData[prop.url]"
          :style="iconSty"
          alt=""
        />
        <input
          v-model="valueData[prop.label]"
          type="text"
          class="sm-dropdown-content-text"
          :style="styleInputFun"
          readonly
          :placeholder="placeholder"
        />
      </div>
      <icon-font v-show="showType" name="icon-up" :size="iconSize" :style="`color: ${iconColorBg};`" />
      <icon-font v-show="!showType" name="icon-down" :size="iconSize" :style="`color: ${iconColorBg};`" />
    </div>

    <div class="sm-dropdown-ul" :class="{ 'sm-dropdown-ul-show': showType }" :style="memuStyle">
      <div
        v-for="(item, index) in options1"
        :key="index"
        :value="item[prop.value]"
        :style="[dropdownLi, item[prop.value] == valueData[prop.value] ? activeStyle : '', itemStyle]"
        :class="['sm-dropdown-li']"
        @mousedown.stop="liclick(item)"
      >
        <div class="sm-dropdown-content">
          <img
            v-if="item[prop.url]"
            class="sm-dropdown-content-img"
            :src="item[prop.url]"
            :style="dropdownSty"
            alt=""
          />
          <div class="sm-dropdown-content-title">{{ item[prop.label] }}</div>
        </div>
      </div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import _common from '@style/components/ui.modules.scss'

export default {
  model: {
    prop: 'value',
    event: 'changeVal'
  },
  props: {
    // 输入框的内边距
    padding: {
      type: [String],
      default: () => {
        return _common.vue_select_default_pa
      }
    },
    // 输入框的字体颜色
    color: {
      type: [String],
      default: () => {
        return ''
      }
    },
    // 输入框的背景颜色
    backgroundColor: {
      type: [String],
      default: () => {
        return ''
      }
    },
    // 边框色值
    border: {
      type: [String],
      default: () => {
        return ''
      }
    },
    // 输入框字体
    fontSize: {
      type: [Number, String],
      default: () => {
        return parseInt(_common.vue_select_default_input_font_size)
      }
    },
    // 输入框圆角
    borderRadius: {
      type: [Number, String],
      default: () => {
        return parseInt(_common.vue_select_default_radius)
      }
    },
    // 下拉图标色值
    iconColor: {
      type: String,
      default: ''
    },
    // 下拉图标大小
    iconSize: {
      type: [Number, String],
      default: parseInt(_common.vue_select_default_icon_size)
    },
    // menu 框圆角
    menuBorderRadius: {
      type: [String, Number],
      default: () => {
        return parseInt(_common.vue_select_default_menu_radius)
      }
    },
    // menu 总内边距
    menuPadding: {
      type: [String],
      default: () => {
        return _common.vue_select_default_menu_pa
      }
    },
    // 弹窗的背景色
    memuBg: {
      type: [String],
      default: () => {
        return ''
      }
    },
    // 下拉框选择的圆角
    activeRadius: {
      type: [String, Number],
      default: () => {
        return parseInt(_common.vue_select_default_active_radius)
      }
    },
    // 选中字体颜色
    activeColor: {
      type: [String],
      default: () => {
        return ''
      }
    },
    // 选中背景色
    activeBg: {
      type: [String],
      default: () => {
        return ''
      }
    },
    // 选中字体大小
    activeFontSize: {
      type: [Number, String],
      default: () => {
        return _common.vue_select_default_active_font_size
      }
    },
    // 选中的 padding
    activeItemPadding: {
      type: [String],
      default: () => {
        return _common.vue_select_default_active_item_pa
      }
    },
    // 纵轴移动
    offsetY: {
      type: [Number, String],
      default: () => {
        return _common.vue_select_default_offset_y
      }
    },
    // 横轴移动
    offsetX: {
      type: [Number, String],
      default: () => {
        return _common.vue_select_default_offset_x
      }
    },
    prop: {
      type: Object,
      default: () => {
        return {
          label: 'label',
          value: 'value',
          url: 'url'
        }
      }
    },
    // 图片宽
    imgWidth: {
      type: [String, Number],
      default: parseInt(_common.vue_select_default_img_width)
    },
    // 图片高
    imgHeight: {
      type: [String, Number],
      default: parseInt(_common.vue_select_default_img_height)
    },
    // 图片圆角
    imgRadius: {
      type: [String, Number],
      default: _common.vue_select_default_img_radius
    },
    // 是否隐藏
    disabled: {
      type: Boolean,
      default: false
    },
    // 绑定值
    value: {
      type: [String, Number],
      default() {
        return ''
      }
    },
    // 数据源
    options: {
      type: Array,
      default() {
        return []
      }
    },
    placeholder: {
      type: String,
      default: ''
    },
    // 输入框颜色
    inputColor: {
      type: String,
      default: '#fff'
    },
    // 输入框字体粗细
    inputWeight: {
      type: [Number, String],
      default: 400
    },
    // 输入框高度
    inputHeight: {
      type: [String],
      default: () => {
        return _common.vue_select_default_height
      }
    }
  },
  data() {
    return {
      showType: false, // 下拉显示隐藏
      valueData: {},
      valueType: '', // title 显示文字
      dataValue: '', // 改变时使用的值
      options1: [], // 数据源
      selectState: false // 控制下拉框div失去焦点触发点击事件是否无效状态
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    // input 框样式
    styleFun() {
      return {
        fontSize: typeof this.fontSize == 'string' ? this.fontSize : `${this.fontSize}px`,
        padding: this.padding,
        height: this.inputHeight,
        borderRadius: typeof this.borderRadius == 'string' ? this.borderRadius : `${this.borderRadius}px`,
        color: this.color || _common.vue_select_icon_color,
        border: `1px solid ${this.border || _common.vue_select_border}`,
        backgroundColor: this.backgroundColor || _common.vue_select_bg
      }
    },
    // 下拉框选中的样式
    activeStyle() {
      return {
        backgroundColor: this.activeBg || _common.vue_select_border_card,
        // fontSize: typeof (this.activeFontSize) == "string" ? this.activeFontSize : `${this.activeFontSize}px`,
        color: this.activeColor || _common.vue_select_active_white,
        borderRadius: typeof this.activeRadius == 'string' ? this.activeRadius : `${this.activeRadius}px`
      }
    },
    // 下拉框样式
    memuStyle() {
      return {
        marginTop: typeof this.offsetY == 'string' ? this.offsetY : `${this.offsetY}px`,
        marginLeft: typeof this.offsetX == 'string' ? this.offsetX : `${this.offsetX}px`,
        backgroundColor: this.memuBg || _common.vue_select_menu_bg,
        padding: this.menuPadding,
        borderRadius: typeof this.menuBorderRadius == 'string' ? this.menuBorderRadius : `${this.menuBorderRadius}px`
      }
    },
    // 下拉框内容样式
    dropdownLi() {
      return {
        fontSize: typeof this.fontSize == 'string' ? this.fontSize : `${this.fontSize}px`
      }
    },
    // 下拉框图标颜色
    iconColorBg() {
      return this.iconColor || _common.vue_select_icon_color
    },
    // 选中的下拉框边距
    itemStyle() {
      return {
        padding: this.activeItemPadding
      }
    },
    // 国家图标样式 -- input 内
    iconSty() {
      return {
        // width: this.imgWidth + 'px',
        height: this.imgHeight + 'px',
        borderRadius: typeof this.imgRadius == 'string' ? this.imgRadius : `${this.imgRadius}px`
      }
    },
    // 国家图标样式 -- 下拉框 内
    dropdownSty() {
      return {
        // width: this.imgWidth + 'px',
        height: this.imgHeight + 'px',
        borderRadius: typeof this.imgRadius == 'string' ? this.imgRadius : `${this.imgRadius}px`
      }
    },
    // 输入框显示样式
    styleInputFun() {
      return {
        fontSize: typeof this.fontSize == 'string' ? this.fontSize : `${this.fontSize}px`,
        color: this.inputColor,
        fontWeight: parseFloat(this.inputWeight)
      }
    }
  },
  watch: {
    options: {
      // 数据源
      handler(val) {
        this.options1 = val
        if (this.options1.find(v => v[this.prop.value] == this.dataValue)) {
          this.valueData = this.options1.find(v => v[this.prop.value] == this.dataValue)
        }
      },
      immediate: true,
      deep: true
    },
    value: {
      // 绑定的值
      handler(val) {
        this.dataValue = val
      },
      immediate: true,
      deep: true
    },
    dataValue: {
      // 数据改变时使用的值
      handler(val) {
        if (this.options1.find(v => v[this.prop.value] == val)) {
          this.valueData = this.options1.find(v => v[this.prop.value] == val)
        }
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    // 点击事件 控制显示隐藏
    smSelect() {
      if (!this.disabled && !this.selectState) {
        this.showType = !this.showType
        /**
         * 给div增加focus
         * 必须添加属性 tabindex="-1"
         * 通过@blur 事件 让下拉显示框隐藏
         * */
        this.$refs.smSelectRef.focus()
        this.selectState = false
      }
    },
    // div失去焦点事件
    smSelectBlur() {
      // 阻止点击事件的默认行为
      event.preventDefault()
      this.selectState = true
      this.showType = false
      setTimeout(() => {
        this.selectState = false
      }, 200)
    },
    // 下拉列表点击事件
    liclick(item) {
      this.$emit('changeVal', item.value)
      this.$emit('change', item.value, item)
      this.valueData = item
      this.showType = false
    }
  }
}
</script>
<style scoped lang="scss">
@import '@style/components/ui.modules.scss';

.sm-dropdown-bg {
  display: flex;
  position: relative;
  flex: 1;
  user-select: none;
  cursor: pointer;

  .sm-dropdown-select {
    text-align: center;
    border-radius: $vue_select_dropdown_radius;
    flex: 1;
    display: flex;
    justify-content: $vue_select_dropdown_justify_content;
    align-items: $vue_select_dropdown_align_items;
  }

  .sm-dropdown-ul {
    width: $vue_select_dropdown_ul_width;
    max-height: $vue_select_dropdown_ul_max_height;
    line-height: $vue_select_dropdown_ul_line_height;
    box-sizing: border-box;
    text-align: $vue_select_dropdown_ul_align;
    position: absolute;
    z-index: 60;
    overflow-y: auto;
    display: none;
    box-shadow: $vue_select_dropdown_ul_shadow;
  }

  .sm-dropdown-ul::-webkit-scrollbar {
    width: 0;
    height: 0;
  }

  .sm-dropdown-ul-show {
    display: block;
  }

  .sm-dropdown-li {
    color: $vue_select_dropdown_li_color;
    display: flex;
    justify-content: $vue_select_dropdown_li_justify_content;
    align-items: $vue_select_dropdown_li_align_items;
    cursor: pointer;
  }

  .sm-dropdown-input {
    display: inline;
    padding: 0;
    border: 0px;
  }

  .sm-dropdown-content {
    display: flex;
    justify-content: $vue_select_dropdown_content_justify_content;
    align-items: $vue_select_dropdown_content_align_items;
    width: 100%;
    .sm-dropdown-content-text {
      text-align: $vue_select_dropdown_content_title_align;
      content: attr(placeholder);
      color: #999;
      width: 100%;
    }

    &-img {
      margin: $vue_select_dropdown_content_img_margin;
    }

    &-title {
      text-align: $vue_select_dropdown_content_title_align;
    }
  }
}
input::placeholder {
  color: $vue_select_input_placeholder_color !important;
}
input {
  outline: none;
  cursor: pointer;
  user-select: none;
}
</style>
