<template>
  <!-- Font class 类型 -->
  <i v-if="type == 'fontClass'" :class="`iconfont ${name}`" :style="customSize" v-on="$listeners"></i>
  <!-- Symbol 类型 -->
  <svg v-else class="icon svg-icon" aria-hidden="true" :style="customStyle" v-on="$listeners">
    <use :xlink:href="`#${name}`"></use>
  </svg>
</template>

<style src="@source/iconfont/iconfont.css"></style>
<script>
import '@source/iconfont/iconfont.js'
import customDark from './dark.js'

export default {
  props: {
    // 图标类型，是 Font class 类型，还是 Symbol 类型
    // Font class：受 font-size 控制大小，需自定义字体颜色
    // Symbol：受 宽高 控制大小，无需自定义字体颜色
    type: {
      type: [String, Number],
      default: 'Symbol'
    },
    // 图标名称
    name: {
      type: String,
      default: '',
      required: true
    },
    // 图标大小
    size: {
      type: [String, Number],
      default: '18'
    },
    // Font class 类型 设置字体颜色 --> 从 dark.js 里获取
    dark: {
      type: String,
      default: ''
    }
  },
  computed: {
    // Font class 样式
    customSize() {
      const color = customDark[this.dark] || ''
      return {
        fontSize: `${this.size}px`,
        color: color ? color : this.dark
      }
    },
    // Symbol 样式
    customStyle() {
      const color = customDark[this.dark] || ''
      return {
        width: `${this.size}px`,
        height: `${this.size}px`,
        fill: 'currentColor',
        color: color ? color : this.dark
      }
    }
  }
}
</script>
