<template>
  <div class="btn-tab">
    <div class="navbar_box" :style="navbarStyle">
      <div class="navbar_item" v-for="(item, index) in list" :key="index" @click="onChangeTab(item, index)">
        <div class="btn_nav" :class="{ active: type == item.value }" :style="`borderRadius: ${radius} !important; `">
          <div
            class="btn_text"
            :style="`width: ${item.width}; height: ${item.height}; font-size: ${isApp ? '12px' : '14px'};`"
          >
            {{ item.name }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import _commit from '@style/components/ui.modules.scss'

export default {
  props: {
    // 宽度
    width: {
      type: [String, Number],
      default: 0
    },
    // 高度
    height: {
      type: [String, Number],
      default: _commit.btn_tab_default_height
    },
    // border 弧度
    radius: {
      type: String,
      default: _commit.btn_tab_default_radius
    },
    // tab 列表
    tagList: {
      type: Array,
      default() {
        return [
          // {
          //     name: '',
          //     value: 1
          // }
        ]
      }
    },
    // 默认选中的类型
    type: {
      type: [String, Number],
      default: 0
    },
    // 默认底部背景色
    defaultBg: {
      type: String,
      default: _commit.$btn_tab_bg
    }
  },
  data() {
    return {
      list: [] // tab 列表
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    navbarStyle() {
      const w = typeof this.width == 'string' ? this.width : `${this.width}px`
      const len = Array.isArray(this.tagList) ? this.tagList.length : 1
      return {
        width: w,
        borderRadius: this.radius,
        background: this.defaultBg,
        '--len': len
      }
    }
  },
  watch: {
    tagList: {
      handler() {
        this.getGameUrl()
      },
      deep: true,
      immediate: true
    },
    type() {
      this.getGameUrl()
    }
  },
  methods: {
    // 使用 canvas 加载名称，以防名称太长
    getGameUrl() {
      const len = Array.isArray(this.tagList) ? this.tagList.length : 1
      this.list = this.tagList.map(item => {
        return {
          ...item,
          width: `${parseInt(this.width) / len}px`,
          height: `${parseInt(this.height)}px`
        }
      })
    },
    // 切换tab
    onChangeTab(item, i) {
      this.$emit('changeTab', { item, i })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

.btn-tab {
  width: 100%;
  @include setBox(row, center, center);

  .navbar_box {
    background-color: $btn_tab_bg;
    @include setBox(row, space-between, center);

    .navbar_item {
      width: calc(100% / var(--len));
      cursor: pointer;
      .btn_nav {
        padding: $btn_tab_itme_padding;
        @include setBox(row, center, flex-end);
        cursor: pointer;
        &.active {
          background: $btn_tab_itme_active_bg !important;
          border-image: $btn_tab_itme_active_border_image;
          box-shadow: $btn_tab_itme_active_box_shadow;
          &:hover {
            @include setFilter($btn_hover_filter, $btn_hover_filter_type);
          }
        }

        .btn_text {
          @include set_text_ellipsis(1);
          width: 100%;
          line-height: 1;
          @include setBox(row, center, center);
          text-align: center;
        }
      }
    }
  }
}
</style>
