<template>
  <div class="recomendar_history">
    <back-bar
      class="history_back"
      :size="backProps.size"
      :line="backProps.line"
      :center="backProps.center"
      :py-height="backProps.padding"
      :left-arrow="isApp"
      :right-arrow="!isApp"
      :backText="$t('UI_Activity_Details')"
      @back="closeDialog"
      @close="closeDialog"
    >
    </back-bar>

    <div class="history_view">
      <table-temp :table-head="headers" :table-list="dataTable" :load-more-state="loadMoreState" @nextPage="nextPage">
        <template slot="date" slot-scope="{ row }">
          <div class="column_flex" v-html="dateTime(row.date)"></div>
        </template>
      </table-temp>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { newCleanBetList } from '@/api/modules/bet.js'
import BackBar from '@/components/ui/back-bar'
import tableTemp from '@/components/ui/table-temp'
import _common from '@style/components/ui.modules.scss'

export default {
  components: {
    BackBar,
    tableTemp
  },
  data() {
    return {
      // 搜索
      page: 1,
      limit: 10,
      loadMoreState: false,

      headTitle: [], // 表头
      dataTable: [], // 数据
      loadMoreState: false // 是否显示下一页按钮
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    // 返回组件的传值
    backProps() {
      const {
        recomendar_history_back_size,
        recomendar_history_back_line,
        recomendar_history_back_center,
        recomendar_history_back_padding
      } = _common

      return {
        size: Number(recomendar_history_back_size),
        line: !(recomendar_history_back_line == false + ''),
        center: recomendar_history_back_center,
        padding: Number(recomendar_history_back_padding)
      }
    },
    // 处理表头
    headers() {
      const columns = this.headTitle.map(head => {
        const label = Object.values(head)[0] || ''
        const prop = Object.keys(head)[0] || ''
        let item = {
          label,
          prop,
          align: 'center'
        }
        item.width = '130px'
        if (prop == 'date' || prop == 'total') {
          item.width = '60px'
          item.fixed = 'left'
        }
        if (prop == 'date') item.slot = 'date'

        return item
      })

      const {
        recomendar_history_table_size,
        recomendar_history_table_bold,
        recomendar_history_table_radius,
        recomendar_history_table_zebra,
        recomendar_history_table_is_scroll,
        recomendar_history_table_head_bg,
        recomendar_history_table_head_text_color,
        recomendar_history_table_head_radius,
        recomendar_history_table_head_size,
        recomendar_history_table_head_bold,
        recomendar_history_table_head_align,
        recomendar_history_table_head_margin,
        recomendar_history_table_head_padding_app,
        recomendar_history_table_head_padding_pc,
        recomendar_history_table_head_fixed,
        recomendar_history_table_hor_bg,
        recomendar_history_table_hor_app_padding,
        recomendar_history_table_hor_pc_padding,
        recomendar_history_table_hor_border_t,
        recomendar_history_table_hor_border_r,
        recomendar_history_table_hor_border_b,
        recomendar_history_table_hor_border_l,
        recomendar_history_table_hor_margin,
        recomendar_history_table_hor_color,
        recomendar_history_table_hor_size,
        recomendar_history_table_hor_bold,
        recomendar_history_table_hor_align
      } = _common
      return {
        size: {
          fontSize: recomendar_history_table_size,
          fontWeight: recomendar_history_table_bold
        },
        radius: recomendar_history_table_radius,
        zebra: recomendar_history_table_zebra,
        isScroll: !(recomendar_history_table_is_scroll == false + ''),
        head: {
          bg: recomendar_history_table_head_bg,
          color: recomendar_history_table_head_text_color,
          radius: recomendar_history_table_head_radius,
          size: {
            fontSize: recomendar_history_table_head_size,
            fontWeight: recomendar_history_table_head_bold,
            textAlign: recomendar_history_table_head_align
          },
          margin: recomendar_history_table_head_margin,
          padding: this.isApp ? recomendar_history_table_head_padding_app : recomendar_history_table_head_padding_pc,
          fixed: !(recomendar_history_table_head_fixed == false + '')
        },
        hor: {
          bg: recomendar_history_table_hor_bg,
          padding: this.isApp ? recomendar_history_table_hor_app_padding : recomendar_history_table_hor_pc_padding,
          border_t: recomendar_history_table_hor_border_t,
          border_r: recomendar_history_table_hor_border_r,
          border_b: recomendar_history_table_hor_border_b,
          border_l: recomendar_history_table_hor_border_l,
          margin: recomendar_history_table_hor_margin,
          color: recomendar_history_table_hor_color,
          size: {
            fontSize: recomendar_history_table_hor_size,
            fontWeight: recomendar_history_table_hor_bold,
            textAlign: recomendar_history_table_hor_align
          }
        },
        columns
      }
    }
  },
  created() {
    this.getList()
  },
  methods: {
    // 请求列表数据
    async getList() {
      const params = {
        token: this.user.token,
        page: this.page,
        limit: this.limit,
        language: this.languageType,
        type: 0
      }
      const res = await newCleanBetList(params).catch(e => e)
      if (res.code != 0) return

      const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      this.headTitle = resData.title && Array.isArray(resData.title) ? resData.title : []
      const list = resData.data && Array.isArray(resData.data) ? resData.data : []
      this.dataTable = this.dataTable.concat(list)

      this.loadMoreState = list.length >= this.limit
    },
    // 下一页
    nextPage() {
      this.page += 1
      this.getList()
    },
    // 处理时间格式
    dateTime(time = '') {
      if (!time) return ['--', '--']

      const timeImdex = time.indexOf('-')
      if (timeImdex == -1) return
      const first = time.substring(0, timeImdex + 1)
      const last = time.substring(timeImdex + 1)
      return `<span>${first}</span><span>${last}</span>`
    },
    // 关闭
    closeDialog() {
      this.$emit('close')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/ui.modules.scss';

// 针对app
.recomendar_history {
  background-color: $recomendar_history_bg;
  @include setBox(column, flex-start, flex-start);
  position: fixed;
  top: 0px;
  left: 0px;
  height: $recomendar_history_app_height;

  .history_back {
    flex-shrink: 0;
    flex-grow: 0;
  }

  .history_view {
    width: $recomendar_history_table_width;
    flex-shrink: 1;
    flex-grow: 1;
    overflow: hidden;
    padding: $recomendar_history_table_app_padding;
  }
}

// 针对pc
@include breakpoint-only-up() {
  .recomendar_history {
    position: relative;
    height: $recomendar_history_pc_height;
    border-radius: $recomendar_history_pc_radius;
    .history_view {
      padding: $recomendar_history_table_pc_padding;
    }
  }
}
</style>
