<template>
  <div class="overflow-x-hidden">
    <div class="grid_list_box flex_row">
      <div v-for="(s, i) in 12" :key="i" class="grid_list_item">
        <v-skeleton-loader :width="cardSize.width" :height="cardSize.height" type="image" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/views/menu.index.modules.scss'
const { grid_space, grid_space_app, lobby_box_padded, xs_lobby_box_padded } = variables

export default {
  props: {
    // 样式
    styleOpt: {
      type: Object,
      default() {
        return {}
      }
    },
    attrs: {
      type: Object,
      default() {
        return {
          boilerplate: true,
          elevation: 2
        }
      }
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp']),
    size() {
      return this.isApp ? 3 : 6
    },
    cardSize() {
      // 原图大小 160 /220
      const size = this.size
      const spacing = this.isApp ? grid_space_app * 2 : grid_space * 2
      const outer_padded = this.isApp ? xs_lobby_box_padded * 2 : lobby_box_padded * 2
      const w = 160
      const h = 220
      const rate = h / w
      const width = (this.containerBox.width - outer_padded + spacing) / size - spacing
      return {
        width: `${width}px`,
        height: `${width * rate}px`
      }
    }
  }
}
</script>

<style lang="scss" scope>
@import '@style/views/menu.index.modules.scss';

.grid_list_box {
  width: auto;
  flex-wrap: wrap;
  margin: $grid_box_margin;
}
.grid_list_item {
  padding: $grid_container_padding;
  margin-bottom: $grid_container_margin_bottom;
}
.theme--dark.v-skeleton-loader .v-skeleton-loader__image {
  width: 100% !important;
  height: 100% !important;
}

@include breakpoint-only-xs() {
  .grid_list_box {
    margin: $grid_box_app_margin;
  }
  .grid_list_item {
    padding: $grid_container_app_padding;
    margin-bottom: $grid_container_app_margin_bottom;
  }
}
</style>
