<template>
  <div class="comp top_logo">
    <img :src="loadIcon('logo')" class="logo_app" @click="goHome" />
    <div class="logo_inner">
      <icon-font
        :name="leftMenu ? 'icon-yincangdaohang' : 'icon-xianshidaohang'"
        :size="commonStyle('m_icon_pc_dreawer_icon_size')"
        class="dreawer_icon cursor_point"
        @click="onLeftDrawer"
      >
      </icon-font>
      <img class="pc_logo cursor_point" :src="loadIcon('logo_lg')" @click="goHome" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import _common from '@style/components/game-container.top-navigation.modules.scss'

export default {
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'user']),
    ...mapState('modal', ['leftMenu'])
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    loadIcon: key => loadIcon(key, false),
    // 左侧抽屉
    onLeftDrawer() {
      this.$store.dispatch('modal/setLeftMenu', !this.leftMenu)
    },
    goHome() {
      if (this.$route.name === 'menu') return
      // 强制跳过新手引导
      if (this.user && this.user.uid) {
        const storageKey = 'first_guide_redirect_' + this.user.uid
        if (localStorage.getItem(storageKey)) {
          this.$router.push({ name: 'menu', query: { ref: 'casino' } })
          return
        }
      }
      this.$router.push({ name: 'menu' })
    }
  }
}
</script>

<style scoped lang="scss">
@import '@style/components/game-container.top-navigation.modules.scss';

// 针对移动端样式
.comp.top_logo {
  @include setBox(row, flex-start, center);
  user-select: none;
  .logo_app {
    // 适配不同尺寸logo
    max-width: $m_icon_app_logo_mx_width;
    max-height: $m_icon_app_logo_max_height;
  }
  .logo_inner {
    display: none;
  }
}
// 针对pc样式
@include breakpoint-only-up() {
  .comp.top_logo {
    .logo_app {
      display: none;
    }
    .logo_inner {
      @include setBox(row, flex-start, center);
      .dreawer_icon {
        color: $text_color_1;
      }
      .pc_logo {
        height: $m_icon_pc_logo_height;
        margin: $m_icon_pc_logo_margin;
      }
    }
  }
}
</style>
